export const itemDescriptionData = {
  "-1": "Przedmiot dostpny tylko podczas specjalnych daily runw oraz seedu GFVE LLLL. Pojawia si na piedestale na pocztku kadego pitra (dwie sztuki na pitrach XXL).\nEfekt zaley od liczby sztuk przedmiotu, jak posiada Isaac:\n* pierwsza sztuka zwiksza Obraenia, zy i Szybko Strzaw\n* druga sztuka zastpuje zy Isaaca pociskami\n* trzecia sztuka zmienia statystyki Isaaca podobnie do Soy Milk oraz powoduje, e Isaac strzela dwoma strumieniami pociskw\n* czwarta sztuka daje Isaacowi szerokie strzay pociskami, ktre odbijaj si od przeszkd\n* pita sztuka daje Isaacowi strzay pojedynczym pociskiem o wysokich obraeniach, ktry odbija si od przeszkd\n* szsta sztuka daje Isaacowi szybkie strzay z ma celnoci, ktre odpychaj go do tyu\n* sidma sztuka zmienia strzay Isaaca w rakiety, ktre wybuchaj przy uderzeniu\n* sztuka sma i nastpne daj Isaacowi losowe strzay spord tych wymienionych powyej\n* dziewita sztuka i nastpne dodaj losowe efekty ez do strzaw Isaaca\nZaczynajc od drugiej sztuki, przypiesza gr.\nZaczynajc od czwartej sztuki, daje przeciwnikom szans na powielenie si.\nKada sztuka zwiksza efekt trzsienia si ekranu.\n+2 Szybkoci, ignorujc jej limit. Inne przedmioty, ktre zwikszaj Szybko, take bd ignorowa limit.\nWszystkie wybuchy s zastpione przez stockowy efekt.\nPrzeciwnicy wybuchaj przy mierci (te wybuchy nie zadaj obrae ani nie niszcz przeszkd). Niektrzy bossowie maj specjalne animacje wybuchu.\nWszystkim animacjom generacji znajdziek i przedmiotw towarzyszy bd wybuchy.\nPrzeciwnicy przy mierci daj Isaacowi znacznie wicej punktw ni normalnie, a ich dokadna liczba jest pokazana na ekranie, kiedy gin.\nWoda oraz krew jest zastpiona przez jasnoniebiesk ciecz.\nNacinicie przycisku upuszczenia gra efekt airhornu.\nKoszmary Isaaca podczas przechodzenia midzy pitrami s zastpione wersjami z G Fuel.",
  1: "+0,7 ez.",
  10: "Przyzywa 2 Pretty Fly, ktre blokuj pociski przeciwnikw i zabijaj przeciwnikw-muchy przy kontakcie.\nLimit posiadanych Pretty Fly to 3.",
  100: "Przyzywa chowaca, ktry poda za Isaaciem i co 2/3 sekundy strzela samonaprowadzajcymi zami, ktre zadaj 3,5 Obrae.\nMa szans wypa ze Stevena.",
  101: "+1 Czerwone Serce.\n+0,3 Obrae.\n+0,2 ez.\n+0,38 Zasigu.\n+0,3 Szybkoci.\n+0,5 Wysokoci ez.",
  102: "Przy uyciu generuje losow piguk.",
  103: "Daje szans na wystrzelenie zatrutej zy.\nSzansa jest obliczana wg wzoru 1/(4 - Szczcie/4).\nZatruci przeciwnicy otrzymuj obraenia z trucizny 2 razy, w sumie otrzymujc 2x Obraenia Isaaca.",
  104: "zy, ktre zadaj wicej ni 1 punkt Obrae, przy kontakcie z przeciwnikami lub przeszkodami rozdzielaj si na dwie mniejsze, posiadajce 1/2 Obrae i 1/2 pozostaego Zasigu. S one take 2x mniejsze wizualnie.",
  105: "Przy uyciu zmienia wszystkie przedmioty w pokoju na inny przedmiot z puli tego pokoju.\nStartowy przedmiot Isaaca, jeli zosta odblokowany.",
  106: "+5 bomb.\nBomby Isaaca maj zwikszony zasig eksplozji oraz zadaj 185 Obrae.\nMa szans wypa z Super Wratha.",
  107: "Przy uyciu oddziela gow od ciaa Isaaca na czas trwania obecnego pokoju. Gracz steruje gow Isaaca, ktra otrzymuje latanie, natomiast ciao bdzie ciga przeciwnikw, zadajc im 5,5 Obrae kontaktowych na tick.\nSzybko ciaa zaley od szybkoci Isaaca.",
  108: "Wszystkie obraenia s zmniejszone do 1/2 serca.\nNie zmniejsza obrae z przejcia do Mausoleum/Gehenny.\nKeeper i Tainted Keeper nadal otrzymuj 1 monet obrae.",
  109: "Dodaje 0,04 Obrae za kad monet posiadan przez Isaaca.",
  11: "Dodatkowe ycie.\nPo mierci Isaac odrodzi si z tyloma kontenerami czerwonych serc, ile mia przed mierci.\nJeli Isaac nie mia czerwonych serc, odrodzi si z 1/2 Soul Hearta.",
  110: "+0,38 Zasigu.\n+0,5 Wysokoci ez.\nDaje szans na wystrzelenie zy z efektem unieruchomienia. Szansa zaley od Szczcia i wynosi 50% przy Szczciu ok. 20.\nUnieruchomienie przeciwnika, ktry otrzymuje obraenia tylko w jednej czci ciaa (np. Scolex czy Carrion Queen), spowoduje, e bdzie on otrzymywa obraenia wszdzie.\nUnieruchomienie Hearta powoduje ten sam efekt na jego Masce.",
  111: "Przy uyciu Isaaca puszcza bka, zadawajc 5 Obrae i zatruwajc pobliskich przeciwnikw.\nTrucizna zadaje Obraenia Isaaca przez 6 tickw.",
  112: "Przyzywa chowaca, ktry blokuje pociski przeciwnikw i zadaje 7 Obrae kontaktowych na tick (105 na sekund).\nZwiksza prdko wszystkich innych chowacw.",
  113: "Przyzywa chowaca, ktry poda za Isaaciem i automatycznie strzela w przeciwnikw w swoim zasigu 3 razy na sekund, zadajc 3 Obrae na z.",
  114: "Zastpuje zy Isaaca noem, ktry przeszywa przeciwnikw i przelatuje nad przeszkodami. Przytrzymywanie klawiszu strzau zwiksza jego zasig i moc.\nN zadaje 2x Obraenia Isaaca, gdy Isaac go trzyma, i 6x Obraenia Isaaca w najdalszym punkcie. Moe zada obraenia do 20 razy w cigu 1 sekundy.\nN moe by trzymany i wystrzeliwany pod ktem.",
  115: "Daje Isaacowi zy spektralne.\n+0,4 ez.",
  116: "Zmniejsza czas przeadowania o 1 pasek.\nPrzedmioty, ktre normalnie maj czas przeadowania 1 pasek, zmienia si on na 15 sekund (przedmiot aduje si tylko wtedy, gdy w pokoju znajduj si przeciwnicy).\nPrzedmioty, ktre aduj si czasowo, zaczynaj z poow adunku zamiast z pustym.\nCakowicie aduje przedmiot aktywny przy podniesieniu.",
  117: "Przy otrzymaniu obrae przez Isaaca przyzwany zostanie chowaniec, ktry bdzie ciga przeciwnikw i zadawa 2 Obraenia kontaktowe na tick (ok. 5 na sekund).\nJeli Isaac ma 1/2 serca (1 serce w przypadku Eve), chowaniec pozostanie aktywny midzy pokojami (ale nie pomidzy pitrami). W przeciwnym wypadku zniknie po wyjciu z obecnego pokoju.\nPrzedmiot startowy Eve.",
  118: "3x Opnienie ez.\nZamienia zy Isaaca w adowany laser. Laser jest spektralny, przeszywajcy, ma nieskoczony zasig i zadaje Obraenia Isaaca przez 9 tickw (ok. 0,63 sekundy).\nPodniesienie drugiej kopii Brimstone'a da Isaacowi +1 Obrae, 1,2x mnonik Obrae, a laser bdzie 2x szerszy. Podniesienie dodatkowych kopii doda +1 Obrae.\nAzazel ma wbudowan mechanik podobn do Brimstone'a. Jego laser nie ma jednak nieskoczonego zasigu i aduje si duej.\nTainted Azazel ma rwnie wbudowan mechanik podobn do Brimstone'a. Jego laser jest jednak cieszy i zadaje 0,5x Obrae Isaaca na tick.",
  119: "+1 Czerwone Serce.\nLeczy 5 czerwonych serc przy podniesieniu.\n+0,3 Szybkoci.\nMa szans wypa z Blood Donation Machine po wpaceniu do niej.",
  12: "+1 Czerwone Serce.\n+0,3 Obrae.\n1,5x Obrae.\n+1,5 Zasigu.\n+0,3 Szybkoci.\n+0,5 Wysokoci ez.\nLeczy wszystkie czerwone serca.\nPowiksza Isaaca.\nMnonik obrae nie dodaje si do mnonika z Cricket's Head, poczenia Blood of the Martyr i The Book of Belial, ani samego siebie.\nMa szans wypa z grzybw.",
  120: "+1,7 ez.\n+0,3 Szybkoci.\nZmniejsza obraenia wg nastpujcego wzoru: (bonusy do Obrae z innych przedmiotw * 0,9) - 0,4.\nZmniejsza gow Isaaca, co, w poczeniu z innymi efektami zmniejszajcymi rozmiar, zmniejsza jego hitbox.",
  121: "+1 puste Czerwone Serce.\n+1 Obrae.\n+0,38 Zasigu.\n+0,5 Wysokoci ez.\n-0,2 Szybkoci.\nZwiksza gow Isaaca, co, w poczeniu z innymi efektami zmniejszajcymi rozmiar, zwiksza jego hitbox.",
  122: "Daje bonus +1,5 do Obrae i +0,3 do Szybkoci, gdy Isaac ma 1/2 czerwonego serca lub mniej.\nPrzedmiot startowy Eve. Dla niej bonus jest aktywowany przy posiadaniu 1 czerwonego serca lub mniej, i zwiksza jej mnonik obraen do x1.",
  123: "Przy uyciu przyzywa chowaca spord nastpujcych: ???'s Only Friend, Angry Fly, Ball of Bandages, Big Fan, Bob's Brain, Boiled Baby, Bot Fly, Brother Bobby, Cube of Meat, Demon Baby, Distant Admiration, Dry Baby, Forever Alone, Freezer Baby, Fruity Plum, Gemini, Guardian Angel, Halo of Flies, Headless Baby, Incubus, Juicy Sack, Leech, Lil Brimstone, Lil Delirium, Lil Haunt, Little Chubby, Little Gish, Little Steven, Psy Fly, Robo-Baby 2.0, Robo-Baby, Rotten Baby, Seraphim, Sissy Longlegs, Sister Maggy, Smart Fly, Succubus, Tinytoma.\nChowaniec utrzymuje si przez cae pitro.\nBox of Friends ani Mongo Baby nie kopiuj chowacw przywoanych przez Monster Manual.",
  124: "Przy uyciu wywouje efekt losowego przedmiotu aktywnego spord nastpujcych: Anarchist Cookbook, Best Friend, Bob's Rotten Head, The Book of Belial, Book of Revelations, Book of Shadows, The Book of Sin, Crack the Sky, Crystal Ball, Deck of Cards, Doctor's Remote, The Gamekid, The Hourglass, Lemon Mishap, Mom's Bottle of Pills, Mom's Bra, Mom's Pad, Monster Manual, Monstro's Tooth, Mr. Boom, My Little Unicorn, The Nail, The Necronomicon, The Pinking Shears, Prayer Card, Shoop Da Whoop!, Spider Butt, Tammy's Head, Telepathy for Dummies, Teleport, We Need to Go Deeper!, Yum Heart.\nJeli wylosowany efekt bdzie efektem przedmiotu, ktrym trzeba rzuci lub strzeli (np. Bob's Rotten Head lub Shoop Da Whoop!), adunek Dead Sea Scrolls nie zostanie zuyty, dopki efekt nie zostanie wykorzystany. Ponowne uycie Dead Sea Scrolls pominie ten efekt i wylosuje nowy.",
  125: "+5 bomb.\nBomby dostaj efekt samonaprowadzania.",
  126: "Przy uyciu zadaje, przy pierwszym uyciu, 1 serce obrae (czerwone serca maj priorytet), a przy nastpnych uyciach w pokoju 0,5 serca obrae, i daje Isaacowi +1,2 Obrae na czas trwania obecnego pokoju.\nEfekt nie wpywa na szans na Devil Room/Angel Room.\nPrzedmiot startowy Eve po wpaceniu 439 monet do Greed Donation Machine.",
  127: "Przy uyciu obecne pitro zostanie ponownie wygenerowane, a Isaac zacznie je, jakby zszed na nowe pitro.\nPo uyciu w Chest lub Dark Roomie, po tym, jak drzwi do Mega Szatana zostay otwarte, drzwi na nowym pitrze nie bd otwarte, a Isaac nie bdzie posiada klucza, co uniemoliwi walk z Mega Szatanem.\nJeli Forget Me Now zostanie zostawione w Treasure Roomie lub Boss Roomie, a Isaac powrci do niego podczas Ascentu, po uyciu go przedmiot nadal bdzie w danym pokoju na nowym pitrze, co umoliwi jego uycie nieskoczon liczb razy.",
  128: "Przyzywa muszk, ktra orbituje Isaaca w duej odlegoci od niego i zadaje 2 Obrae kontaktowych na tick (30 na sekund).\nMa szans wypa z Super Envy'ego.",
  129: "+2 Czerwone Serca.\nDodatkowo leczy 1/2 czerwonego serca.\n-0,2 Szybkoci.\nMoe wypa z Super Gluttony'ego.",
  13: "+0,2 Szybkoci.\nIsaac zadaje 24 Obrae kontaktowych na tick oraz zatruwa przeciwnikw, ktrzy go dotkn. Zatrucie zadaje (0,2 * x<sup>2</sup> + 2) Obrae przez 6-7 tickw, gdzie x = liczba czerwony serc i Bone Heartw Isaac posiada.\nMa szans wypa z Lusta.",
  130: "Daje Isaacowi latanie.\nJeli statystyka Szybkoci wynosi poniej 1,5, zwiksza j do 1,5.\nPrzy uyciu Isaac szaruje przez pokj, bdc nietykalnym i zadajc obraenia rwne 4 * statystyki Obrae + 28.\nWypada z Headless Horsemana.",
  131: "Przyzywa chowaca, ktry wyrzuca bomb co 3 pokoje. Pierwsze wyrzucenie nastpi po 2 pokojach.",
  132: "zy dostaj bonus do obrae zaleny od odlegoci, jak pokonay, okoo +0,5-0,6 na pytk.\nPoza Greed Mode'em moe wypa tylko z Krampusa lub z Mystery Gifta.",
  133: "Przy uyciu usuwa kontener Czerwonego Serca ze zdrowia Isaaca i zamienia go w 3 Soul Hearty.",
  134: "Daje 1/3 szansy na to, e nagroda za przejcie pokoju zostanie zamieniona na zwyk skrzynk lub Zot Skrzynk, ale te 1/3 szansy na to, e zostanie zamieniona w brak nagrody.",
  135: "Przy uyciu rani Isaaca na 1/2 serca lub 1 serce od Rozdziau 4 i generuje 1-2 monety na normalnym poziomie trudnoci i 1 monet na Hard modzie.\nW przypadku Keepera generuje 0-1 monet.\nMa szans wypa z Blood Donation Machine po wpaceniu do niej.",
  136: "Przyzywa kuk, ktra wybucha po 5 sekundach, zadajc 185 Obrae.",
  137: "+5 bomb pierwszy raz przedmiot jest podniesiony.\nPowoduje, e bomby postawione przez Isaaca nie wybuchaj automatycznie, dopiero uycie przedmiotu powoduje ich eksplozj.",
  138: "+0,3 Obrae.\n+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  139: "Generuje trinket przy podniesieniu.\nPozwala Isaacowi na posiadanie 2 trinketw na raz.",
  14: "+0,3 Szybkoci.\n+1,5 Zasigu.\n+0,5 Wysokoci ez.",
  140: "+5 bomb.\nBomby zatruwaj przeciwnikw i zostawiaj za sob trujc chmur.\nDaje Isaacowi odporno na trujce chmury, np. ze zniszczenia grzybw.\nMoe wypa z Super Slotha.",
  141: "Generuje 7 monet wok Isaaca.",
  142: 'Raz na pokj, gdy zdrowie Isaaca osignie 1/2 serca, dostaje on Soul Hearta.\nWyjcie i ponownie wejcie do pokoju pozwala na ponowne wykorzystanie efektu.\nPrzy Bone Heartach efekt aktywuje si i przy pustych, i przy w poowie napenionych sercach.\nrda obrae, ktre s "oddawaniem krwi" (Blood Donation Machine, Devil Beggar, Hell Game, Konfesjona, IV Bag, Razor Blade, Blood Rights, Dull Razor, Sharp Plug, AShard of Glass Shard of Glass, Blood Bombs, Blood Oath) nie aktywuj Scapulara. Kolce w Sacrifice Roomie aktywuje efekt.',
  143: "+0,3 Szybkoci.\n+0,2 Szybkoci Strzaw.",
  144: "Przyzywa chowaca, ktry poda za Isaaciem i zbiera z ziemi monety. Po zebraniu 6 centw, wyrzuca pierwsz nagrod. Nastpnie ma 67% szansy na wyrzucenie kolejnej nagrody po 3 centach i 33% szansy po 4 centach. Moliwe nagrody to czerwone serca, bomby, klucze, karty lub runy, piguki, trinkety, baterie oraz przedmioty z puli Beggara.\nLucky Penny podniesiony przez Bum Frienda nie da Isaacowi bonusu do Szczcia.",
  145: "Przy uyciu generuje 2-4 Niebieskie Muszki.",
  146: "Przy uyciu dodaje Eternal Heart do zdrowia Isaaca.",
  147: "Przy uyciu Isaac przestaje strzela zami i bdzie trzyma w rce kilof (jak ko The Forgottena), ktrym moe niszczy przeszkody, otwiera drzwi (wliczajc Secret Roomy) i atakowa przeciwnikw, zadajc im 3x swoich Obrae. Wykonanie jakiejkolwiek z tej akcji zmniejszy adunek Notched Axe. Gdy adunek zejdzie do 0 (po wykonaniu 64 akcji), Notched Axe nie moe zosta uyty a do nastpnego pitra.\nIsaac moe dowolnie przecza si midzy kilofem a zami.",
  148: "Przy otrzymaniu obrae Isaac generuje 2-6 niebieskich muszek.\nMa ma szans wypa z Huska.",
  149: "zy Isaaca zostaj zastpione wybuchajcymi i trujcymi pociskami, wystrzeliwanymi w uku.\n-(x * 2 + 10) ez, gdzie x = obecne Opnienie ez.\n+40 Obrae.\n0,4x wielkoci ez.\n-0,2 Szybkoci Strzaw.\nMinus do Zasigu.\nMnoniki obrae nie bior pod uwag bonusu do obrae z Ipecaca.\nWysoko ez skaluje si z Zasigiem.\nZasig eksplozji pociskw wzrasta z wielkoci ez.",
  15: "+1 Czerwone Serce.\nLeczy wszystkie czerwone serca Isaaca.\nMa szans wypa z Gluttony'ego.",
  150: "Daje szans na wystrzelenie zba zamiast zy, ktry zadaje x3,2 Obrae Isaaca.\nSzansa wynosi 1/(10 - Szczcie).",
  151: "zy maj szans 1/6 na wygenerowanie Niebieskiej Muszki, gdy uderzaj przeciwnika.",
  152: "zy na sekund * 2/3.\nzy z prawego oka Isaaca zastpione s przez laser o nieskoczonym zasigu, ktry zadaje 13% Obrae Isaaca na tick, 15 razy na sekund.\nLaser moe odpycha przeciwnikw.",
  153: "zy na sekund * 0,42.\nIsaac strzela 4 zami na raz.\nMaksymalna ilo ez na strza, jaka moe by osignita z poczenia The Inner Eye, 20/20, Mutant Spider i The Wiz to 20 na Keeperze i 16 na innych postaciach.",
  154: "zy wystrzelone z lewego oka Isaaca otrzymuj +2 Obrae.\nBonus nie liczy si do mnonikw obrae.\nCain ma 50% szansy na wystrzelenie zy z efektem Chemical Peel.",
  155: "Przyzywa chowaca, ktry lata ukonie po pokoju, blokujc pociski przeciwnikw i zadajc obraenia kontaktowe 8 na tick / 17,1 na sekund.\nzy wystrzelane z lewego oka Isaaca zadaj 34% wicej obrae.\nChowaniec ma wiksz renic ni oczy generowane przez Peepera.\nMa ma szans wypa z Bloata.",
  156: "Przy otrzymaniu obrae, przedmiot aktywny Isaaca otrzymuje 1 adunek.",
  157: "Przy otrzymaniu obrae Isaac dostaje bonus do Obrae, trwajcy przez cae pitro.\nPierwszy bonus +0,5, kady nastpny jest +0,2 od poprzedniego, a do +1,5 przy szstym uderzeniu.\nEfekty dodaj si do 6 razy, maksymalnie dajc +6 bonusu.\nPrzedmiot startowy Samsona, nawet jeli nie zosta jeszcze odblokowany.",
  158: "Przy uyciu pokazuje rozkad pitra na mapie, oprcz Super Secret Roomu i wyrzuca Soul Hearta (50% szansy) lub losow kart lub run (50% szansy).\nPasywnie zwiksza szans na Planetarium o 15%, lub 100%, jeli Isaac pomin ju Treasure Room.\nMa szans wypa z Fortune Telling Machine po wpaceniu do niej.",
  159: "Daje Isaacowi lot oraz zy spektralne.",
  16: "+2 Czerwone Serca.\nDodatkowo leczy wszystkie czerwone serca Isaaca.",
  160: "Przy uyciu przyzywa 5 promieni wiata z nieba, ktre zadaj 20 + Obraenia Isaaca, do 8 razy, i utrzymuj si przez 0,8 sekundy. Promienie maj wysok szans na bezporednie namierzenie przeciwnikw.",
  161: "Po mierci Isaac odradza si jako ??? z 3 Soul Heartami.\nTainted ??? zostanie odrodzony jako on sam, a nie ???.",
  162: "Daje Isaacowi szans na wywoanie efektu Book of Shadows przy otrzymaniu przez niego obrae, tworzc tarcz, ktra chroni go przed obraeniami przez 7 sekund.\nSzansa jest zalena od Szczcia i wynosi 100% przy Szczciu 27.",
  163: "Przyzywa chowaca, ktry poda za Isaaciem i co 2/3 sekundy strzela zami spektralnymi, ktre zadaj 3,5 Obrae.\nMa szans wypa z czaszek.",
  164: "Przy uyciu Isaac bdzie trzyma wieczk nad gow. Nacinicie klawisza strzau wyle pomie w danym kierunku. Pomie zadaje 23 Obrae na tick (zadaje obraenia ok. 6 razy na sekund) i wygasza si po ok. 2 sekundach.\nPomie jest w stanie rwnie zniszczy pociski przeciwnikw i Shopkeeperw oraz wygasi ogniska.",
  165: "+1 Obrae.\n+0,23 Szybkoci Strzau.",
  166: "Przy uyciu przelosowuje wszystkie znajdki, skrzynki i trinkety w obecnym pokoju.\nJeli znajdki/skrzynki/trinkety zostay podwojone przez (np. przez Jer lub Diplopi), oba egzemplarze zostan przelosowane w to samo.\nPrzelosowanie zotego trinketu w inny trinket spowoduje, e powstay trinket rwnie bdzie zoty. Przelosowanie w cokolwiek innego nada powstaej znajdce/skrzynce zoty poysk, ale jest to tylko efekt wizualny, ktry znika po wyjciu i ponownym wejciu do pokoju.",
  167: "Przyzywa chowaca, ktry poda za Isaaciem i co 2/3 sekundy strzela 2 zami na raz. Kada za zadaje 4 Obrae.\nMa ma szans wypa z Mr. Freda.",
  168: "zy Isaaca s zastpione przez rakiety, ktrymi mona celowa za pomoc celownika na ziemi.\nRakiety zadaj 20x Obraenia Isaaca.",
  169: "Obraenia = (Obraenia + 4) x 2.\n0,42x zy wystrzeliwane na sekund.\nJeli za zabije przeciwnika, leci dalej z pozostaymi jej obraeniami.",
  17: "+99 kluczy.",
  170: "Za Isaaciem bdzie poda cie Daddy'ego Long Legsa. ktry bdzie depta przeciwnikw, jednorazowo zadajc im 20 Obrae przez 2 ticki. Noga Daddy'ego Long Legsa pozostaje na ziemi przez ok. 1 sekund, a przeciwnicy, ktrzy w ni wejd, take otrzymaj obraenia.\nOdstp midzy kolejnymi nadepniciami to ok. 4 sekundy.\nMa ma szans wypa z Daddy'ego Long Legs.",
  171: "Spowalnia wszystkich przeciwnikw w pokoju na 4 sekundy i zadaje im 10 obrae.\nPrzeciwnicy zabici przez przedmiot wyrzucaj z siebie Niebieskie Pajki.",
  172: "Przyzywa orbitala, ktry blokuje pociski przeciwnikw oraz zadaje 15 Obrae co 2/15 sekundy (112,5 Obrae na sekund).",
  173: "Kade czerwone serce ma 33% szansy na bycie zmienionym w Soul Hearta.",
  174: "Przyzywa chowaca, ktry poda za Isaaciem i strzela zami z losowym efektem.\nMoliwe efekty:\n* krwawa za krtkiego zasigu - 3 Obrae\n* czerwona za - 3,5 Obrae\n* podwjny strza - 3,5 Obrae\n* oczarowujca rowa za - 3,5 Obrae\n* spowalniajca czarna za - 3,5 Obrae\n* za spektralna - 3,5 Obrae\n* samonaprowadzajca fioletowa za - 3,5 Obrae\n* samonaprowadzajca i spektralna za - 3,5 Obrae\n* krwawa za - 4 Obrae\n* wielka krwawa za - 5 Obrae",
  175: "Przy uyciu otwiera wszystkie drzwi w obecnym pokoju, wliczajc wszystkie drzwi wymagajce klucza lub bomby do otwarcia, przejcie do Mausoleum/Gehenny, Challenge Roomy i Boss Challenge Roomy, drzwi do Mega Szatana, przejcie do Boss Rusha od wewntrz, drzwi pokojw z mini-bossami od wewntrz i drzwi Boss Roomw od wewntrz.\nDad's Key **nie** moe otworzy drzwi do Boss Rusha ani Husha, jeli min limit czasowy, Angel Roomw i Devil Roomw, skrzynek i Key Blockw, przejcia do The Void w ???, Strange Door ani drzwi do Mom's Heart w Mausoleum II/Gehennie II.",
  176: "+0,16 Szybkoci Strzaw.\n+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  177: "Przy uyciu zabiera Isaacowi 1 monet i ma szans na wyrzucenie znajdki lub przyzwanie Pretty Fly, tak jak Slot Machine.",
  178: "Przyzywa chowaca, ktry wystrzeliwuje w kierunku, w ktrym Isaac strzela. Po uderzeniu w przeciwnika, zadaje 7 Obrae i rozbija si, tworzc kau niebieskiego luzu, ktry zadaje obraenia i unieruchamia przeciwnikw.\nGdy kaua zniknie, Holy Water odradza si.",
  179: "+1 Eternal Heart.\nDaje Isaacowi latanie.",
  18: "+100 monet.\nMa szans wypa ze Slot Machine po wpaceniu do niej.",
  180: "Przy otrzymaniu obrae Isaac puszcza 5 bkw, zadajc 5 Obrae i zatruwajc pobliskich przeciwnikw oraz pozostawiajc trujce chmury.\nTrucizna zadaje Obraenia Isaaca przez 6 tickw.",
  181: "Daje Isaacowi latanie.\nJeli statystyka Szybkoci wynosi poniej 1,5, zwiksza j do 1,5.\nPrzy uyciu Isaac szaruje przez pokj, bdc nietykalnym i zadajc obraenia rwne 4 * statystyki Obrae + 28, oraz przyzywa z nieba promienie wiata, ktre zadaj 4 * statystyki Obrae Isaaca + 10.\nWypada z Conquesta.",
  182: "+1 Czerwone Serce.\nLeczy wszystkie czerwone serca.\n2,3x mnonik obrae.\n+1 Obrae.\n+0,75 Wysokoci ez.\n-0,4 ez.\n-0,25 Szybkoci Strzaw.\nDaje Isaacowi samonaprowadzajce zy.",
  183: "+0,7 ez.\n+0,16 Szybkoci Strzaw.",
  184: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.\nDaje Isaacowi latanie.",
  185: "Daje Isaacowi latanie i zy spektralne.",
  186: "Przy uyciu zadaje Isaacowi, przy pierwszym uyciu, cae serce obrae (zabierajc czerwone serca jako pierwsze), a przy nastpnych uyciach w pokoju 0,5 serca obrae, i aktywuje efekt The Necronomicon, zadajc 40 Obrae wszystkim przeciwnikom w pokoju (obraenia te ignoruj skalowanie obrae.",
  187: "Przyzywa chowaca, ktry poda za Isaaciem, blokuje pociski przeciwnikw i Isaac moe si nim zamachiwa, aby zadawa obraenia.\nChowaniec zadaje (x * 2 + 3) Obrae na tick (2 ticki na sekund), gdzie x = poziom chowaca. Poziom chowaca wzrasta, gdy zabija on przeciwnika, do maksymalnie poziomu 5. Po przejciu na nastpne pitro, chowaniec zmniejsza poziom z 2 na 1, z 3 i 4 na 2, a z 5 na 3.\nHitbox chowaca jest taki sam niezalenie od poziomu.",
  188: "Przyzywa chowaca, ktrego ruchy s lustrzanym odbiciem ruchw Isaaca i strzela w jego kierunku co 1/2 sekundy, zadajc 3,5 Obrae.\nPrzy grze jako Cain, Abel zadaje 7,5 Obrae, a jego zy s czerwone.",
  189: "+1 Czerwone Serce.\n+0,3 Obrae.\n+0,2 ez.\n+0,2 Szybkoci.\n+0,5 Zasigu.\n+1 Wysokoci ez.\nLeczy wszystkie czerwone serca.",
  19: "+10 bomb.",
  190: "+99 bomb.",
  191: "Daje Isaacowi losowy efekt ez.\nEfekt zmienia si co kilka sekund.",
  192: "Daje Isaacowi +3 Zasigu oraz samonaprowadzajce strzay na czas trwania obecnego pokoju.",
  193: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.\n+0,3 Obrae.",
  194: "+0,16 Szybkoci Strzaw.\nGeneruje losow kart tarota przy podniesieniu.\nDaje dodatkowe 15% szansy na pojawienie si pierwszego Planetarium.",
  195: "Przy podniesieniu wyrzuca 4 losowe piguki.",
  196: "+0,4 ez.\nWyrzuca 2 Soul Hearty.",
  197: "+0,5 Obrae.\n+0,38 Zasigu.\n+0,5 Wysokoci ez.",
  198: "Generuje 1 monet, 1 klucz, 1 bomb, 1 Czerwone Serce, 1 kart lub run, 1 piguk i 1 trinket.",
  199: "+2 klucze.\nPowoduje, e kada skrzynka niebdca Red Chestem, ktra wygeneruje serca, monety, bomby i/lub klucze, wygeneruje drug grup tych znajdziek.",
  2: "Isaac wystrzeliwuje 3 zy na raz.\nzy na sekund * 0,51.\nMaksymalna ilo ez na strza, jaka moe by osignita z poczenia The Inner Eye, 20/20, Mutant Spider i The Wiz to 20 na Keeperze i 16 na innych postaciach.",
  20: "Pokazuje wszystkie pokoje na mapie, oprcz Secret Roomu i Super Secret Roomu.\nNie pokazuje zawartoci jeszcze nieodwiedzonych pokoi.",
  200: "Daje Isaacowi szans na wystrzelenie oczarowujcych ez.\nSzansa jest zalenia od Szczcia i wynosi 1/(10 - Szczcie/3).",
  201: "+0,3 Obrae.\nDaje Isaacowi szans na wystrzelenie dezorientujcych ez. Szansa jest zalena od Szczcia i wynosi 100% przy Szczciu 27.",
  202: "Przy kontakcie z Isaaciem, przeciwnicy otrzymuj obraenia rwny 3,5 + 0,2 * x, gdzie x = liczba monet Isaaca (obraenia s zadawane do 2 razy na sekund), oraz zostaj unieruchomieni i zamienieni w zoto. Jeli zostan zabici w tym stanie, wyrzuc 1-4 centw.\nZamienienie w zoto przeciwnicy, ktrzy zostan zamroeni, nie wyrzuc monet.\nZ Midas' Touch, efekty, ktre generuj kupy, mog wygenerowa Zot Kup.\nPrzy uyciu na Ultra Greedzie, jego pancerz zostanie zniszczony do 25% efektywnoci.",
  203: "Daje 50% szansy na zamienienie czerwonego serca, bomby, centa lub klucza w ich podwjne wersje.\nNie dziaa na powk czerwonego serca, Charged Key ani Scared Hearts.",
  204: "Daje Isaacowi 50% na wyrzucenie losowej monety, bomby, klucza lub serca przy otrzymaniu obrae.",
  205: "Uycie przedmiotu aktywnego, gdy jest nienaadowany, w peni go aduje w zamiana za serca. Jeden adunek kosztuje 1/2 serca.\nSharp Plug zabiera czerwone serca najpierw. Przedmiot nie moe zabi Isaaca, zawsze zostawia go minimalnie z 1/2 serca.",
  206: "+1 Obrae.\n-1 Opnienia ez.\nGowa Isaaca zostanie oddzielona od jego ciaa i bdzie je orbitowaa, blokujc pociski przeciwnikw i zadajc 7 Obrae na tick (105 na sekund). Isaac nadal strzela zami z gowy.",
  207: "Przyzywa orbitala, ktry zadaje 7 Obrae kontaktowych na tick (56 na sekund) i blokuje pociski przeciwnikw.\nZebranie kolejnego Ball of Bandages zmienia go w orbital, ktry co 0,66 sekundy strzela oczarowujcymi zami, zadajcymi 3,5 Obrae.\nZebranie kolejnego Ball of Bandage zmienia go w Bandage Girl, ktra ciga pobliskich przeciwnikw i zadaje im 5 Obrae kontaktowych na tick (10 na sekund), oraz strzela oczarowujcymi zami w tym samym kierunku, co Isaac.\nZebranie kolejnego Ball of Bandage zmienia go w wiksz Bandage Girl, ktra zadaje 10 Obrae kontaktowych na tick (20 na sekund), a przeciwnicy zabici przez ni maj szans na wyrzucenie penego czerwonego serca.\nZebranie kolejnych Ball of Bandages powtarza cykl.\nPrzedmiot wypada z walki z Jedcami Apokalipsy.",
  208: "+1 Obrae.\nZwiksza szans na pojawienie si championw o 15%.\nEfekt nie dziaa na bossw oraz w The Void.",
  209: "+5 bomb.\nPrzy wybuchu, bomby Isaaca dezorientuj wszystkich przeciwnikw w pokoju i zadaj im 10 Obrae.\nBomby lecz Brownie'ego, Dangle'a, Dingle'a i Cloga o 25% ich maksymalnego zdrowia. Dr. Fetus i Epic Fetus z efektem Butt Bombs nie powoduje leczenia.\nMa ma szans wypa z Colostomii.",
  21: "Pokazuje na mapie wszystkie pokoje specjalne, oprcz Secret Roomw i Super Secret Roomw.\nJest w stanie pokaza walk z Greedem w Sklepie lub Secret Roomie, w ten sposb ujawniajc pooenie Secret Roomu.",
  210: "Po staniu w miejscu i nieatakowaniu przez 1 sekund, Isaac zamienia si w nietykaln statu. Nacinicie jakiego klawisza (wliczajc postawienie bomby i uycie przedmiotu aktywnego lub karty/piguki) usuwa nietykalno.\nMa ma szans wypa z Great Gideona.",
  211: "Gdy Isaac otrzymuje obraenia, przyzywa 3-5 niebieskich pajkw.\nMa ma szans wypa z Widow.",
  212: "Przy mierci Isaac ma 50% szans na odrodzenie si z 1/2 Czerwonego Serca lub, jeli nie mia kontenerw czerwonych serc, z 1/2 Soul Hearta.\nGuppy's Collar jest szstym przedmiotem w kolejnoci aktywacji przedmiotw odradzajcych, po Soul of Lazarus, 1up!, drugim yciem Lazarusa, Dead Cat i Inner Child.",
  213: "-0,16 Szybkoci Strzaw.\nzy Isaaca otrzymuj tarcz, ktra blokuje pociski przeciwnikw.\nza Isaaca znika po zablokowaniu pocisku przeciwnika.",
  214: "+1,5 do statystyki Zasigu.\nPo otrzymaniu obrae, Isaac zostawia za sob czerwony luz, ktry zadaje przeciwnikom 2 punkty obrae na tick.",
  215: "Gwarantuje pojawienie si Devil Roomu lub Angel Roomu na kadym pitrze, na ktrym mog si one pojawi. Pokoje te nie znikn nawet po wyjciu z nich i z Boss Roomu.\nDziaa nawet wtedy, gdy Isaac pokona ju bossa i potem podnis Goat Head - wejcie do Devil Roomu/Angel Roomu zostanie wygenerowane.",
  216: "+3 Black Hearty.\n+1 Obrae.\nMa ma szans wypa z The Heretica.",
  217: "Leczy 1 czerwone serce przy podniesieniu.\nIsaac generuje Niebieskie Pajki, kiedy strzela zami. Maksymalnie 5 pajkw moe zosta wygenerowanych.\nSzansa na wygenerowanie pajka zaley od Szczcia i wynosi 100% przy Szczciu 10.",
  218: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.\nIsaac ma szans na uleczenie 1/2 serca, kiedy zegar osiga now minut (00:01:00, 00:02:00, itd.).\nRegeneracja nie zajdzie w Crawl Space.\nRegeneracja dziaa na Bone Hearty oraz monety Keepera i Tainted Keepera.\nMa szans wypa z polipw.",
  219: "+1 puste Czerwone Serce.\nDaje szans na wyrzucenia czerwonego serca po otrzymaniu obrae. Szansa zalena jest od Szczcia i wynosi 50% przy Szczciu 29.",
  22: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  220: "+5 bomb.\nPrzy wybuchu, bomby wyrzucaj z siebie 10 ez we wszystkich kierunkach. zy zadaj 25 + Obraenia Isaaca i synergizuj si z modyfikatorami ez Isaaca.",
  221: "zy Isaaca odbijaj si od cian, przeciwnikw, przeszkd i bomb.",
  222: "-2 do opnienia ez.\nPrzytrzymanie klawisza strzau powoduje, e zy zatrzymuj si w powietrzu. Puszczenia klawisza uwalnia je.",
  223: "+5 bomb.\nWybuchy nie zadaj Isaacowi obrae, za to lecz go 1/2 serca.\nOgniska i ognie przeciwnikw nie zadaj Isaacowi obrae, ale go nie lecz.",
  224: "-20%x mnonik Zasigu.\n-1 Opnienia ez.\n1,2x mnonik wielkoci ez.\nPrzy uderzeniu w ziemi, przeszkod, cian lub przeciwnika, zy Isaaca rozpadaj si na 4 mniejsze zy, ktre s mniejsze 0,6x od normalnych ez i zadaj 0,5 Obrae Isaaca.",
  225: "Przy otrzymaniu obrae, Isaac ma szans na wyrzucenie Soul Hearta. Szansa jest zalena od Szczcia i wynosi maksymalnie 50% przy Szczciu 22.\nPrzeciwnicy maj szans na wyrzucenie po mierci 1/2 Czerwonego Serca.\nMa ma szans wypa z Sisters Vis.",
  226: "+1 Czerwone Serce\n+1 Soul Heart\n+1 Black Heart",
  227: "+3 monety.\nPodniesienie tego przedmiotu na Keeperze lub Tainted Keeperza najpierw doda monety do ich zdrowia, a reszt do monet.\nPrzy otrzymaniu obrae, Isaac ma szans na wyrzucenie 1 lub 2 centw.\nW przypadku Keepera i Tainted Keepera, szansa jest na wyrzucenie 0 lub 1 centw.",
  228: "zy na sekund +0,5.\n-1 Opnienia ez.\nDaje szans na wystrzelenie zy z efektem strachu. Szansa jest zalena od Szczcia i wynosi 12,5% przy Szczciu 0, a 100% przy Szczciu 85.",
  229: "4,3x Opnienie ez.\nDaje Isaacowi adowany atak, ktry wyrzuca 14 ez, podobnie do ataku Monstro. Atak moe zosta wykonany tylko wtedy, gdy jest w peni naadowany.\nKada za ma losowy mnonik rozmiaru pomidzy 0,9x a 1,33x.\nMa ma szans wypa z Monstra II.",
  23: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  230: "+1,5 do Obrae.\n+0,2 do Szybkoci.\nZamienia wszystkie czerwone serca w czarne serca + daje 2 dodatkowe czarne serca. Nie usuwa Bone Heartw, ale je oprnia. W przypadku Keepera zmniejsza jego ycie do 1 monety, a Bethany do 1/2 czerwonego serca.\nDaje szans na zy z efektem strachu, zalen od Szczcia (12,5% przy 0 Szczcia, 100% przy 85 Szczcia).",
  231: "Daje szans na wystrzelenie spowalniajcego strzau. Szansa zaley od statystyki Szczcia i wynosi 100% dla Szczcia 18.\nIsaac zostawia za sob czarny spowalniajcy luz.",
  232: "+0,3 Szybkoci.\nSpowalnia przeciwnikw do 80% ich oryginalnej szybkoci i szybkoci atakw.\nEfekt jest odrbny od efektu spowolnienia i dodaje si z nim.",
  233: "+7 Wysokoci ez.\nDaje zy spektralne.\nzy orbituj Isaaca, dopki nie spadn na ziemi. Zmienienie kierunku strzelania przekierowuje zy.",
  234: "Kady zabity przeciwnik generuje Niebieskiego Pajka.\nNie dotyczy to sytuacji, w ktrej przeciwnicy s zabici przez co niekontrolowanego przez Isaaca (np. kolce, zauroczeni przeciwnicy, itp.). Przeciwnicy zabici przez Niebieskie Pajki take nie wygeneruj niebieskich pajkw.\nZniszczenie Shopkeepera wygeneruje Niebieskiego Pajka.\nPrzeciwnicy segmentowi (tacy jak Pin lub Chub) wygeneruj Niebieskiego Pajka za kady swj fragment.",
  236: "Isaac zmienia przeciwnikw w kup przy dotkniciu ich.\nEfekt nie dziaa na bossw.\nKupy znikaj po wyjciu z pokoju.",
  237: "+1,5 Obrae.\n-0,3 ez.\n2x mnonik wielkoci ez.\nDaje Isaacowi przeszywajce zy.",
  238: "Po poczeniu z Key Piece 2 otwiera drzwi do Mega Szatana w Chest/Dark Roomie.\nZwiksza szans na Angel Room.\nWypada z Uriela.",
  239: "Po poczeniu z Key Piece 1 otwiera drzwi do Mega Szatana w Chest/Dark Roomie.\nZwiksza szans na Angel Room.\nWypada z Gabriela.",
  24: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  240: "Losowo zwiksza 4 i zmniejsza 2 statystyki Isaaca.\nMoliwe zmiany to:\n* 1 Zdrowia\n* 1 Obrae\n* 0,5 ez.\n* 1,5 Zasigu.\n* 0,2 Szybkoci Strzau.\n* 0,2 Szybkoci.\n* 1 Szczcia.\nDodane statystyki ez mog przekroczy limit 5.\nZmiany statystyk s zachowane, nawet jeli przedmiot jest usunity, np. na Tainted Isaacu.",
  241: "Podwaja wszystkie znajdki i skrzynki, ktre s nagrodami za przejcie pokoju, ale ma 1/3 szansy na kompletne usunicie nagrody.",
  242: "Daje Isaacowi 50% szansy na nieotrzymanie obrae od pociskw, ktre go trafi.\nMa ma szans wypa z Mask of Infamy.",
  243: "Isaac otrzymuje tarcz, ktr moe blokowa pociski przeciwnikw.\nTarcza jest zwrcona w kierunku, w ktrym Isaac strzela. Moe by trzymana pod ktem przy uyciu 2 klawiszw strzau na raz, lub w jakimkolwiek kierunku na padzie.\nTarcza nie blokuje promieni ani wybuchw.",
  244: "Isaac otrzymuje laser o nieskoczonym zasigu (ktry nie zastpuje jego ez), ktry ma 1/6 szansy na wystrzeleniu co ok. 0,07 sekundy (rednio 2,5 raza na sekund).\nObraenia lasera skaluj si z obraeniami Isaaca.\nLaser ma szans na dostanie efektu losowego przedmiotu lub trinketu, w tym Hook Worm, Brain Worm, Spoon Bender, Tiny Planet, Mysterious Liquid, Rubber Cement.",
  245: "Isaac strzela dwoma zami na raz.\n0,8x statystyki Obrae.",
  246: "Pokazuje na mapie Secret Roomy i Super Secret Roomy.",
  247: "Wizualnie zwiksza rozmiar chowacw o 25% i zwiksza ich efektywno.\nChowace, ktrych obraenia (ez oraz kontaktowe) nie skaluj si z obraeniami Isaaca, otrzymuj 2 razy wiksze obraenia. luz nie podlega temu efektowi.\nChowace, ktre generuj znajdki co pewn liczb pokoi, wymagaj 1 pokj mniej.\nChowacom, ktre maj pewn szans na wyrzucenie znajdziek, szansa ta si zwiksza.\nChowace, ktre podnosz znajdki, wymagaj ich mniej (oprcz Dark Buma, na ktrego ten efekt nie dziaa).",
  248: "Zwiksza rozmiar i obraenia niebieskich pajkw, niebieskich muszek, szaraczy oraz chowacw-pajkw i chowacw-much.",
  249: "Po walce z bossem Isaac dostanie do wyboru 2 przedmioty. Moe wzi tylko jeden, drugi zniknie.\nEfekt nie dziaa na walk z Mam, nawet jeli The Polaroid/The Negative jeszcze nie zostay odblokowane.\nEfekt dziaa na przedmiot generowany po przejciu Boss Rushu.",
  25: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.\nJeli jaka pula przedmiotw zostanie w peni wyczerpana, od tego momentu bdzie zawiera tylko Breakfast.",
  250: "Wszystkie bomby-znajdki zostaj zmienione w podwjne bomby.",
  251: "Generuje 1 losow kart lub run.\nZamienia wszystkie piguki w karty (oprcz piguki w Sklepie, w ktrym Started Deck zostanie kupiony).\nIsaac moe nosi 2 karty na raz.",
  252: "Pozwala Isaacowi na trzymanie 2 piguek na raz.\nGeneruje losow piguk przy podniesieniu.\nWszystkie karty zostan zamienione w piguki, wliczajc kart obecnie trzyman przez Isaaca.\nCzaszka z X-em w Depths II/Necropolis II/Dank Depths II bdzie zawieraa piguk Telepills zamiast The Fool.\nPostacie, ktre maj aktywny item w miejscu na piguki, otrzymaj trzecie miejsce na piguki.",
  253: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.\n+1 Szczcia.",
  254: "+1 Obrae.\n+1,5 Zasig.\n+0,5 Wysokoci ez.\nPowysze statystyki odnosz si jedynie do ez wystrzeliwanych z lewego oka Isaaca.\nMa szans wypa z polipw.",
  255: "+0,5 ez.\n+0,2 Szybkoci Strzaw.",
  256: "+5 bomb.\nBomby postawione przez Isaaca bd zadaway przeciwnikom 16 Obrae kontaktowych, a po wybuchu zostawiaj za sob ogie. Ogie utrzymuje si przez 10 sekund i moe zablokowa 4 pociski przeciwnikw i/lub zada 4 ticki obrae. Zadaje 23 Obrae przy penej wytrzymaoci, 17,25 Obrae po otrzymaniu obrae raz i 11,5 Obrae po otrzymaniu obrae dwa i trzy razy.\nOgie nie zadaje obrae Isaacowi.\nIsaac wieci w ciemnoci, co moe pomc z Curse of Darkness lub ciemnymi pokojami w Mines/Ashpit.\nMa ma szans wypa z Hornfela.",
  257: "zy Isaaca podpalaj przeciwnikw oraz maj szans na wybuchnicie przy uderzeniu w przeciwnika, zostawiajc za sob ogie.\nWybuch zadaje Obraenia Isaaca. Ogie zadaje 4 ticki Obrae: 23 Obrae, 17,3 Obrae i 2 razy 11,5 Obrae. Ogie moe te zablokowa pociski przeciwnikw.\nOgie nie rani Isaaca, ale wybuch tak.\nSzansa na wystrzelenie wybuchowej zy jest zalena od Szczcia i wynosi 100% przy Szczciu 13.\nIsaac wieci w ciemnoci, co moe pomc z Curse of Darkness i ciemnymi pokojami w Mines/Ashpit.",
  258: "Przelosowuje przedmioty pasywne i statystyki przy podniesieniu i na pocztku kadego pitra.\nStatystyki dostaj losowy bonus lub minus:\n* Szybko, Szybko Strzaw: -0,2, 0 lub +0,2\n* Zasig, zy: -0,5, 0 lub +0,5\n* Obraenia, Szczcie: -1, 0 lub +1\nNie wpywa na trinkety, karty, piguki, Pretty Fly, The Polaroid, The Negative, Key Piece 1 i 2, Knife Piece 1 i 2 oraz przedmioty aktywne.\nTransformacje mog zosta usunite, jeli przedmioty, ktre je spowodoway, zostan zmienione.\nMissing No. nie moe zosta przelosowane przez wikszo zmieniajcych efektw w grze, wliczajc D4, Genesis, The Stars? i Tainted Edena. Jedyny sposobem na pozbycie si go jest Butter!.",
  259: "+1 Obrae.\nDaje szans na wystrzelenie zy z efektem strachu. Szansa jest zalena od Szczcia i wynosi 100% przy Szczciu 20.\nMa ma szans wypa z Dark One'a.",
  26: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  260: "+1 Black Heart\n+15% do Devil/Angel Roomu\nZapobiega kltwom oraz usuwa obecne (oprcz Curse of the Labyrinth).\nNie dziaa na ustalone kltwy w wyzwaniach Cursed!, Pitch Black oraz Ultra Hard.\nUsuwa kltwy wywoane przez piguki Amnesia i ???, ale nie zapobiega tym kltwom, gdy piguka jest zjedzona po podniesieniu Black Candle.",
  261: "+0,5 Obrae.\n2x wielkoci ez.\nzy zmniejszaj si i trac obraenia, im duej s w powietrzu, zaczynajc od mnonika 3x w bliskiej odlegoci. Po okrelonym czasie nie zadaj w ogle obrae.",
  262: "+1 Black Heart.\nZadaje 80 Obrae wszystkim przeciwnikom w pokoju, jeli przy otrzymaniu obrae cakowite zdrowie Isaaca bdzie wynosi 1 serce.\nDodaje bonus +40 Obrae do The Necronomicon i podobnych efektw (np. z Black Heartw).",
  263: "Wyrzuca losow run przy pierwszym podniesieniu.\nAktywuje efekt runy lub Soul Stone'a trzymanego przez Isaaca.\nPocztkowy czas adowania to 4 adunki, potem zaley od ostatniej aktywowanej karty:\n* 1 - Rune Shard, Soul of Lazarus\n* 2 - Ansuz, Berkano, Hagalaz, Soul of Judas\n* 3 - Blank Rune, Soul of ???, Soul of the Lost, Soul of Magdalene\n* 4 - Black Rune, Algiz, Ehwaz, Perthro, Soul of Eden, Soul of Eve, Soul of the Forgotten, Soul of Isaac, Soul of Jacob and Esau\n* 6 - Dagaz, Soul of Apollyon, Soul of Azazel, Soul of Bethany, Soul of Cain, Soul of Samson\n* 12 - Jera, Soul of the Keeper, Soul of Lilith",
  264: "Przyzywa muszk, ktra orbituje Isaaca, blokujc pociski przeciwnikw i zadajc 1,5 Obrae kontaktowych na tick (22,5 Obrae na sekund). Gdy Isaac otrzyma obraenia, muszka ciga przeciwnikw i ich atakuje, zadajc tyle samo obrae.\nMuszka nie zabiera jednego z 3 miejsc na orbitale.\nMa ma szans wypa z Min-Mina.",
  265: "Przyzywa chowaca, ktry poda za Isaaciem i blokuje pociski przeciwnikw. Po zablokowaniu pocisku ma 10% szansy na wywoanie efektu The Necronomicon, zadajc 40 Obrae wszystkim przeciwnikom w pokoju.\nMa szans wypa z czaszek.",
  266: "Przyzywa chowaca, ktry poda za Isaaciem i zostawia za sob biay luz spowalniajcy przeciwnikw oraz generuje 1-2 niebieskich pajkw po przejciu kadego pokoju.\nMa ma szans wypa z The Wretched.",
  267: "Przyzywa chowaca, ktry strzela laserem zadajcym 3,5 Obrae w przeciwnika, ktry znajdzie si z nim w jednej linii.\nChowaniec porusza si w stron, w ktr Isaac strzela.",
  268: "Przyzywa chowaca, ktry generuje Niebieskie Muszki, gdy wcinity jest klawisz strzau.\nMaksymalnie 1 muszka wygenerowana przez Rotten Baby moe istnie w tym samym czasie.",
  269: "Przyzywa chowaca, ktry poda za Isaaciem i generuje czerwony luz, ktry zadaje przeciwnikom, ktrzy nie lataj, 2 Obrae na tick.",
  27: "+0,3 Szybko.",
  270: "Przyzywa chowaca, ktry poda za Isaaciem i atakuje oraz ciga pobliskich przeciwnikw, zadajc 1,5 Obrae na tick (ok. 3,2 na sekund). Jeli Leech zabije przeciwnika, leczy Isaaca o 1/2 Czerwone Serce (lub 1 monet w przypadku Keepera/Tainted Keepera).\nW przypadku przeciwnikw, ktrzy rozpadaj si na kup misa i odradzaj (np. Globiny i ciemnoczerwone Championy), kade rozpadnicie zregeneruje 1/2 Czerwone Serca Isaaca.\nMa szans wypa z wiader w Downpour.\nMa ma szans wypa z Wormwooda.",
  271: "Przyzywa chowaca, ktry wyrzuca serce, monet, bomb lub klucz na zmian co 5 lub 6 pokoi.",
  272: "Przyzywa muszk, ktra lata po pokoju jak Boom Fly i wybucha przy dotkniciu przeciwnika, zadajc 60 obrae wszystkim w zasigu wybuchu i 10 obrae przeciwnikowi, ktrego dotkna.\nOdradza si 10 sekund po wybuchu lub po wejciu do nowego pokoju.\nWybuch synergizuje si z efektami bomb.",
  273: "Przyzywa chowaca, ktry wystrzeliwuje w kierunku przeciwnikw, gdy Isaac strzela, zadajc im 3,5 Obrae, wybuchajc i zatruwajc przeciwnikw.\nWybuch zadaje 100 Obrae i ignoruje Pancerz.\nZatrucie zadaje 2x Obraenia Isaaca przez jaki czas.\nWybuch moe rwnie zrani Isaaca.",
  274: "Po otrzymaniu obrae przyzywa na czas trwania pokoju muszk, ktra orbituje Isaaca w redniej odlegoci (podobnie do Forever Alone) i zadaje 10 Obrae kontaktowych na tick.",
  275: "Przyzywa chowaca, ktry, po naadowaniu, strzela cienkim laserem Brimstone'a. Laser zadaje 3 Obrae na tick i moe uderzy do 8 razy.\nLil Brimstone ma najwyszy priorytet w kolejce chowacw oprcz Incubusa.",
  276: "Isaac staje si nietykalny, ale dostaje chowaca-serce, ktre przyjmuje obraenia za niego. Serce ustawia si za Isaaciem, w przeciwnym kierunku do tego, w ktrym Isaac strzela.\nSerce aduje atak, gdy Isaac strzela. Uwolnienie ataku powoduje, e serce odpycha przeciwnikw i ich pociski, generuje ma kau czerwonego luzu oraz strzela 8 zami we wszystkich kierunkach. zy zadaj 8 Obrae.\nIsaac otrzymuje 7 Obrae kontaktowych, ktre zadaj obraenia 2-3 ticki na sekund.\nUniemoliwia Isaacowi uycie Sacrifice Roomu.\nLasery Husha oraz promienie wiata Isaaca nie zadaj sercu obrae.\nDrzwi do przejcia do Mausoleum/Gehenny zadadz Isaacowi obraenia.",
  277: "Przyzywa chowaca, ktry ciga przeciwnikw, zadajc im 2 Obraenia kontaktowe na tick (4 na sekund) i nadajc im efekt strachu.\nMa ma szans wypa z The Haunta.",
  278: "Przyzywa chowaca, ktry poda za Isaaciem i zbiera z ziemi czerwone serca. Za kade 1 i 1/2 serca, ktre zbierze, wyrzuca jedn z nastpujcych nagrd:\n* Black Heart - 40% szansy\n* piguka - 20% szansy\n* karta lub runa - 20% szansy\n* niebieski pajk - 10% szansy\n* wrogi pajk - 10% szansy\nDark Bum nie podniesie Blended Heartw ani Rotten Heartw.",
  279: "Przyzywa duego orbitala, ktry blokuje pociski przeciwnikw i zadaje 2 Obrae kontaktowych na tick.",
  28: "+0,3 Szybkoci.",
  280: "Przyzywa chowaca, ktry chodzi po pokoju i generuje Niebieskie Pajki (maksymalnie 5 na raz), jeli w pokoju s przeciwnicy.\nZauracza przeciwnikw na krtk chwil przy kontakcie.",
  281: "Przyzywa chowaca, ktry chodzi po pokoju, blokuje pociski przeciwnikw oraz stanowi przynt dla przeciwnikw.\nJeli przeciwnicy s bliej do Punching Baga ni do Isaaca, bd atakowa chowaca, przez maksymalnie 6 sekund. Po tym czasie z powrotem bd atakowa Isaaca, zauwaajc Punching Baga po nastpnych 6 sekundach.",
  282: "Przy uyciu, Isaac skacze w kierunku, w ktrym si porusza. Dugo skoku zaley od szybkoci poruszania si Isaaca i tego, jak dugo zosta przytrzymany przycisk aktywacji przedmiotu.\nPodczas skoku Isaac jest odporny na obraenia i nie moe podnosi znajdziek.\nIsaac nie moe przeskakiwa przez drzwi.",
  283: "Przy uyciu wywouje efekt D1, D4, The D6, D7, D8, D10, D12 oraz D20.",
  284: "Przy uyciu przelosowuje kady przedmiot pasywny Isaaca. Nowe przedmioty s brane z tych samych pul, z ktrych wzite zostay oryginalne.\nOryginalne przedmioty wracaj do pul i Isaac moe je ponownie napotka.\nD4 nie przelosowuje przedmiotw fabularnych ani Missing No..\nD4 moe przelosowa przedmioty w te, ktre jeszcze nie zostay odblokowane. Licz si one do Collection Page.\nPrzelosowanie przedmiotw, ktre daj Isaacowi znajdki (np. A Dollar lub Pyro), nie zabiera znajdziek, ale przelosowanie przedmiotw, ktre daj Isaacowi serca, usuwa te serca. Podobnie przelosowanie przedmiotw, ktre daj Isaacowi transformacj, usuwa transformacj.\nPrzelosowanie w przedmioty, ktre normalnie dayby Isaacowi serca, nie dodaj serc.\nIstnieje szansa, e D4 nie przelosuje kadego przedmiotu.",
  285: "Przy uyciu przelosowuje wszystkich przeciwnikw w obecnym pokoju w sabszych przeciwnikw.\nKady przeciwnik ma okrelonego sabszego przeciwnika, w ktrego si przelosowuje. Przeciwnicy na najniszym poziomie w hierarchii po uyciu D10 znikaj lub zostaj przelosowani w przeszkod lub znajdk.",
  286: "Aktywuje efekt karty trzymanej przez Isaaca.\nUycie na ? Card teleportuje do pokoju I AM ERROR, lub, jeli nie jest to moliwe, przyzywa Shopkeepera z pokoju I AM ERROR.\nPocztkowy czas adowania to 4 adunki, potem zaley od ostatniej aktywowanej karty:\n* 1 - Rules Card, Suicide King\n* 2 - The Fool, The Magician, The High Priestess, The Hermit, The Stars, The Moon, Joker, Get out of Jail Free Card, The Emperor?, The Chariot?, The Hermit?, The Moon?, The World?\n* 3 - The Empress, The Chariot, Strength, The Hanged Man, Death, The Devil, The Tower, The World, Huge Growth, Era Walk, Strength?\n* 4 - The Emperor, Wheel of Fortune, The Magician?, The High Priestess?, The Empress?, The Lovers?, Wheel of Fortune?, Death?, The Devil?, The Tower?, Holy Card\n* 6 - The Lovers, Justice, Temperance, Judgement, Ace of Clubs, Ace of Diamonds, Ace of Hearts, Ace of Spades, Chaos Card, Credit Card, A Card Against Humanity, The Hanged Man?, Temperance?, The Sun?, Wild Card\n* 12 - The Hierophant, The Sun, 2 of Clubs, 2 of Diamonds, 2 of Hearts, 2 of Spades, Ancient Recall, The Fool?, The Hierophant?, Justice?, The Stars?, Judgement?, Queen of Hearts",
  287: "Przy uyciu wywouje efekt Treasure Map, The Compass lub Blue Map na czas trwania obecnego pitra.\nPonowne uycie przedmiotu wywoa efekt, ktrego Isaac jeszcze nie otrzyma. Jeli otrzyma on ju wszystkie efekty lub nie moe ich otrzyma (np. przez Curse of the Lost), ponownie uycie da mu efekt X-Ray Vision.",
  288: "Przy uyciu generuje 4-8 Niebieskich Pajkw.\nMa ma szans wypa z Rag Mana.",
  289: "Przy uyciu Isaac bdzie trzyma wieczk nad gow. Nacinicie klawisza strzau wyle pomie w danym kierunku. Pomie zadaje 23 Obrae na tick (zadaje obraenia maksymalnie 5 razy ) i wygasza si po 10 sekundach.\nPomie jest w stanie rwnie zniszczy pociski przeciwnikw i Shopkeeperw . Zniszczenie pociskw przeciwnikw powoduje zmniejszenie rozmiaru ogniska, a do jego wygaszenia.",
  29: "Generuje 3-6 niebieskich muszek przy podniesieniu.\n+1,5 Zasigu.\n+0,5 Wysokoci ez.",
  290: "Podniesienie czerwonego serca w momencie, gdy Isaac nie ma pustych kontenerw serc, dodaje je do soika, maksymalnie 4 serca.\nPrzy uyciu, wyrzuca serca na ziemi.",
  291: "Przy uyciu zmienia wszystkich przeciwnikw w kupy oraz zalewa pokj wod, wygaszajc zwyke i czerwone ogniska.\nNie dziaa na bossy, mini-bossy, Mom's Hand, Wizooby oraz Lil' Haunty podczas walki z The Hauntem. \nNatychmiast zabija Dingle, Dangle, Brownie, Cloga i Turdlings.\nZamienia law w Mines w kamie, co pozwala na chodzenie po niej.\nNa kupy wygenerowane przez ten przedmiot nie dziaa Petrified Poop i nie lecz one Isaaca, jeli ma on transformacj Oh Crap.\nMa ma szans wypa z Cloga.",
  292: "Przy uyciu daje Isaacowi 1 Black Hearta oraz powoduje, e z walki z bossem wypada przedmiot z puli Devil Roomu, ktry musi zosta kupiony za serca (liczy si to jako wzicie ukadu z Diabem).\nEfekt nie dziaa na The Polaroid, The Negative, Dad's Note, Boss Roomy w The Void, ani nagrod po przejciu Boss Rusha.\nBossy z ustalonym przedmiotem, ktry wyrzucaj, nadal wyrzuc ten przedmiot, ale bdzie on kosztowa 1 serce.\nPodczas Ascentu przedmioty, ktre nie zostay wzite, mog by zakupione za 15 monet.",
  293: "Przy uyciu wystrzeliwuje laser Brimstone'a prostopadle w 4 kierunkach.\nLaser zadaje 10 Obrae przez 20 tickw.\nWypada z Krampusa.",
  294: "Przy uycia, Isaac puszcza bka, ktry odpycha pobliskich przeciwnikw i pociski. Przeciwnicy, ktrzy zostan odepchnity w cian lub przeszkod, otrzymuj 10 Obrae.",
  295: "Przy uyciu zuywa 1 monet i zadaje obraenia wszystkim przeciwnikom w pokoju. Obraenia s rne 10 + 2x Obraenia Isaaca.",
  296: "Przy uyciu, zamienia jeden Soul Heart lub Black Heart ze zdrowia Isaaca w Czerwone Serce.\nUycie przedmiotu na Black Heartach aktywuje ich efekt zadawania obrae.",
  297: "Przy uyciu generuje nagrody zalene od pitra:\n* I pitro Rozdziau 1 i Rozdziau 1,5 - 2 Soul Hearty\n* II pitro Rozdziau 1 i Rozdziau 1,5 oraz Rozdzia 1 w Greed Modzie - 2 bomby i 2 klucze\n* I pitro Rozdziau 2 i Rozdziau 2,5 - 1 przedmiot z puli Boss Roomu\n* II pitro Rozdziau 2 i Rozdziau 2,5 oraz Rozdzia 2 w Greed Modzie - 1 przedmiot z puli Boss Roomu oraz 2 Soul Hearty\n* I pitro Rozdziau 3 i Rozdziau 3,5 - 4 Soul Hearty\n* II pitro Rozdziau 3 i Rozdziau 3,5 oraz Rozdzia 3 w Greed Modzie - 20 monet\n* I pitro Rozdziau 4, Corpse I oraz Rozdzia 4 w Greed Modzie - 2 przedmioty z puli Boss Roomu\n* II pitro Rozdziau 4 oraz Corpse II - The Bible\n* ??? - nic\n* Sheol oraz Sheol w Greed Modzie - 1 przedmiot z puli Devil Roomu oraz 1 Black Heart\n* Cathedral - 1 przedmiot z puli Angel Roomu oraz 1 Eternal Heart\n* Dark Room - nic (odblokowuje Moving Box)\n* Chest oraz The Shop i pitro Ultra Greeda w Greed Modzie - 1 moneta\n* The Void - nic\n* Home - Red Key\nPitra XL licz si jako pierwsze pitro.\nJeli pula, z ktrej Pandora's Box normalnie wygenerowaaby przedmiot, jest pusta, wygeneruje przedmiot z puli Treasure Roomu.\nEfekt Pandora's Box nie jest podwojony przez Car Battery.\nJeli Isaac posiada Strange Key, Pandora's Box zamiast swojego normalnego efektu wygeneruje 6 przedmiotw z losowych pul.",
  298: "Przy uyciu daje nietykalno i +0,28 Szybkoci na 6 sekund, ale Isaac nie moe strzela zami.",
  299: "-0,3 Szybkoci.\nW pokoju z przeciwnikami, Isaac stopniowo zyskuje szybko w tempie +0,065 na tick. Kiedy jego szybko osignie 2, otrzymuje on efekt My Little Unicorn na 5 sekund (jest nietykalny, ale nie moe strzela).\nPo zejciu efektu, Isaac traci bonus do szybkoci.",
  3: "Daje Isaacowi samonaprowadzajce zy.\nMa ma szans wypa z Rag Mega.",
  30: "+1,5 Zasigu.\n+0,5 Wysokoci ez.\nDaje Isaacowi 12 Obrae kontaktowych na tick (24 na sekund).",
  300: "+0,25 do Szybkoci.\nSzarowanie w przeciwnikw z du prdkoci zadaje im 25 Obrae i zapobiega dostaniu obrae kontaktowych.",
  301: "+3 Soul Hearty.\nPrzy otrzymaniu obrae przez Isaaca aktywuje efekt The Wafer (wszystkie obraenia s zredukowane do 1/2 serca) na czas trwania obecnego pokoju.",
  302: "Zwiksza rozmiar Isaaca i pozwala mu na niszczenie przeszkd przez wchodzenie w nie.",
  303: "Daje Isaacowi szans na wywoanie efektu Book of Shadows po otrzymaniu obrae, ktry daje mu tarcz chronic przez obraeniami przez 7 sekund. Szansa zaley od statystyki Szczcia i wynosi 100% przy Szczciu 10.\nZmienia wszystkie negatywne piguki w pozytywne, ale ich nie identifykuje.",
  304: "+6 monet.\n+6 bomb.\n+6 kluczy.\nBalansuje statystyki Obrae, Zasigu, ez oraz Szybkoci Isaaca. Efekt nie jest jednorazowy i bdzie aplikowany za kadym razem, gdy statystyki Isaaca bd zmieniony, wliczajc tymczasowe zmiany.",
  305: "Daje Isaacowi trujce zy. Trucizna rani przeciwnikw 2 razy, zadajc w sumie 2x Obraenia Isaaca.",
  306: "+0,2 Szybkoci.\nIsaac dostaje przeszywajce zy.",
  307: "+1 Czerwone Serce.\n+0,5 Obrae.\n-1 Opnienia ez.\n+0,1 Szybkoci.\n+0,75 Zasigu.\n+1 moneta.\n+1 bomba.\n+1 klucz.",
  308: "Isaac wytwarza niebieski luz, ktry zadaje 66% jego Obrae na tick.\nluz synergizuje si z efektami ez.\nMa ma szans wypa z Beelzebuba.",
  309: "-1 Opnienie ez.\n1,25x wielko ez.\nZwiksza odrzut ez.",
  31: "+2,25 Zasigu.\n+0,5 Wysokoci ez.\nPrzy podniesieniu generuje 1 losowe serce.",
  310: "2x mnonik Obrae.\n-0,5 Szybkoci Strzaw.\n0,66x mnonik ez / 1,5x mnonik Opnienia ez.",
  311: "Po mierci odradza Isaaca w poprzednim pokoju jako Dark Judas.\nLazarus' Rags jest dziesitym przedmiotem w kolejnoci aktywacji przedmiotw odradzajcych, przed Missing Poster i Birthright.\nTainted Judas zostanie odrodzony jako on sam, a nie Dark Judas.",
  312: "+1 Czerwone Serce.\nWarto czerwonych serc jest podwojona: 1/2 Czerwonego Serca leczy 1 serce Isaaca, Czerwone Serce/Przestraszone Serce leczy 2, a Podwjne Serce leczy 4.\nBlended Hearty lecz 2 czerwone serca lub 1 czerwone serce + 1 Soul Heart, ale nie mog doda 2 Soul Heartw.\nNie ma wpywu na Rotten Hearty.\nNie dziaa na monety Keepera i Tainted Keepera.",
  313: "Daje Isaacowi tarcz, ktra chroni go przed jednym uderzeniem. Tarcza odnawia si po przejciu do nastpnego pokoju.\nPo zablokowaniu uderze przez Holy Mantle, Isaac otrzymuje 1 sekund nietykalnoci.\nEfekt Holy Card dodaje si z Holy Mantle.\nThe Lost posiada Holy Mantle po wpaceniu 879 monet do Greed Donation Machine. Technicznie nie jest to przedmiot startowy (nie moe zosta przelosowany i znika przy zmianie postaci np. przy uyciu Clickera lub odrodzeniu jako inna posta), ale cz postaci. Efekt wbudowanego Holy Mantle The Losta nie dodaje si z efektem przedmiotu Holy Mantle.",
  314: "+1 Czerwone Serce.\nLeczy dodatkowe 1 czerwone serce.\n-0,4 Szybkoci.\nIsaac niszczy przeszkody, w ktre wejdzie.\nWejcie w Czerwon Kup nie zada Isaacowi obrae.\nWejcie w statu Anioa w Angel Roomie obudzi go, a Isaac dostanie od niego obraenia kontaktowe.\nMa ma szans wypa z Mega Fatty'ego.",
  315: "Przeciwnicy, znajdki oraz trinkety s magnetycznie przycigane do miejsc, gdzie spadaj zy Isaaca.",
  316: "Daje Isaacowi moliwo adowania ez. Atak po penym naadowaniu wystrzeli 5 ez, mniej przy niepenym naadowaniu.\nJeli Isaac otrzyma obraenia podczas adowania ataku (nie gdy jest w peni naadowy) spowoduje wyteleportowanie Isaaca do losowego pokoju.\nObecny stan ataku jest zasygnalizowany przez oko Isaaca - jest czarne, gdy atak jest adowany, a gdy jest w peni naadowany, miga na czarno i biao.",
  317: "W momencie uderzenie przeciwnika lub spadnicia na ziemi, zy Isaaca generuj ma kau zielonego luzu.\nluz zadaje 1 Obrae na tick (30 na sekund) i znika po chwili.",
  318: "Przyzywa chowaca, podobnego do Suture'a (mniejszej czci bossa Gemini), ktry atakuje przeciwnikw bliskich do Isaaca, zadajc 3 Obrae na tick (ok. 8 na sekund).\nMa ma szans wypa z Gemini.",
  319: "Przyzywa chowaca, ktry poda za Isaaciem i losowo strzela w czterech kierunkach, kopiujc wszystkie efekty ez Isaaca, ale zadajc 25% mniej Obrae.",
  32: "+0,7 ez.",
  320: "Przyzywa muszk, ktr mona kontrolowa klawiszami strzaw, i ktra zadaje 3,5 punkty Obrae na tick.",
  321: "Isaac otrzymuje kul na acuchu, ktr cignie za sob i ktra blokuje pociski przeciwnikw i zadaje 5 Obrae na tick (ok. 10,7 Obrae na sekund).\nJeli kula osignie wystarczajca prdko, moe zniszczy przeszkody.",
  322: "Przyzywa chowaca, ktry co 1/3 sekundy strzela zami innych chowacw Isaaca, po kolei przeczajc si midzy nimi.\nChowace, ktre Mongo Baby moe skopiowa, to: Brother Bobby, Ghost Baby, Harlequin Baby, Lil Loki, Little Gish, Little Steven, Rainbow Baby, Robo-Baby, Rotten Baby, Sister Maggy, Demon Baby, Freezer Baby.\nW przypadku Lil Lokiego, zy Mongo Baby bd czerwone, ale nie bdzie on strzela w 4 kierunkach.\nMongo Baby nie bdzie kopiowa ez chowacw przyzwanych przy pomocy Monster Manual.",
  323: "Przy uyciu wystrzeliwuje 8 ez we wszystkich kierunkach. zy posiadaj Zasig oraz Szybko Strzaw Isaaca i synergizuj si z efektami jego ez (ale nie z przedmiotami zastpujcymi zy, np. Brimstone'em). zy posiadaj rwnie Obraenia Isaaca + 5.\nUycie zajmuje 0,1 sekundy, podczas ktrych Isaac nie moe normalnie strzela zami.\nKada za normalnie wystrzelona przez Isaaca daje przedmiotowi 1 adunek.",
  324: "Teleportuje Isaaca do Treasure Roomu, Secret Roomu, Super Secret Roomu, pokoju I AM ERROR, lub, z bardzo ma szans, do Black Marketu.\nNie ma adnego efektu w Home.",
  325: "Przy uyciu oddziela gow Isaaca od jego ciaa na czas trwania obecnego pokoju. Gowa jest nieruchoma i strzela zami posiadajcymi statystyki i modyfikatory ez Isaaca w kierunku, w ktrym strzela Isaac. Ciao jest kontrolowane przez gracza i wystrzeliwuje moliw do kontrolowania fontann krwi, ktra zadaje 0,66-1,01x Obrae Isaaca.\nUycie przedmiotu kilka razy w pokoju wygeneruje wicej gw, ale tylko ostatnia moe strzela.",
  326: "Przytrzymanie klawisza aktywacji stopniowo zmniejsza adunek. Gdy adunek zostanie zmniejszony do zera, daje Isaacowi chwil nietykalnoci. Podczas gdy Isaac jest nietykalny, zadaje 3,5 Obrae kontaktowych, a promienie wiata uderz przeciwnikw, ktrzy go dotkn, zadajc 15 Obrae.\nDotknicie przeciwnika lub pocisku, gdy aktywuje si nietykalno, spowoduje wystrzelenie w czterech kierunkach promieni wiata, ktre zadaj 12 Obrae na tick, w sumie 180 Obrae.\nPrzytrzymanie klawisza aktywacji 1 sekund duej po otrzymania nietykalnoci zada Isaacowi obraenia.\nPrzedmiot moe zosta ponownie uyty tylko wtedy, gdy w peni si naaduje.",
  327: "Jeli, po otrzymaniu obrae, Isaac ma 1/2 czerwonego serca lub mniej, aktywuje efekt Book of Shadows, dajc mu na 5 sekund tarcz chronic przed obraeniami.\nUmoliwia przejcie do Chest po pokonaniu Isaaca w Cathedral.\nUmoliwia wejcie do Strange Door w Depths II/Necropolis II/Dank Depths II.\nPo odblokowaniu zawsze wypada z walki z Mam.",
  328: "+1 Obrae.\nJeli, po otrzymaniu obrae, Isaac ma 1/2 czerwonego serca lub mniej, aktywuje efekt The Necronomicon, zadajc 40 Obrae wszystkim przeciwnikom w pokoju.\nUmoliwia przejcie do Dark Roomu po pokonaniu Szatan w Sheolu.\nUmoliwia wejcie do Strange Door w Depths II/Necropolis II/Dank Depths II.\nPo odblokowaniu zawsze wypada z walki z Mam.",
  329: "zy Isaaca zastpione s jedn wielk z, ktr Isaac moe kontrolowa i ktra zadaje obraenia, gdy unosi si nad przeciwnikiem.\nSzybko zy zaley od statystyki Szybkoci Strzaw, a liczba uderze na sekund od statystyki ez.\n+0,2 Szybkoci Strzaw.",
  33: "Daje Isaacowi latanie na czas trwania obecnego pokoju.\nNatychmiastowo zabija Mam (take t w Mauzoleum), Mom's Heart (take to w Mauzoleum) i It Lives.\nZabija Isaaca, jeli zostanie uyta na Szatanie.",
  330: "5,5x szybkostrzelno.\n0,2x Obraenia.\n0,5x rozmiar ez.\nZmniejsza odrzut ez.\nPrzedmiotw, ktre zastpuj zy i ktre trzeba adowa, takie jak Brimstone, Chocolate Milk czy Monstro's Lung, nie trzeba wicej adowa i mog by uywane bez przerwy.",
  331: "+0,5 Obrae.\n-0,3 ez.\n-0,3 Szybkoci Strzaw.\n+0,8 Wysokoci ez.\nzy Isaaca otrzymuj efekt samonaprowadzania oraz aur, ktra zadaje 2 Obrae na tick.",
  332: "Po mierci odradza Isaaca w tym samym miejscu, w ktrym umar, jako Lazarus Risen.\nLazarus' Rags jest sidmym przedmiotem w kolejnoci aktywacji przedmiotw odradzajcych, po Soul of Lazarus, 1up!, drugim yciem Lazarusa, Dead Cat, Inner Child i Guppy's Collar.\nTainted Lazarus zostanie odrodzony jako on sam, a nie Lazarus Risen.",
  333: "Daje efekt Treasure Map, Blue Map i The Compass, odsaniajc ca map wraz z zawartoci wszystkich pokoi, wliczajc Secret Roomy i Super Secret Roomy.",
  334: "+3 Czerwone Serca.",
  335: "+2 Soul Hearty.\nIsaac otrzymuj aur, ktra odpycha pociski przeciwnikw.\nAura ma co chwil inn si, czasami tylko spowalnia pociski, czasami cakowicie zmienia kierunek ich lotu.",
  336: "-1,5 Zasigu.\n-0,4 Szybkoci Strzau.\n-0,5 Wysokoci ez.\n1,5x mnonik wielkoci ez.\nDaje Isaacowi zy spektralne i przeszywajce.",
  337: "Co 4 nowo odkryte pokoje, wszyscy przeciwnicy w pokoju i ich ataki s spowolnione, ale istnieje maa szansa, e zamiast tego zostan przypieszone o 40%. Efekt znika po wyjciu z pokoju.",
  338: "Przy uyciu Isaac bdzie trzyma w rce bumerang. Nacinicie klawiszu strzau rzuci bumerang w danym kierunku. Bumerang zadaje przeciwnikom Obraenia Isaaca i paraliuje ich na 2 sekundy, oraz apie znajdki i przynosi je do Isaaca.\nZasig bumerangu skaluje si z Zasigiem Isaaca.\nBumerang jest w stanie zniszczy Shopkeeperw oraz zada obraenia i sparaliowa Hosty i Walking Hosty, nawet jeli nie s one otwarte, oraz The Haunta i Lil' Haunty, nawet jeli s nietykalne.\nPrzedmiot aduje si ponownie dopiero, gdy bumerang wrci do Isaaca.",
  339: "+1 Black Heart.\n+1,5 Zasigu.\n+0,5 Wysokoci ez.\n+0,16 Szybkoci Strzaw.",
  34: "+2 Obrae na czas trwania obecnego pokoju.\nZwiksza szans na Devil Room/Angel Room o 12,5% (jeli Isaac ma przedmiot Schoolbag, szansa jest zwikszona tylko jeli The Book of Belial jest na pierwszym miejscu).\nEfekt dodaje si przy kilkukrotnym uycie przedmiotu oraz z kart XV - The Devil.\nJeli Isaac posiada Blood of the Martyr, The Book of Belial daje mu dodatkowy mnonik obrae 1,5x. Mnonik nie dodaje si z tym z Magic Mushroom i Cricket's Head.\nPrzedmiot startowy Judasa.",
  340: "+0,3 Szybkoci.\nZmniejsza rozmiar Isaaca.\nDaje Isaacowi losow piguk. Jeli Isaac posiada ju piguk/kart/itp., zostanie ona wyrzucona na ziemi.",
  341: "+0,7 ez.\n+0,16 Szybkoci Strzaw.",
  342: "+1 Czerwone Serce.\nLeczy 2 czerwone serca.\n+0,7 ez.\n-0,16 Szybkoci Strzaw.",
  343: "+1 Soul Heart.\n+1 Szczcia.\nGeneruje 2 klucze przy podniesieniu.",
  344: "+1 Black Heart.\nGeneruje 2-3 bomby oraz Match Stick.",
  345: "+1 Obrae.\n+1,5 Zasigu.\n+0,5 Wysokoci ez.",
  346: "+1 serce.\nDodatkowo leczy 1 czerwone serce.",
  347: "Przy uyciu podwaja wszystkie przedmioty i znajdki w pokoju.\nNie podwaja runy Jery i Diplopii.\nPo podwojeniu przedmiotw w Boss Rushu lub The Polaroidu/The Negative'u, tylko jeden z podwojonych przedmiotw moe zosta wzity. Podniesienie duplikatw zacznie Boss Rush.\nKilka kopii tego samego przedmiotu liczy si do transformacji.",
  348: "Aktywuje efekt piguki trzymanej przez Isaaca.\nPocztkowy czas adowania to 4 adunki, potem zaley od ostatniej aktywowanej piguki:\n* 1 - Bad Gas, Bombs are Key, Feels like I'm walking on sunshine!, Friends Till The End!, Horf!, I Found Pills, Puberty, Something's Wrong..., Telepills, X-Lax\n* 2 - Bad Trip, Infested?, Infested!, Percs!, Pheromones, Re-Lax\n* 3 - Experimental Pill, Explosive Diarrhea, I'm Drowsy..., Lemon Party, Power Pill!\n* 4 - Gulp!, Hematemesis, I can see forever!, piguki obniajce statystyki\n* 6 - ???, Addicted!, Amnesia, I'm Excited!!!, One makes you larger, One makes you small, Paralysis, R U A Wizard?, Retro Vision, Vurp!, piguki zwikszajce statystyki\n* 12 - 48 Hour Energy, Balls of Steel, Full Health",
  349: "Przy uyciu ma 50% szansy na wygenerowanie monety (Penny, Nickela lub Dime'a).\nPrzedmiot startowy Keepera po pokonaniu nim Isaaca.",
  35: "Przy uyciu zadaje 40 Obrae wszystkim przeciwnikom w pokoju.",
  350: "Przy wejciu do pokoju wszyscy przeciwnicy s zatruwani. Zatrucie zadaje im 2x Obraenia Isaaca.\nZabici przeciwnicy zostawiaj za sob zielony luz, ktry zadaje 1 punkt Obrae na tick.\nIsaac jest odporny na trujce chmury.",
  351: "Unieruchamia wszystkich przeciwnikw w pokoju na kilka sekund oraz wywouje efekt Mama Mega!, zadajc 105 Obrae przeciwnikom oraz dodatkowe 21 Obrae od trucizny.",
  352: "Przy uyciu wystrzeliwuje wielk, przeszywajc z, ktra moe przelatywa nad przeszkodami. za zadaje 10 + 10x Obraenia Isaaca oraz ma 1,5x mnonik wielkoci. Wystrzelenie zy odrzuca Isaaca do tyu.\nPrzy otrzymaniu obrae, przedmiot ten zmienia si w Broken Glass Cannon, a Isaac otrzymuje dodatkowe 2 serca obrae, +1,5 Zasigu i zostawia za sob czerwony luz. Jeli dodatkowe obraenia byyby miertelne, zostawi one Isaaca na 1/2 serca.",
  353: "+5 bomb.\nPrzy wybuchu bomby Isaaca wywoaj 2 eksplozje wicej w kadym kierunku prostopadle do bomby. Eksplozje te zadaj 75 Obrae, ale jeli nastpna eksplozja uderzy tego samego przeciwnika, jej obraenia zostan zmniejszone 2 razy.",
  354: "+1 Czerwone Serce.\nGeneruje losowy trinket.",
  355: "+1 Soul Heart.\n+0,38 Zasigu.\n+0,5 Wysokoci ez.\n+1 Szczcia.",
  356: "Przedmiot aktywny Isaaca aktywuje si podwjnie.\nPosiadanie wicej ni 1 kopii Car Battery nie ma dalszego efektu.",
  357: "Przy uycia dodaje 1 kopi wszystkich chowacw Isaaca na czas trwania obecnego pokoju. Jeli Isaac nie ma adnych chowacw, zamiast tego wygenerowany zostanie Demon Baby.\nPrzedmiot startowy Lilith, nawet jeli nie zosta jeszcze odblokowany.",
  358: "Daje efekt piguki R U a Wizard?, tzn. Isaac strzela po przektnej z obu oczu naraz.\nzy Isaaca przelatuj nad przeszkodami.\nMaksymalna ilo ez na strza, jaka moe by osignita z poczenia The Inner Eye, 20/20, Mutant Spider i The Wiz to 20 na Keeperze i 16 na innych postaciach.\nMa ma szans wypa z Reap Creepa.",
  359: "Statystyka Obrae +1,5.\nzy Isaaca zamieniaj si w gwodzie, ktre maj zwikszony odrzut.",
  36: "Generuje kup w miejscu, gdzie Isaac stoi, oraz odpycha znajdki, przeciwnikw i ich pociski.\nMa szans wypa z wiader w Dross.\nMa ma szans wypa z Dingle'a.\nPrzedmiot startowy ???.",
  360: "Przyzywa chowaca, ktry strzela takimi samymi zami, jak Isaac. Jego obraenia s jednak zredukowane o 25%.\nIncubus ma drugi najwyszy priorytet w kolejce chowacw, po King Baby.\nLilith domylnie posiada Incubusa, jednak nie jest to technicznie przedmiot startowy, ale cz postaci (nie moe zosta przelosowany i znika przy zmianie postaci np. przy uyciu Clickera lub odrodzeniu jako inna posta). Obraenia Incubusa Lilith **nie** s zredukowane o 25%.",
  361: "Przyzywa chowaca, ktry strzela, w 2 razy mniejszym tempie ni inne chowace, zami, ktre maj Obraenia i efekty ez Isaaca.",
  362: "Przyzywa chowaca, ktry po przejciu pokoju ma 10% szansy na wygenerowanie trinketu i 25% szansy na wygenerowanie znajdki (serca, monety, bomby, klucza, piguki, karty, runy lub baterii).",
  363: "Przyzywa orbitala, ktry przyciga do siebie i blokuje pociski przeciwnikw oraz zadaje 7 Obrae kontaktowych na tick (105 Obrae na sekund).\nPo zablokowaniu 10 pociskw w jednym pokoju, Sworn Protector wyrzuca 1 Eternal Hearta (raz na pokj; wyjcie z pokoju i ponowne wejcie umoliwia wygenerowania wicej Eternal Heartw).",
  364: "Przyzywa muszk, ktra wolno orbituje Isaaca w redniej odlegoci od niego (pomidzy Distant Admiration a Forever Alone), zadajc 3 Obrae kontaktowych na tick (45 na sekund).",
  365: "Przyzywa muszk, ktra w kadym pokoju poleci w prostej linii do pierwszej ciany lub przeszkody, po czym bdzie porusza si po pokoju wzdu cian/przeszkd, podobnie jak Guts.\nMuszka zadaje 7 Obrae kontaktowych na 7 tickw (56 na sekund).\nPrzeciwnicy blisko muszki bd prbowali atakowa j zamiast Isaaca.",
  366: "+5 bomb.\nBomby Isaaca po wybuchu wyrzucaj z siebie 4-5 maych bomb, ktre zadaj 1/2 obrae i maj zmniejszony zasig wybuchu. Bomby te wybuchaj przy wyldowaniu na ziemi.\nMae bomby synergizuj si z efektami bomb Isaaca.",
  367: "+5 bomb.\nBomby przyklejaj si do przeciwnikw, a po ich zabiciu wyrzucaj Niebieskie Pajki.\nPo wybuchu bomby zostawiaj po sobie biay luz, ktry spowalnia przeciwnikw.",
  368: "Opnienie ez maleje stopniowo, podczas gdy Isaac strzela w jednym kierunku, do maksymalnie 1/2 Opnienia ez Isaaca, zaokrglajc w d.\nStatystyka wraca do swojej oryginalnej wartoci, gdy Isaac przestanie strzela lub zmieni kierunek strzelania.",
  369: "+3 Zasigu.\n+1,5 Wysokoci ez.\nDaje Isaacowi zy spektralne.\nZamiast uderza w ciany, zy Isaaca wylatuj za ekran i kontynuuj lot po przeciwnej stronie pokoju.\nMa ma szans wypa z Mega Maw.",
  37: "Przy uyciu Isaac stawia du bomb, ktra zadaje 185 Obrae przy wybuchu. Bomba synergizuje si z efektami bomb.\nMa szans wypa z Wratha.",
  370: "+0,7 ez.\n* +1,5 Zasigu.\n* +0,5 Wysokoci ez.\nPrzy podniesieniu generuje 3 losowe serca (szansa na kady typ jest taka sama i niezalena od normalnych szans na Soul Hearty/Black Hearty).",
  371: "Przy otrzymaniu obrae przez Isaaca, generuje 6 bomb w pokoju, podobnie do karty The Tower. Bomby zostan wygenerowane moliwie najbliej przeciwnikw.\nEfekt nie zostanie wywoany przy otrzymaniu obrae od jakichkolwiek bomb ani przez wejcie/wyjcie z Curse Roomu.\nBomby synergizuj si z efektami bomb Isaaca.",
  372: "Przyzywa chowaca, ktry co jaki czas generuje Lil' Battery/Micro Battery, dodaje adunek do przedmiotu aktywnego Isaaca (maksymalnie 2 razy na pokj) oraz unieruchamia przeciwnikw.",
  373: "Mnonik Obrae Isaaca zwiksza si, gdy kolejne zy Isaaca trafiaj celnie w przeciwnikw. Mnonik wzrasta o 0,25x za kade celne uderzenie, maksymalnie 2x po 4 uderzeniach.\nJeli za nie trafi w przeciwnika i wylduje na ziemi lub cianie, istnieje szansa, e mnonik zostanie zresetowany do 1x. Szansa wynosi 20% przy pierwszym niecelnym strzale, 33% przy drugim i 50% przy kadym nastpnym.\nWraz ze wzrostem mnonika zy Isaaca zwikszaj rozmiar i wiec si na czerwono.\nJeli Isaac strzela kilkoma zami na raz, np. z Mutant Spidera, tylko jedna z nich musi uderzy przeciwnika, eby mnonik si zwikszy.\nW walce z The Beast nie ma adnych cian ani ziemi, wic mnonik nigdy si nie zresetuje.",
  374: "Daje Isaacowi szans na wystrzelenie wieccej zy, ktra, po uderzeniu przeciwnika, przywouje promie wiata z nieba. Promie zadaje 3x Obraenia Isaaca.\nSzansa zaley od Szczcia i wynosi 1/(11 - Szczcie), maksymalnie 50% przy Szczciu 9.",
  375: "Daje Isaacowi 20% szansy na zablokowanie pociskw przeciwnikw. Gdy pocisk jest zablokowany, Host Hat wystrzeli 3 pociski, podobnie do Hosta. Pociski te synergizuj si z efektami ez Isaaca.\nIsaac jest odporny na eksplozje oraz na pociski spadajce na niego z gry.\nMa ma szans wypa z The Gate'a.",
  376: "Po zakupieniu artykuu w Sklepie, na jego miejsce pojawia si nowy artyku tego samego typu.\nCena nowych artykuw jest zwikszona. W przypadku przedmiotw ceny s zwikszone o 2 * x, a w przypadku wszystkich innych artykuw o 1 * x, gdzie x = liczba zakupw.\nKupienie artykuu ze znik wygeneruje nowy przedmiot ze znik.",
  377: "Zmienia zachowanie pajczych przeciwnikw:\n* Spider, Big Spider, Swarm Spider, Strider, Rock Spider, Tinted Rock Spider, Level 2 Spider, Trite, Baby Long Legs, Small Baby Long Legs, Crazy Long Legs i Small Crazy Long Legs nie mog zada Isaacowi obrae\n* Ragling, Rag Man's Ragling, Blister, Ticking Spider, Migraine, Wall Creep, Blind Creep, Rage Creep, The Thing, Soy Creep i Tainted Soy Creep nie zadaj Isaacowi obrae kontaktowych, ale mog mu zada obraenia na inne sposoby\n* Coal Spider nie s agresywne w stosunku do Isaaca, ale zadaj mu obraenia kontaktowe\nWikszo pajkw bdzie nadal prbowa zaatakowa Isaaca, mimo tego, e nie zadaj mu obrae.\nEfekt nie dziaa na Twitchy'ego, poniewa jest on klasyfikowany jako wariant Gapera, a nie pajk.\nW przypadku Jacoba & Esau, efekt dziaa na obie postaci, niezalenie od tego, ktry posiada przedmiot.",
  378: "Strzelanie bez przerwy przez 3 sekundy powoduje, e Isaac stawia bomb z efektem Butt Bombs. Bomba zadaje 60 Obrae przy wybuchu oraz 10 Obrae wszystkim przeciwnikom w pokoju i ich dezorientuje.\nAby postawi nastpn bomb, trzeba przerwa strzelanie i zacz je z powrotem.\nBomby lecz bossw Brownie, Dingle, Dangle, Clog i Colostomia o 25% ich maksymalnego zdrowia.\nMa ma szans wypa z Turdlingsw.",
  379: "Daje Isaacowi zy spektralne oraz mnonik 2x wielkoci ez. zy wizualnie maj ksztat pksiyca.",
  38: "Przy uyciu wystrzeliwuje 10 ez dookoa Isaaca. zy zachowuj szybko strzaw i efekty ez Isaaca, oraz zadaj Obraenia Isaaca + 25.",
  380: "+5 monet.\nDrzwi zamknite na klucz, Key Blocki oraz skrzynki bd odtd wymagay monety do otwarcia.",
  381: "+0,7 ez.\nDaje Isaacowi losowy przedmiot (oprcz Key Piece 1/2, Broken Shovel, Mom's Shovel, Knife Piece 1/2) na pocztku nastpnej rozgrywki.\nNie ma efektu, jeli zosta pozyskany przez Metronome lub Lemegeton.\nW przypadku Jacoba & Esau, Jacob otrzyma przedmiot.",
  382: "Przy uyciu, Isaac bdzie mg rzuci kul. Kula apie przeciwnika, w ktrego trafi, usuwajc go z pokoju.\nKady przeciwnik, oprcz tych, ktrzy s nietykalni, mini-bossw oraz bossw, moe zosta pochwycony.\nNastpne uycie i rzucenie Friendly Ballem uwolni przeciwnika jako przyjacielskiego przeciwnika. Przeciwnicy ci utrzymuj si midzy pokojami i pitrami, dopki nie zgin.\nJeli po rzuceniu kul i zapaniu przeciwnika Isaac wejdzie w lec na ziemi kul, zostanie ona od razu naadowania i pozwoli na natychmiastowe uwolnienie przeciwnika.",
  383: "Przy uyciu, wszystkie zy Isaaca rozdziel si na 6 ez we wszystkim kierunkach, zachowujc obraenia i szybko, oraz majc szans na zaaplikowanie efektw ez.",
  384: "Przyzywa chowaca, ktry moe zosta adowany i wystrzeliwany w kierunku, w ktrym Isaac strzela, zadajc obraenia zalene od stopnia naadowania - ok. 5 przy najmniejszym naadowaniu i ok. 20 przy najwikszych.\nWystrzelony chowaniec odbija si od cian i przeciwnikw.\nMa ma szans wypa z Gurdy, Gurdy Jr., Mega Gurdy lub Gurglingsw.",
  385: "Przyzywa chowaca, ktry poda za Isaaciem i zbiera z ziemi monety. Jego forma zmienia si w zalenoci od liczby zebranych monet:\n* poziom 1 (0-5 centw) - tylko zbiera monety\n* poziom 2 (6-11 centw) - co jaki czas wyrzuca znajdk, skrzynk, trinket lub przedmiot po przejciu pokoju\n* poziom 3 (12-23 centw) - strzela zami w tym samym kierunku, w ktrym strzela Isaac, zadajc 3,5 Obrae; jego zy maj szans na wyrzucenie monety, gdy trafi w przeciwnika\n* poziom 4 (24 centw i wicej) - ciga przeciwnikw i zadaje im 2,5 Obrae kontaktowych na tick (37,5 na sekund) i co jaki czas wyrzuca Troll Bomb\nPo osigniciu poziomu 4, Bumbo nadal bdzie zbiera monety, majc szans na wyrzucenie znajdki, skrzynki, trinketw lub przedmiotu z losowej puli za kad monet, ktr zbierze.\nBumbo na poziomach 1-3 moe przelatywa nad dziurami i przeszkodami.\nLucky Penny podniesiony przez Bumbo nie da Isaacowi bonusu do Szczcia.",
  386: "Przy uyciu przelosowuje przeszkody (kamienie, kupy, ogniska, itd.) w inne przeszkody.",
  387: "Przyzywa chowaca, ktry emituje aur, ktra spowalnia przeciwnikw i ich pociski, ktre znajduj si w jej obrbie i pewny czas po opuszczeniu go.\nSpowolnienie nie dziaa na bossw, ale na ich pociski tak.",
  388: "Przyzywa chowaca, ktry poda za Isaaciem i zbiera klucze, w zamian wyrzucajc losowe skrzynki.\nKey Bum nie bdzie podnosi Zotych Kluczy.\nPo podniesieniu Charged Key, Key Bum wyrzuci Lil' Battery.",
  389: "Chowaniec, ktry wyrzuca runy lub Soul Stone'y na zmian co 7 lub 8 pokoi.\nJeli adna runa/Soul Stone nie zostay odblokowane, wyrzuca karty.",
  39: "Unieruchomia wszystkich przeciwnikw w pokoju na ok. 4 sekundy.",
  390: "Przyzywa chowaca, ktry poda za Isaaciem i strzela samonaprowadzajcymi zami (podobnymi do tych z Sacred Hearta), ktre zadaj 10 Obrae i maj 75% Szybkoci Strzaw Isaaca.",
  391: "Przeciwnicy s w stanie zadawa sobie wzajemnie obraenia pociskami.\nGdy przeciwnik otrzyma obraenia od innego przeciwnika, bdzie go prbowa zaatakowa zamiast atakowa Isaaca.",
  392: "Daje Isaacowi efekt losowego przedmiotu zodiakalnego. Efekt zmienia si co pitro.",
  393: "Daje szans na wystrzelenie zy, ktra moe zatru przeciwnikw. Zatrucie zadaje Obraenia Isaaca na tick i trwa 2 ticki.\nZatruwa przeciwnikw, ktrzy dotkn Isaaca. Trucizna z dotyku trwa 6 tickw.\nPrzeciwnicy zabici przez trucizn z dotyku maj 20% szansy na wyrzucenie Black Hearta.",
  394: "+0,7 ez.\n+3 Zasigu.\n+0,3 Wysokoci ez.\nGdy Isaac zacznie strzela, na pododze pojawi si czerwony celownik. Isaac oraz jego chowace bd bez przerwy strzelali w stron celownika.\nCelownik znika przy przejciu do nastpnego pokoju lub przy naciniciu klawiszu zmiany karty/piguki (domylnie Ctrl).",
  395: "zy Isaaca s zastpione przez laserowe piercienie. Strzay mog zosta adowane, eby zwikszy rozmiar piercieni.\nPiercienie przechodz przez przeszkody i przeciwnikw i maj nieskoczony zasig.\nObraenia piercieni zale od stopnia naadowania, zaczynajc z 25% Obrae Isaaca i koczc na 100% przy penym naadowaniu.",
  396: "Uycie przedmiotu generuje na ziemi portal. Po stworzeniu dwch portali Isaac moe przeskakiwa midzy nimi.\nUycie przedmiotu ponownie zastpuje najstarszy portal.\nPortale mog by wygenerowane w kadym pokoju, ale jeli portal znajduje si w pokoju o negatywnym wspczynniku siatki (Devil Room, Angel Room, pokj I AM ERROR, Crawl Space, Boss Rush, Black Market, pokj Mega Szatana, pokj z portalem do Voidu w ???), Isaac nie bdzie mg przenie si do tego pokoju.",
  397: "+0,5 ez.\n+1,5 Zasigu.\n+0,16 Szybkoci Strzaw.\n+0,5 Wysokoci ez.\nIsaac otrzymuje promie, ktry jest skierowany w t stron, w ktr zwrcona jest jego gowa. zy Isaaca zawsze lec wzdu promienia.\nGdy Isaac poruszy si lub zmieni kierunek strzaw, zy przenios si do promienia w locie. Uniemoliwia to strzelanie pod ktem.",
  398: "Daje Isaacowi szans (niezalen od Szczcia) na wystrzelenie zmniejszajcej zy. Zmniejszeni przeciwnicy uciekaj od Isaaca i mog zosta zabici przez wejcie w nich.\nPrzeciwnicy, ktrzy przy mierci wybuchaj, wystrzeliwuj pociski lub generuj innych przeciwnikw nie zadadz obrae Isaacowi po zabiciu przez wejcie w nich.\nEfekt zmniejszenia trwa 5 sekund. Przed powrotem do normalnego rozmiaru przeciwnicy bd przez chwil miga na biao.\nEfekt nie dziaa na bossw oraz mini-bossw.",
  399: "Strzelanie bez przerwy przez 2,35 sekundy aduje atak, ktry jest aktywowany przy puszczeniu klawiszu strzaw. Atak uwalnia czarny okrg wok Isaaca, ktry powiksza si i zmniejsza, zadajc przeciwnikom, ktrzy w niego wejd, Obraenia Isaaca na tick przez 30 tickw (ok. 1,97 sekundy).\nAtak jest naadowany, gdy na czole Isaaca pojawi si czerwony odwrcony krzy.\nAtak synergizuje si z efektami ez.",
  4: "+0,5 Obrae.\n1,5x mnonik Obrae.\nMnonik nie dodaje si z mnonikami z Magic Mushroom, The Book of Belial z Blood of the Martyr ani samego siebie.",
  40: "Przy uyciu rani Isaaca za 1/2 serca i tworzy eksplozj, ktra zadaje 185 Obrae w duym obszarze.\nW przeciwiestwie do innych przedmiotw zadajcych Isaacowi obraenia, moe zosta aktywowany, gdy Isaac jest nietykalny np. po otrzymaniu przez niego obrae.\nEfekt synergizuje si z efektami bomb.",
  400: "Daje Isaacowi wczni (ktra nie zastpuje ez), ktry trzyma w kierunku, w ktrym strzela. Wcznia zadaje 2x Obraenia Isaaca na tick (6x Obraenia Isaaca na sekund) oraz ma szans na nadanie przeciwnikom efektu strachu.",
  401: "zy Isaaca maj szans (niezalen od Szczcia) na przyklejenie si do przeciwnika, po czym wybuchaj kilka sekund pniej, zadajc 60 + Obraenia Isaaca.",
  402: "Generuje 1-6 losowych znajdziek i trinketw wok Isaaca.\nPrzedmioty bd generowane z losowych pul. Nie dotyczy to ustalonych przedmiotw, jak np. Cube of Meat/Ball of Bandages po walce z Harbingerami.\nPule s oddzielne dla normalnego trybu gry i Greed Mode'a, tzn. przedmioty w Greed Modzie bd generowane z pul z Greed Mode'a, a w normalnym trybie gry z pul normalnego trybu gry.\nKada pula ma wag zalen od liczby przedmiotw w niej, np. jest wiksze prawdopodobiestwo otrzymania przedmiotu z puli Treasure Roomu lub Sklepu ni z Bomb Buma lub Planetarium.\nJeli przedmiot spoza puli Devil Roomu pojawi si do zakupu jako ukad z Diabem, bdzie kosztowa 1 Czerwone Serce (lub 3 Soul Hearty, jeli Isaac nie ma Czerwonych Serc).",
  403: "Przyzywa chowaca, ktry chodzi po pokoju, zadajc losowy efekt przeciwnikom, ktrzy go dotkn.\nChowaniec ma szans na wyrzucenie Lil' Battery lub Niebieskiego Pajka po przejciu pokoju.\nIsaac widzi paski zdrowia przeciwnikw oraz obraenia ez.",
  404: "Przyzywa chowaca, ktry poda za Isaaciem i blokuje pociski przeciwnikw. Przy zablokowaniu pocisku, ma 10% szansy na puszczenie bka, jednego z trzech typw:\n* oczarowujcy przeciwnikw - zadaje 5 Obrae\n* zatruwajcy przeciwnikw - zadaje 5 Obrae, a trucizna 3,5\n* odpychajcy przeciwnikw - zadaje 6 Obrae",
  405: "Przyzywa chowaca, ktry poda za Isaaciem. Podwjne nacinicie klawiszu strzau wystrzeli chowaca, ktry przelosuje pierwszego przeciwnika, trinket lub znajdk, na ktre natrafi. Jeli nie moe przelosowa przeciwnika (np. bossa), zadaje mu obraenia.\nTrinkety mog zosta przelosowane w znajdki.",
  406: "Przy uyciu przelosowuje Obraenia, zy, Zasig i Szybko Isaaca. Kada z tych statystyk otrzymuje mnonik, ktry wchodzi w efekt po wszystkich innych modyfikacjach.\nMnonik ez moe przekroczy limit 5.\nMnonik Szybkoci **nie** moe przekroczy limitw 0,1 i 2, ale grny limit otrzymuje ten sam mnonik. Efekt A Pony i White Pony, ktre normalnie zwikszaj Szybko na 1,5, jeli jest poniej tej wartoci, nie jest uwzgldniony po przelosowaniu przy uyciu D8.\nMnoniki s losowane w taki sposb, e ich iloczyn pozostaje mniej wicej taki sam.",
  407: "Daje Isaacowi aur, ktra daje mu bonus do statystyk zaleny od koloru aury:\n* czerwona: +4 Obrae\n* niebieska: -4 Opnienia ez\n* ta: +0,5 Szybkoci\n* pomaraczowa: +3 Zasigu, +1 Wysokoci ez\nPo otrzymaniu obrae aura i jej bonusy znikaj. Odnawia si z losowym kolorem po wejciu do innego pokoju.",
  408: "Gdy Isaac zabija przeciwnika, istnieje szansa na wygenerowanie na jego miejscu czarnego piercienia, ktry zadaje Obraenia Isaaca.\nSzansa zaley od Szczcia.",
  409: "+2 Black Hearty.\nKiedy Isaac nie ma Czerwonych Serc, otrzymuje lot oraz, gdy Isaac jest w pokoju z przeciwnikami, a sekundy na zegarze gry wynios 00, 20 lub 40 przynajmniej 2 razy, daje Isaacowi 10-sekund tarczy, ktra chroni go przed obraeniami.\nNie ma efektu na Keeperze i Tainted Keeperze.",
  41: "Nadaje wszystkim przeciwnikom w pokoju efekt strachu na 5 sekund.\nPrzy uyciu generuje 1 Niebiesk Muszk.",
  410: "Isaac co jaki czas wystrzeli wolno poruszajce si oko o nieskoczonych zasigu, ktre bdzie strzelao zami takimi samymi jak zy Isaaca w kierunku, w ktrym strzela Isaac.\nOczy znikaj po uderzeniu ciany, przeszkody lub przeciwnika, nawet jeli Isaac ma zy przeszywajcy lub spektralne.\nSzansa jest w maym stopniu zalena od Szczcia.",
  411: "Za kade zabicie przeciwnika Isaac otrzymuje +0,5 Obrae na czas trwania obecnego pokoju.\nZniszczenie Shopkeepera rwnie daje bonus.",
  412: "Po otrzymaniu obrae pewn ilo razy, Isaac otrzyma chowaca.\nMoliwe chowace to: Dark Bum, Demon Baby, Incubus, Leech, Lil Brimstone, Succubus, Lil Abaddon i Twisted Pair.\nEfekt zadziaa 4 razy, pierwszy raz po otrzymaniu obrae przez Isaaca 15 razy, drugi - po kolejnych 15 razach, trzeci - po nastpnych 30 razach, czwarty - po kolejnych 30 razach.",
  413: "Po podniesieniu 15 serc (kadego typu, wliczajc znikajce serca Tainted Magdaleny, oprcz Gold Heartw i Blended Heartw), Isaac otrzyma chowaca oraz wygenerowany zostanie Soul Heart.\nMoliwe chowace to: Holy Water, Guardian Angel, Sworn Protector, The Relic i Seraphim.\nPo tym, jak Isaac otrzyma wszystkie chowace, efekt bdzie tylko generowa Soul Hearty.",
  414: "Treasure Roomy bd odtd zawiera 2 przedmioty. Tylko jeden moe zosta podniesiony, drugi znika.\nMae Treasure Roomy bd zawieray tylko 1 przedmiot. Treasure Roomy, ktre nie zawieraj przedmiotw lub ktre zawieraj 2 przedmioty, ktre oba mog zosta podniesione, zostaj zastpione przez Treasure Roomy z 2 przedmiotami do wyboru.\nNa pitrach alternatywnych, Treasure Roomy bd zawiera 2 widoczne przedmioty i 1 niewidoczny. Tylko jeden moe zosta podniesiony.",
  415: "+2 Soul Hearty.\nGdy wszystkie kontenery Czerwonych Serc i Bone Hearty Isaaca s wypenione (wliczajc monety Keepera), daje Isaacowi 2x Obrae, +0,5 Wysokoci ez i -0,3 Szybkoci Strzau.\nEfekt deaktywuje si, jeli Isaac otrzyma obraenia. Zostanie odnowiony po przejciu do nastpnego pokoju.",
  416: "Jeli pokj normalnie nie wygenerowaby adnej nagrody, wygenerowane zostan 1-3 losowych monet. Wszystkie monety bd tego samego typu.\nLimit monet posiadanych przez Isaaca zostaje zwikszony do 999.",
  417: "Przyzywa chowaca, ktry lata na ukos po pokoju, wytwarzajc czarn aur.\nPrzeciwnicy w aurze otrzymuj 2,5 Obrae na tick (ok. 7,5-10 Obrae na sekund).\nJeli Isaac znajduje si w aurze, otrzymuje mnonik 1,5x Obrae. Mnonik dodaje si z innymi mnonikami (np. z Magic Mushroom lub Cricket's Head).",
  418: "Kada za Isaaca dostaje losowy efekt spord nastpujcych: Ouija Board, Cupid's Arrow, Spoon Bender, Spider Bite, The Common Cold, Mom's Contacts, The Parasite, A Lump of Coal, My Reflection (bez bonusu do Szybkoci Strzaw), Polyphemus (tylko moliwo lecenia dalej z pozostaymi obraeniami po zabiciu przeciwnika; bez bonusu do Obrae), Wiggle Worm, The Mulligan, Ipecac (bez bonusu do Obrae ani trucizny), Mom's Eyeshadow, IRon Bar, Ball of Bandages (poziom 4), Tiny Planet (bez bonusu do Zasigu), Anti-Gravity, Cricket's Body, Rubber Cement, Mom's Perfume, Proptosis, Fire Mind, Strange Attractor, Pisces, Pulse Worm, Ring Worm, Flat Worm, Hook Worm, Godhead, Ball of Tar, Mysterious Liquid, Lost Contact, Explosivo, Continuum, Holy Light, Head of the Keeper, Serpent's Kiss (przeciwnicy mog wyrzuci Black Hearty przy mierci), God's Flesh, Bumbo (poziom 3), Ouroboros Worm, Glaucoma, Sinus Infection, Parasitoid, Sulfuric Acid, Compound Fracture, Eye of Belial, Eye of Greed (bez efektu tracenia monet), Euthanasia, Jacob's Ladder, Little Horn, Technology Zero, Pop!, Lachryphagy, Trisagion, Flat Stone, Haemolacria, Large Zit, Knockout Drops, Uranus, Lodestone, Rotten Tomato, Eye of the Occult, Immaculate Heart, Terra, Brain Worm, Mucormycosis i Ocular Rift.\nOprcz tego zy mog otrzyma efekt niektrych ognikw generowanych przez Book of Virtues, wliczajc wyrzucanie serc/monet/kart/run przy zabiciu przeciwnika, teleportowanie przeciwnikw w losowe miejsce w pokoju, zmienianie przeciwnikw w kup czy zmienianie przeciwnikw w losowe ogniki.",
  419: "Teleportuje Isaaca do nastpnego pokoju, ktry jeszcze nie zosta odwiedzony. Bdzie to pokj stykajcy si z odwiedzonym pokojem, jeli moliwe.\nKolejno pokojw, do ktrych Isaac zostaje teleportowany, to: Grave Room, zwyke pokoje, Boss Room, Super Secret Room, Shop, Treasure Room, Sacrifice Room, Dice Room, Library, Curse Room, Mini-Boss Room, Challenge Room, Bedroom, Arcade, Vault, Secret Room, Devil Room/Angel Room, I AM ERROR room.\nPo odwiedzenie wszystkich pokoi, przedmiot bdzie teleportowa Isaaca w pokoju I AM ERROR.\nNiemoliwy jest teleport do Crawl Space, Boss Rushu, wejcia do ???, Mega Szatana ani wejcia do alternatywnych piter.\nUycie przedmiotu podczas walki z The Beast przeteleportuje Isaaca w losowe miejsce w pokoju, nie przerywajc walki.\nW Lustrzanym Odbiciu, Isaac zostanie przeteleportowany do Ultra Secret Roomu przed I AM ERROR, natomiast nastpne uycie przenisie go do pokoju I AM ERROR na zwykym pitrze.\nPo pokonaniu Mom's Heart w Mausoleum/Gehennie, przedmiot przeteleportuje Isaaca do pokoju I AM ERROR z przejciem do Coprse, nawet jeli inne pokoje zostay jeszcze nieodwiedzone.",
  42: "Przy uyciu Isaac trzyma nad gow bomb, ktr mona rzuci. Wybuch bomby zadaje obraenia rwne (185 + Obraenia Isaaca) i zostawia za soba trujc chmur, ktra zatruwa przeciwnikw i zadaje im 5 Obrae.\nMoe wypa ze Slotha.",
  420: "Isaac zostawia za sob czarny proszek, ktry po chwili znika. Stworzenie okrgu przy pomocy proszku przyzywa pentagram, ktry zadaje przeciwnikom znajdujcym si na nim 10 Obrae na tick.",
  421: "Przy uyciu Isaac puszcza bka, oczarowujc pobliskich przeciwnikw.",
  422: "Przy uyciu Isaac zostanie przeteleportowany do poprzedniego pokoju. Wszystkie, co stao si w obecnym pokoju, zostanie cofnite - zuyte zdrowie, bomby, monety, klucze itd. zostan zwrcone, wynik punktowy i czas gry zostan cofnite (ale uycie Glowing Hour Glass zabiera ok. 1 sekund), jak rwnie osignicia postaci i pienidze wpacone do Donation Machine/Greed Donation Machine.\nMoe zosta uyta 3 razy na pitro, po czym funkcjonuje jak zwyka The Hourglass.\nKarta Joker nie zostaje zwrcona, poniewa teleportuje ona do nowego pokoju, wic cofnicie do czasu, zanim zostaa zuyta, jest niemoliwe.\nNie zwraca zdrowia straconego na wejcie/wyjcie z Curse Roomu, poniewa zostao one stracone w poprzednim pokoju, a nie obecnym.\nNie zachowuje Devil Roomu/Angel Roomu po cofniciu do Boss Room.\nNie wpywa na szans na wygenerowanie portalu do The Void.\nNie cofa efektu piguki Retro Vision.\nNie usuwa przedmiotw z Collection Page.\nUycie Glowing Hour Glass po pokonaniu Mom's Heart w Mausoleum II/Gehennie II wrci Isaaci do Boss Roomu bez czci noa i z zamknitymi drzwiami do Mom's Heart, uniemoliwiajc przejcie do Corpse.\nLiczba Soul Heartw Bethany nie zostaje zresetowana.\nPodczas walki z The Beast tylko teleportuje Isaaca na rodek pokoju.",
  423: "Tworzy biay piercie wok Isaaca, ktry raz na sekund zadaje Obraenia Isaaca wszystkim przeciwnikom w jego obrbie.\nPociski przeciwnikw maj szans na bycie odbitymi przez piercie. Odbicie zmienia je w samonaprowadzajce zy o Obraeniach Isaaca, ale bez jego efektw ez.",
  424: "Generuje sakiewk.\nMonety maj 10% szansy na bycie zastpionymi sakiewkami, a bomby (wliczajc Troll Bomby), klucze, karty, runy i baterie 20%.\nNie dotyczy to znajdziek wygenerowanych bezporednio przez Isaaca, np. przy pomocy IV Bag czy Fanny Pack.\nEfekt dziaa retroaktywnie, znajdki ju istniejce na pitrze mog zosta zmienione w momencie podniesienia przedmiotu.",
  425: "Isaac wieci przed sob strumieniem wiata, ktre spowalnia przeciwnikw i ich strzay. Strumie jest skierowany w kierunku, w ktrym Isaac si porusza.\nEfekt spowolnienia znika z przeciwnikw, gdy wiato na nich nie wieci, ale pociski s spowolnione na stae.\nEfekt spowolnienia nie dziaa na bossw, ale na ich pociski tak.\nUsuwa Curse of Darkness na pitrze, na ktrym przedmiot zosta podniesiony. Nie usuwa kltwy z kolejnych piter, ale zmniejsza jej wizualny efekt.",
  426: "Przyzywa muszk, ktra poda za Isaaciem z opnieniem 0,66 sekundy i zadaje 2 Obrae kontaktowych na tick (30 Obrae na sekund).",
  427: "Przy uyciu generuje popychalne TNT.\nUycie przedmiotu ponownie, gdy nie jest on naadowany, spowoduje wybuch wszystkich postawionych TNT w pokoju.",
  428: "+4 Soul Hearty.\nLeczy wszystkie czerwone serca.",
  429: "Przeciwnicy maj 5% szansy na wygenerowanie 1 centa, kiedy uderzy ich za Isaaca.\nMa 1% szansy wypa z Shopkeepera.",
  430: "Przyzywa much, ktre poda za Isaaciem z opnieniem 0,66 sekundy i strzela w pobliskich przeciwnikw co 1 sekund, zadajc Obraenia Isaaca.",
  431: "Przyzywa chowaca, ktry poda za Isaaciem z opnieniem 0,66 sekundy. Jeli za Isaaca lub jego chowacw przeleci przez Multidimensional Baby, rozdzieli si na 2 zy, ktre dostan bonus do szybkoci 1,33x oraz bonus do wysokoci zy +4.",
  432: "+5 bomb.\nBomby Isaaca maj szans na wyrzucenie znajdki lub trinketu, kiedy wybuchn, oraz na zauroczenie losowych przeciwnikw w pokoju.\nLimit znajdziek/trinketw moliwych do wygenerowania na jednym pitrze to 63-65.",
  433: "Przyzywa chowaca, ktry naladuje ruchy Isaaca i generuje przyjazne Chargery, gdy zostanie dotknity przez przeciwnika.\nChargery zadaj 8,7 Obrae na uderzenie, maj 20 Zdrowia i utrzymuj si tylko na czas trwania danego pokoju. Isaac moe mie maksymalnie 16 Chargerw.",
  434: "Zabicie przeciwnika dodaje muszk do soika, maksymalnie 20. Uycie przedmiotu uwalnia je jako Niebieskie Muszki.\nZabicia przez Niebieskie Muszki, szaracze i strzelajce chowace nie dodaj muszek do soika (ale Niebieskie Pajki, orbitale, chowace cigajce przeciwnikw oraz przyjacielscy przeciwnicy - ale nie ci wygenerowani przez innych przeciwnikw - ju tak).\nZniszczenie Shopkeepera dodaje muszk do soika.\nZabicie przeciwnikw wygenerowanych przez Husha nie dodaje much do soika.",
  435: "Przyzywa chowaca, ktry poda za Isaaciem i co 2/3 sekundy strzela prostopadle w 4 kierunkach, podobnie do Lokiego. Jego zy zadaj 3,5 Obrae.\nMa ma szans wypa z Lokiich.",
  436: "Przyzywa chowaca, ktry poda za Isaaciem i blokuje pociski przeciwnikw. Po zablokowaniu 10 pociskw chowaniec rozlewa mleko na pododze i daje Isaacowi -1 Opnienia ez na czas trwania pitra.\nRozlane mleko jest efektem tylko wizualnym i znika po kilku sekundach.",
  437: "Przy uyciu restartuje obecny pokj i generuje na nowo wszystkich przeciwnikw. Przejcie pokoju normalnie generuje nagrod.\nEfekt nie dziaa w Greed Modzie, pokojach bez przeciwnikw, Challenge Roomach, Boss Challenge Roomach, Boos Roomach, Devil Roomach i Angel Roomach.\nPrzy uyciu podczas walki z Ultra Harbingerami/The Beast, restartuje walk.\nPrzy uycie na Dogmie, generuje drug Dogm.\nUycie w Lustrzanym Odbiciu nie tylko na nowo generuje przeciwnikw, ale rwnie ca zawarto pokoju na normalnym pitrze. Uycie w Sklepie wygeneruje ca zawarto sklepu, a ponowne uycie przelosuje j.",
  438: "+1 Soul Heart\n+0,75 do statystyki ez\nZmniejsza rozmiar Isaaca i podwysza jego gos.",
  439: "Gdy Isaac trzyma przedmiot, otrzymuje +1 Szczcia, a efekty wikszoci trinketw s podwojone (rwnie tych wchonitych przy pomocy Gulp!, Marbles lub Smelter).\nPrzy uyciu generuje losowy trinket.\nJeli Mom's Box zostanie zaabsorbowany przez Void, jego podwajajcy efekt zostanie stracony.",
  44: "Teleportuje Isaaca do losowego pokoju na pitrze.\nIsaac moe wpyn na kierunek, w ktrym zostanie przeteleportowany, poprzez poruszenie si w tym kierunku.\nNiemoliwe jest przeteleportowa si do pokoju I AM ERROR ani Crawl Space/Black Marketu.",
  440: "Czasami, gdy Isaac strzela, przestanie on strzela, a zamiast tego bdzie musia naadowa nastpny atak. Atak ten wyrzuca kamie nerkowy, ktry przeszywa przeciwnikw, zadaje 5x Obraenia Isaaca i ma 2x jego Zasig, a za nim strumie ez. Atak ten utrzymuje si pomidzy pokojami.\nEfekt wystpuje co ok. 25 sekund.",
  441: "Przy uyciu Isaac wystrzeli wielki laser, podobny do tego uywanego przez Mega Szatana, na 15 sekund. Laser utrzymuje si midzy pokojami oraz pitrami.\nLaser zadaje 5x Obraenia Isaaca na tick, jest spektralny i przeszywajcy oraz jest w stanie zniszczy ogniska, TNT i kupy.\nLaser moe by skierowany w kadym kierunku, ale odpycha on Isaaca do tyu.\nPodczas strzelania laserem, Isaac nie moe otwiera skrzynek, podnie znajdziek ani przedmiotw, podnie trofeum na kocu wyzwania ani wej do skrzynki prowadzcej do Chest lub Dark Roomu, ani spa w ku.\nPodniesienie maej baterii lub Charged Key dodaje 6 adunkw do przedmiotu, piguka 48 Hour Energy w caoci go aduje, a Mega Battery aduje go 2 razy.\nPrzedmiot ten ma wag obnion o 80%, co powoduje, e jest rzadziej spotykany ni inne przedmioty.",
  442: "Gdy Isaac posiada dokadnie 1 wypenione Czerwone Serce, dostaje nastpujce bonusy:\n* +2 ez (moe przekroczy limit 5)\n* +1,5 Zasigu.\n* +1 Wysokoci ez.\n+0,2 Szybkoci Strzaw.\nDziaa rwnie na Keeperze/Tainted Keeperze, gdy maj oni 1 monet zdrowia, oraz na The Forgottenie, gdy ma on 1 wypenionego Bone Hearta.",
  443: "+0,3 do statystyki ez.\nDaje szans na wystrzelenie yletki, ktra zadaje 4x Obraenia Isaaca. Szansa obliczana jest wg wzoru 1/(15 - Szczcie).",
  444: "Za kadym razem po 15 wystrzelonych zach, Isaac wystrzeli grup 12 ez ze swojego prawego oka.",
  445: "+0,3 Obrae.\n+0,1 Szybkoci.\nW pokojach ssiadujcym z nieodkrytym Secret Roomem lub Super Secret Roomem, zostanie odegrany efekt dwikowy skowytu psa.\nW pokojach, w ktrych znajduje si nieodkryty Crawl Space, zostanie odegrany efekt dwikowy szczekania psa.\nEfekty nie zostan odegrane w pokoju startowym nowego pitra, dopki Isaac nie wyjdzie i wejdzie do tego pokoju jeszcze raz.\nDodatkowy Crawl Space wygenerowany przez Stud Finder nie wywoa efektu dwikowego.",
  446: "Gdy Isaac strzela, wok niego powstaje aura, ktra zatruwa pobliskich przeciwnikw na 2 ticki. Efekt moe si dodawa do maksymalnie 6 tickw.\nTrucizna zadaje Obraenia Isaaca na tick.",
  447: "Strzelanie bez przerwy przez 4 sekundy wygeneruje brzow chmurk, ktra pocztkowo zadaje obraenia rwne basowym Obraeniom Isaaca (ok. 5 razy na sekund) przeciwnikom, ktrzy przez ni przejd.\nChmurka utrzymuje si przez 10 sekund, podczas ktrych zwiksza swj rozmiar i zmniejsza zadawane obraenia.\nChmurk mona przesun poprzez strzelanie w ni.",
  448: "Daje 25% szansy na wyrzucenie penego Czerwonego Serca przy otrzymaniu obrae.\nPrzy otrzymaniu obrae, Isaac zaczyna krwawi, wystrzeliwujc zy dookoa. Po 20 sekunach krwawienia, Isaac otrzymuje 1/2 serca obrae na Czerwone Serca lub Rotten Hearty (lub monety na Keeperze i Tainted Keeperze). Efekt ten bdzie si powtarza co 20 sekund, dopki Isaac nie uleczy Czerwonego Serca lub Rotten Hearta, nie ma wicej Czerwonych Serc/Rotten Heartw, lub kiedy nastpne obraenia zabiyby go.\nJeli Isaac nie ma Czerwonych Serc ani Rotten Heartw, efekt si nie aktywuje.",
  449: "+1 Soul Heart.\nPociski przeciwnikw, ktre uderz Isaaca, maj 25% szansy na zostanie odbitymi w przeciwnika jako dezorientujce zy, ktre zadaj Obraenia Isaaca i posiadaj jego efekty ez.",
  45: "Leczy 1 czerwone serce.\nEfekt dziaa na Keepera i Tainted Keepera, leczc im 1 monet.\nMoe wypa z Super Lusta.\nPrzedmiot startowy Magdalene oraz Tainted Magdalene w miejscu na kart/piguk.",
  450: "Co 20 ez wystrzelonych przez Isaaca, obok normalnej zy wystrzeliwana jest zota za, ktra zmienia przeciwnikw w zoto, unieruchamia ich i wyrzuca centa. Isaac traci 1 monet, jeli jakie ma.\nJeli Isaac nie ma monet, zota za zadaje jego Obraenia. Jeli ma monety, zota za zadaje 10 + 1,5x jego Obraenia.\nPrzeciwnicy zabici podczas bycia zamienionymi w zoto wyrzuc 1-3 monety.",
  451: "Wyrzuca losow kart przy podniesieniu.\nDaje dodatkowe efekty nastpujcym kartom:\n* The Magician - dodatkowo daje efekt The Book of Belial\n* The High Priestess - przyzywa 2 stopy Matki\n* The Empress - zwiksza bonusy do statystyk czterokrotnie\n* The Emperor - jeli boss nie zosta jeszcze pokonany, dodatkowo daje Isaacowi Soul Hearta\n* The Hierophant - generuje 3 Soul Hearty\n*The Lovers - generuje 3 czerwone serca\n* The Chariot - podwaja czas trwania nietykalnoci\n* Justice - generuje podwjn liczb znajdziek\n* The Hermit - powoduje, e Greed nie moe pojawi si w Sklepie; jeli Sklep zosta ju odwiedzony i zawiera walk z Greedem, zamienia go w normalny Sklep\n* Wheel of Fortune - przyzywa 2 Slot Machine'y\n* Strength - podwaja bonusy do statystyk (bez mnonika obrae)\n* Death - zadaje 80 obrae\n* Temperance - przyzywa 2 Blood Donation Machine'y\n* The Devil - bonus do obrae jest podwojony\n* The Tower - generuje 12 Troll Bomb\n* The Stars - jeli Treasure Room jeszcze nie zosta odwiedzony, bdzie on zawiera 2 przedmioty do wyboru\n* The Sun - zadaje 200 obrae\n* Judgement - przyzywa 2 Beggarw\n* The Fool? - dodatkowo wyrzuca na ziemi wszystkie przedmioty Isaaca, wliczajc Tarot Cloth\n* The Empress? - dodaje 3 tymczasowe kontenery serc\n* The Hierophant? - generuje 6 Bone Heartw (3 przy uyciu Blank Card)\n* The Lovers? - dodaje 4 Zamane Serca i generuje 4 przedmioty (po 2 przy uyciu Blank Card)\n* Justice? - generuje 4-14 skrzynek (2-7 przy uyciu Blank Card)\n* The Hermit? - dodatkowo generuje 1 centa\n* Wheel of Fortune - wywouje dodatkowy efekt Dice Roomu\n* The Hanged Man? - podwaja czas trwania efektu\n* Temperance? - Isaac zjada 20 piguek (10 przy uyciu Blank Card)\n* The Tower? - generuje 14 kamieni (7 przy uyciu Blank Card)\n* The Stars? - usuwa 2 przedmioty i generuje 4\n* Judgement? - przyzywa 2 Restock Machine'y (1 przy uyciu Blank Card)",
  452: "W momencie otrzymania obrae, Isaac wyrzuca z siebie 10 ez we wszystkich kierunkach oraz ma szans na aktywowanie efektu Anemic na 2 sekundy.\nzy maj +25 bonusu do Obrae.",
  453: "+0,38 Zasigu.\n+1 Wysokoci ez.\nPrzy uderzeniu przeszkody lub przeciwnika, zy Isaaca rozbijaj si na 1-3 koci, ktre zadaj 50% Obrae Isaaca.\nMa ma szans wypa z The Pile'a.",
  454: "Generuje losow kart, run lub piguk.\nPozwala Isaacowi na trzymanie 2 kart/run/piguek na raz.",
  455: "+0,38 Zasigu.\nGeneruje Lucky Penny.",
  456: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  457: "+1 Soul Heart.\nKade obraenia zadane Isaacowi maj 20% szansy na niezranienie go.",
  458: "Wyrzuca losowy trinket.\nDaje Isaacowi dodatkowe miejsce na trinkety.",
  459: "Daje szans na wystrzelenie zy, ktra przykleja si do przeciwnikw i zadaje im Obraenia Isaaca co sekund, przez 10 sekund lub dopki przeciwnik nie zginie.\nPrzeciwnicy, ktrzy skacz lub chowaj si pod ziemi, trac przyklejone zy, gdy to robi.",
  46: "+1 Szczcia.\nZwiksza szans na wygranie czego w Slot Machine, Shell Game/Hell Game i Fortune Telling Machine.\nZwiksza szans na otrzymanie nagrody po przejciu pokoju.\nZmienia wszystkie negatywne piguki w pozytywne, podobnie do PHD. Piguki nie s zidentyfikowane.\nPrzedmiot startowy Caina.",
  460: "Daje szans (niezalen od Szczcia) na wystrzelenie dezorientujcej zy. Efekt dezorientacji jest praktycznie permanentny na zwykych przeciwnikach i trwa bardzo dugo, w porwnaniu z innymi efektami, na bossach.\nEkran gry jest ciemniejszy.",
  461: "Daje szans na wystrzelenie kokonu, ktry generuje spowalniajcy biay luz. Jeli kokon trafi przeciwnika (wliczajc nietykalnych przeciwnikw, takich jak Stony) lub ognisko, generowana jest niebieska mucha lub niebieski pajk.\nKokony maj zwikszon wysoko ez i, w wyniku tego, zasig.\nSzansa na wystrzelenie kokonu jest zalenia od Szczcia, wynosi 15% przy Szczciu 0, ale nigdy nie moe osign 100%.",
  462: "+1,5 Zasigu.\n+1 Wysokoci ez.\nIsaac dostaje przeszywajce zy.\nPo przeszyciu jednego przeciwnika (wliczajc obiekty, ktre s uwaane za przeciwnikw, np. ogniska i Grimace'y), zy bd zadaway 2x Obraenia Isaaca, bd samonaprowadzajce oraz otrzymaj 1,3x mnonik wielkoci.",
  463: "+0,3 Obrae.\nDaje szans na wystrzelenie tej zy, ktra rozbija przeszkody (w tym Key Blocki) i otwiera drzwi oraz wejcia do Secret Roomw.",
  464: "+2 Soul Hearty przy podniesieniu.\nModyfikuje nagrody po przejciu pokoju i z Championw, w nastpujcej kolejnoci:\n* Soul Hearty - jeli Isaac nie ma kontenerw Czerwonych Serc i mniej ni 2 Soul Hearty\n* Czerwone Serca - jeli Isaac ma tylko 0,5 Czerwonego Serca\n* klucze - jeli Isaac nie ma kluczy\n* bomby - jeli Isaac nie ma bomb\n* Czerwone Serca - jeli Isaac ma co najmniej 1/2 pustego kontenera Czerwonego Serca\n* monety - jeli Isaac ma mniej ni 15 monet\n* klucze - jeli Isaac ma mniej ni 5 kluczy\n* bomby - jeli Isaac ma mniej ni 5 bomb\n* trinkety - jeli Isaac nie ma adnych trinketw i nie ma trinketw na ziemi w pokoju\n* Soul Hearty - jeli Isaac ma mniej ni 6 serc jakiekolwiek typu\nJeli adny warunek nie jest speniony, nagrody s takie, jakie byyby normalnie.\nSkrzynki nadal mog wypa w Chest.\nThe Lost i Tainted Lost ignoruj warunki dotyczce Soul Heartw.\nTainted ??? ignoruje pierwszy warunek dotyczcy bomb, ale nie drugi, a zawsze ma on mniej ni 5 bomb, gdy jego kupy nie licz si jako bomby.\nNagrody na The Forgottenie zale od aktywnej postaci.",
  465: "+0,35 do statystyki ez.\nNa klawiaturze pozwala na strzelanie ez po przektnej.\nZ kontrolerem analogowym pozwala na strzelanie w jakimkolwiek kierunku.",
  466: "W kadym pokoju, pierwszy zabity przeciwnik uwolni chmur gazu, ktra zadaje 5 Obrae, oraz zatruje pobliskich przeciwnikw. Trucizna zadaje Obraenia Isaaca na tick, przez 6 tickw.\nZatruci przeciwnicy rwnie uwolni chmur gazu przy mierci (nawet jeli nie s ju zatruci), rozprzestrzeniajc efekt.\nEfekt rwnie dziaa na Shopkeeperw.\nChmura gazu i trucizna moe zada obraenia przyjacielskim przeciwnikom.",
  467: "Przyzywa chowaca, ktrym mona sterowa klawiszami strzau. Chowaniec wystrzeliwuje niewidzialny spektralny laser, ktry stale zadale 10% Obrae Isaaca.\nJeli chowaniec nie jest wycelowany klawiszami strzau, bdzie on skierowany w kierunku ruchu. Gdy Isaac stoi w miejscu, chowaniec bdzie wycelowany na d.\nLaser chowaca synergizuje si z efektami ez Isaaca.",
  468: "Przyzywa chowaca, ktry naladuje ruchy Isaaca z 1-sekundowym opnieniem i zadaje 5 Obrae kontaktowych na tick (75 Obrae na sekund).\nPo zadaniu 666 Obrae, chowaniec znika i przyzywa przyjacielskie czarne Chargery. Isaac bdzie odtd zadawa takie obraenia kontaktowe, jakie zadawa Shade.",
  469: "Przyzywa chowaca, ktry poda za Isaaciem i zostawia za sob niebieski luz, ktry zadaje 2 Obrae na tick (6 na sekund) nielatajcym przeciwnikom.\nGdy chowaniec dotknie przeciwnika, ma szans na wygenerowanie promienia wiata z nieba, ktry zadaje 20 + Obraenia Isaaca na tick, do 8 razy.\nMa ma szans wypa z The Rainmakera.",
  47: "Przy uyciu, na ziemi pojawia si czerwony celownik, ktrym mona sterowa uywajc klawiszw strzau. Po ok. 2 sekundach, w celownik uderzy rakieta, zadajc 20x Obraenia Isaaca.",
  470: "Przyzywa chowaca, ktry lata po przektnej po pokoju i zadaje 2 Obrae kontaktowych na tick (30 na sekund).\nGdy Isaac strzela, Hushy zatrzymuje si w miejscu, blokuje pociski przeciwnikw oraz co 0,97 sekundy wystrzeliwuje 8 ez prostopadle i po przektnej (zy zadaj 6 Obrae).",
  471: "Przyzywa chowaca, ktry, po chwili adowania, pluje du grup ez (12-14 ez), podobnie do ataku Monstro.\nKada za zadaje 3,5 Obrae.",
  472: "Przyzywa chowaca, ktry poda za Isaaciem, zawsze zajmujc pierwsze miejsce w kolejce chowacw.\nKiedy Isaac strzela zami, chowaniec zatrzymuje si w miejscu i miga na biao. Podczas tego inne chowace s rwnie nieruchome, ale automatyczne strzelaj do pobliskich przeciwnikw.\nKiedy Isaac przestanie strzela zami, King Baby szybko do niego wraca.",
  473: "Przyzywa chowaca, ktry poda za Isaaciem i wolno szaruje przez pokj, gdy Isaac strzela, zadajc 2,7 Obrae kontaktowych na tick oraz blokujc pociski przeciwnikw.\nMa ma szans wypa z The Matriarch.",
  474: "Isaac otrzymuje ten przedmiot, gdy otrzyma obraenia, gdy posiada Glass Cannon.\nPrzedmiot jest nienaadowany. Po penym naadowaniu, automatycznie zamienia si w Glass Cannon.",
  475: "Przy uyciu zadaje 9 999 999 Obrae wszystkim przeciwnikom w pokoju, po czym zabija Isaaca 3 sekundy pniej.\nNie zabija obu faz bossw takich jak Hush czy Mega Szatan.\nNie zadziaa na Delirium, na drugiej fazie Matki w Corpse ani na drugiej fazie Ultra Greediera jako Tainted Forgotten, The Lost i Tainted Lost (Isaac zginie, zanim ich animacja mierci si skoczy).\nPrzedmiot ten ma wag obnion o 80%, co powoduje, e jest rzadziej spotykany ni inne przedmioty.",
  476: "Przy uyciu generuje 1 znajdk/skrzynk tego samego typu, co 1 losowa znajdka/skrzynka w pokoju (np. uycie na bombie moe wygenerowa bomb, podwjn bomb lub Troll Bomb).",
  477: "Przy uyciu absorbuje wszystkie przedmioty w pokoju.\nPo zaabsorbowaniu aktywnego przedmiotu, Void otrzymuje jego efekt, ktry aktywuje si przy nastpnym uyciu Voidu (w kolejnoci, w jakim zostay zaabsorbowane).\nPo zaabsorbowaniu pasywnego przedmiotu, Isaac otrzymuje 2 bonusy do statystyk. Moliwe bonusy to:\n* +1 Obrae\n* +0,5 ez\n+0,2 Szybkoci (nie moe przekroczy 2)\n* +0,2 Szybkoci Strzaw\n* +1 Szczcie\n* +0,5 Zasigu\nVoid nie moe zaabsorbowa przedmiotw z ukadu z Diabem, Black Marketw i Shopw, chyba e zostay kupione. Void moe zosta uyty, gdy Isaac przez krtk chwil trzyma przedmiot nad gow po zakupie.\nW pokojach z wyborem przedmiotw, uycie Voidu zaabsorbuje tylko jeden przedmiot.\nStartowy przedmiot Apollyona, nawet jeli nie zosta jeszcze odblokowany.",
  478: "Zatrzymuje wszystkich przeciwnikw (wliczajc ogniska strzelajce pociskami) w miejscu na 30 sekund lub dopki Isaac nie zacznie strzela.\nEfekt nie dziaa na Troll Bomby oraz na przeciwnikw powstaych podczas czasu trwania efektu.\nUycie Dark Arts nie zatrzymuje efektu.\nThe Soul The Forgottena bdzie mg porusza si poza zasigiem acucha, jeli przedmiot zosta aktywowany, gdy The Soul by aktywny.",
  479: "Przy uyciu niszczy trinket posiadany przez Isaaca i aplikuje jego efekty na stae.\nZwiksza szans na otrzymanie trinketu (2% szansy na zamienienie znajdki w trinket).\nFaded Polaroid nie moe zosta uyty do otwarcia Strange Door po wchoniciu przez Smelter.",
  48: "Daje Isaacowi przeszywajce zy.",
  480: "Przy uyciu podwaja liczb Niebieskich Muszek/Pajkw Isaac posiada, lub generuje jedn Niebiesk Muszk/Pajka, jeli Isaac nie posiada adnych, oraz zamienia wszystkie znajdki w pokoju w Niebieskie Muszki lub Niebieskie Pajki.",
  481: "Przy uyciu obraca grafiki w obecnym pokoju o 90 i przesuwa je do gry i w lewo (hitboxy przeciwnikw pozostaj takie same), daje Isaacowi losowe efekty ez na czas trwania pokoju oraz na stae zwiksza jedn ze statystyk Isaaca i zmniejsza inn.\nMoliwe zmiany statystyk to: 1 Obrae, 0,5 ez, 0,2 Szybkoci, 0,2 Szybkoci Strzaw, 1 Szczcia, 1,5 Zasigu.\nPrzy uyciu w pokoju z przedmiotami przelosowuje je w zgliczowane przedmioty (jak TMTRAINER).",
  482: "Przy uycia zmienia Isaaca w losow posta i ma 50% szansy na usunicie ostatniego zebranego przedmiotu (nie liczc Clickera). Posta musi zosta odblokowana, oprcz Dark Judasa i Lazarus Risen.\nNiesplamione postaci mog zosta zmienione tylko w niesplamione postaci, a splamione tylko w splamione.\nZmiana w Edena/Tainted Edena nie zuywa tokenw.,\nZmiana w Jacoba & Esau losowo podzieli przedmioty midzy nimi. Zmiana z Jacoba & Esau w inn posta da jej wszystkie przedmioty obu postaci.\nZdrowie jest zachowane po zmianie w inn posta, z nastpujcymi wyjtkami:\n* po zmianie w ???, Dark Judasa, Tainted ???, Tainted Forgottena lub Tainted Bethany wszystkie czerwone serca zostan usunite, a Bone Hearty oprnione; jeli usunoby to ich cae zdrowie, postacie te otrzymuj 1/2 Soul Hearta\n* zmiana z The Losta/Tainted Losta na inn posta da jej 1/2 Soul Hearta\n* kontenery Czerwonych Serc zostan zmienione w monety Keepera/Tainted Keepera, minimalnie 2, maksymalnie 3 w przypadku Keepera lub 2 w przypadku Tainted Keepera; minimalnie jeden kontener zostanie wypeniony, zalenie od liczby wypenionych Czerwonych Serc\n * zmiana z Keepera/Tainted Keepera w inn posta zamieni jego monety w kontenery Czerwonych Serc\n* zmiana w The Forgottena zmieni wszystkie kontenery Czerwonych Serc w Bone Hearty, wypenione w zalenoci od liczby wypenionych Czerwonych Serc; Soul Hearty i Black Hearty zostan usunite, a The Soul zawsze bdzie zaczyna z 1 Soul Heartem; jeli usunoby to jego cae zdrowie, The Forgotten otrzyma pustego Bone Hearta\n* zmiana w Bethany zamieni wszystkie Soul Hearty/Black Hearty w jej adunki; zmiana z Bethany w inn posta zamieni adunki Soul Heartw w Soul Hearty\n* zmiana w Jacoba & Esau podzieli wszystkie serca rwno midzy nimi; zmiana z Jacoba & Esau w inn posta da jej wszystkie serca obu postaci",
  483: "Po uyciu, niszczy wszystkie przeszkody, maszyny, ebraczkw, ogniska, statuy Aniow, otwiera przejcia do Secret Roomw oraz zadaje 200 Obrae kademu przeciwnikowi w obecnym pokoju i kadym innym pokoju na pitrze (wliczajc pokoje, ktre zostay ju odwiedzone).\nOtwiera przejcie do Boss Rusha i ???, nawet jeli limit czasu zosta przekroczony. Po wyjciu z Boss Roomu lub wyjciu z Boss Rusha/przejcia do ???, drzwi zostan zamknite.\nNie otwiera drzwi do Mega Szatana ani drzwi do Mom's Heart w Mausoleum II/Gehennie II.\nUycie w Downpour II/Dross II zniszczy lustro, uniemoliwiajc przejcie do Lustrzanego Odbicia. Uycie w Lustrzanym Odbiciu take zniszczy lustro, uniemoliwiajc powrt.",
  484: "Przy uyciu Isaac puszcza bka, odpychajc przeciwnikw i ich strzay, oraz wysya skalne fale, ktre zadaj 10 Obrae na tick przeciwnikom i niszcz przeszkody.\nPrzedmiot ten nie wystpuje w adnej puli, ale ma 5% szansy na zastpienie Butter Bean podczas rozgrywki.",
  485: "Przy uycia ma 50% szansy na podwojenie wszystkich przedmiotw, znajdziek, skrzynek i trinketw w pokoju, i 50% szansy na zniszczenie ich i wygenerowanie 1 centa.",
  486: "Przy uyciu zadaje Isaacowi obraenia bez zabierania mu zdrowia, dajc mu chwilow nietykalno i wywoujc efekty przedmiotw aktywowanych przez obraenia.",
  487: "Przy uyciu zabiera Isaacowi 1 Czerwone Serce, daje mu +0,2 Obrae, generuje 1 Cube of Meat oraz daje efekt Anemic na czas trwania obecnego pokoju.\nCube of Meat wygenerowany przez ten przedmiot nie liczy si jako przedmiot, nie usuwa Cube of Meat pul i nie zostanie Isaacowi odebrany w obszarze Knife Piece 2 w Mines II/Ashpit II.\nUycie przedmiotu liczy si jako obraenia i wywoa efekty aktywowane przez otrzymanie obrae.",
  488: "Przy uyciu daje efekt losowego przedmiotu na czas trwania obecnego pokoju.\nUycie Metronome kilka razy w tym samym pokoju zamieni poprzedni przedmiot nowym.\nMetronome nie moe da efektu Glowing Hour Glass, Plan C, Clickera, Urn of Souls ani R Key.\nMetronome ma zmniejszon szans na wywoanie efektu Genesis i Death Certificate",
  489: "Dziaa jak dowolny przedmiot-kostka (oprcz Spindown Dice). Midzy kostkami mona przecza si klawiszem zamiany piguki/karty (domylnie Ctrl).\nPocztkowy czas adowania to 2 adunki, potem zaley od ostatniej aktywowanej kostki:\n* D1 - 1\n* D4 - 6\n* The D6 - 6\n* Eternal D6 - 2\n* D7 - 3\n* D8 - 4\n* D10 - 1\n* D12 - 3\n* D20 - 6\n* D100 - 6",
  49: "Przy uyciu, nastpny strza Isaac bdzie laserem, ktry zadaje 2x jego obraenia do 13 razy. Laser moe otrzyma efekty ez Isaaca.\nMoe wypa z Envy'ego.",
  490: "Przy uyciu generuje 2 losowe przedmioty z puli obecnego pokoju.\nPrzedmiot nie ma adunkw przy pierwszym podniesieniu.",
  491: "Przyzywa chowaca, ktry wyrzuca piguk co 7 pokoi. Po podniesieniu pierwsz piguk wyrzuca po 6 pokojach.\nGdy Isaac uyje piguki, Acid Baby zadaje przeciwnikom 10 Obrae i zatruwa ich na 6-7 tickw.",
  492: "+1 Szczcie.\nPrzyzywa chowaca, ktry lata po pokoju. Jeli w pokoju znajduje si Tinted Rock, kamie z Crawl Space pod nim lub przejciem do Secret Roomu/Super Secret Roomu, chowaniec podleci do niego i si nad nim zatrzyma.\nJeli w pokoju znajduje si wicej ni 1 sekret, chowaniec bdzie midzy nimi lata.",
  493: "Isaac dostaje bonus do Obrae zaleny od iloci pustych serc, jakie posiada.\nBonus jest obliczany wg wzoru (2 * x)<sup>1,6</sup> * 0,1, gdzie x = ilo caych pustych serc.\nBonus dziaa rwnie na monety Keepera i Tainted Keepera oraz Bone Hearty Forgottena.",
  494: "Przy uderzeniu w przeszkod lub przeciwnika zy Isaaca generuj iskr elektryczn o maym zasigu. Jeli uderzy ona w przeciwnika, wystrzeli do nastpnego pobliskiego przeciwnika, i tak dalej, maksymalnie 4 razy.\nIskry zadaj 0,5x Obraenia Isaaca.\nJeli podoga jest mokra (np. w Downpour, Dross, Flooded Caves lub po uyciu Flush!), iskry maj zwikszony zasig.",
  495: "Podczas strzelania, Isaac ma szans na wystrzelenie niebieskiego ogniska, podobnego do tego z The Candle, zadajcego obraenia przeciwnikom i blokujcego ich pociski. Ognisko moe przelatywa nad przeszkodami i wygasa po pewnym czasie.\nOgnisko zadaje 3x do 6x Obrae Isaaca, zalenie od jego rozmiaru. Szansa na jego wystrzelenie wynosi 1/(12 - Szczcie), maksymalnie 50% przy Szczciu 10.\nPuszczanie bkw (np. uywajc The Poop, Butter Bean, itp.) rwnie wystrzeliwuje ognisko.",
  496: "Daje szans na wystrzelenie strzykawki, ktra zadaje 3x Obraenia Isaaca i natychmiast zabija zwykych przeciwnikw, powodujc, e rozpadaj si oni na 10 ez we wszystkich kierunkach.\nSzansa jest zalena od Szczcia i wynosi 3,33% przy Szczciu 0.\nStrzay-strzykawki mog zabi przeciwnikw, ktrzy s normalnie nietykalni, np. Death's Heady, oczy Peepa, Grimace'y, itp.\nzy spord 10 ez wystrzeliwanych przez zabitych przeciwnikw mog by kolejymi zami-strzykawkami, ale bd one tylko zadaway 3x Obraenia Isaaca, a nie bd natychmiast zabijay ani powoday rozpadu na nowe zy.",
  497: "Przy wejciu do nowego pokoju, Isaac bdzie zakamuflowany, dziki czemu wszyscy przeciwnicy bd zdezorientowani. Efekt trwa dopki Isaac nie zacznie strzela.\nGdy jest on zakamuflowany, Isaac otrzymuje +0,5 Szybkoci. Przy wyjciu Isaaca z kamuflau, przeciwnicy wok niego otrzymuj obraenia, a Isaac zyska tymczasowy bonus do ez i obrae.\nEfekt nie dziaa na bossw, ogniska, Poky'ch, Slide'y, Stone Grimace'y i Stoney'ch.",
  498: "Kiedy Devil Room lub Angel Room zostanie wygenerowany po walce z bossem, drugi typ pokoju rwnie si pojawi. Po wejciu do jednego, drugi zniknie.\nJeli Isaac teleportuje si do Devil/Angel Roomu przy pomocy karty Joker, Red Chestu lub Sacrifice Roomu przed pokonaniem bossa, szanse na dany pokj bd normalnie obliczone. Po pokonaniu bossa, tylko ten pokj, do ktrego Isaac si wczeniej przeteleportowa, bdzie otwarty.",
  499: "Gwarantuje pojawienie si Angel Roomu na kadym pitrze, na ktrym moe si on pojawi. Pokj ten nie zniknie nawet po wyjciu z niego i z Boss Roomu (oprcz w Greed Modzie).",
  5: "2x + 1,5 Zasigu, 1,6x Szybkoci Strzaw, +1,5 Obrae i -1 Szczcia.\nzy Isaaca po krtkiej chwili po wystrzeleniu zawracaj i lec w kierunku Isaaca.",
  50: "+1 Obrae.\nMa szans wypa z walki ze Stevenem. Szansa jest wiksza, jeli Little Steven zosta ju podniesiony.",
  500: "Przyzywa chowaca, ktry wyrzuca sakiewk na zmian co 5 lub 6 pokoi.",
  501: "Isaac otrzyma Czerwone Serce za kade 25 monet, ktre posiada. Wydanie monet powoduje, e serce znika.\nKeeper i Tainted Keeper mog otrzyma wicej ni 3/2 monety zdrowia z tym przedmiotem. W poczeniu z Deep Pockets, przy 999 monetach Keeper moe mie 15 monet zdrowia, a Tainted Keeper 14 monet, przekraczajc standardowy limit 12 serc.\nPozbycie si serc otrzymanych z Greed's Gullet (np. przez Devil Room lub Guppy's Paw) usunie odpowiadajc im liczb monet.",
  502: "Przy strzelaniu, Isaac ma szans (niezalen od Szczcia) na wystrzelenie dodatkowej zy, ktry zadaje 2x jego Obraenia oraz zostawia za sob biay luz, ktry spowalnia przeciwnikw.\nIsaac wystrzeli t z take przy otrzymaniu obrae.",
  503: "Daje szans na wystrzelenie zy, ktry przyzwie ap Big Horna. apa natychmiast zabija kadego przeciwnika, ktry nie jest bossem (wliczaj normalnie nietykalnych przeciwnikw, takich jak Stoney czy Death's Head, oraz warianty Pokey i Stone Grimace), a bossw atakuje 3 razy po 12 Obrae. apy s w stanie take zniszczy Niebieskie i Fioletowa Ogniska.\nSzansa jest zalena od Szczcia, wynosi 5% przy Szczciu 0 i maksymalnie 20% przy Szczciu 15.\nIsaac zadaje 3,5 Obrae kontaktowych (do 2 razy na sekund).\nMa ma szans wypa z Little Horna lub Big Horna.",
  504: "Przy uyciu generuje nieruchom muszk, ktra strzela w przeciwnikw, zadajc im 3,5 Obrae na strza. Muszka jest nietykalna i ignorowana przez przeciwnikw.\nMa ma szans wypa z Turdleta.",
  505: "Przy wejciu do pokoju z przeciwnikami, istnieje 20% szansy na przyzwanie przyjacielskiego przeciwnika (Attack Fly, Fat Bat, Frowning Gaper, One Tooth, Pooter albo Vis).\nEfekt nie aktywuje si na falach Greed Mode'a, przez co jest praktycznie bezuyteczny.",
  506: "zy, ktre uderzaj przeciwnikw od tyu, zadaj podwjne obraenia i powoduj krwawienie.\nKrwawicy przeciwnicy otrzymuj obraenia zalene od tego, jak szybko si poruszaj, oraz zostawiaj za sob czerwony luz, ktry zadaje obraenia innym przeciwnikom.\nBossowie i mini-bossowie s odporni na krwawienie, ale przyjmuj podwjne obraenia.",
  507: "Przy uyciu zadaje wszystkim przeciwnikom w pokoju Obraenia Isaaca + 10% maksymalnego zdrowia przeciwnika.\nMa 15% szansy na wyrzucenie 1/2 czerwonych serc, gdy zadaje obraenia.\nIpecac daje bonus tylko +2 Obrae, a nie +40.",
  508: "Przyzywa orbitala, ktry przy kontakcie z przeciwnikami zadaje 0,5x Obrae Isaaca na tick (3x Obrae Isaaca na sekund) oraz powoduje, e przeciwnicy zaczynaj krwawi.\nKrwawicy przeciwnicy otrzymuj obraenia zalene od tego, jak szybko si poruszaj, oraz zostawiaj za sob czerwony luz, ktry zadaje obraenia innym przeciwnikom.\nPrzedmiot rwnie blokuje pociski przeciwnikw.",
  509: "Orbital, ktry co 1/3 sekundy strzela do przeciwnikw znajdujcych si w jego linii wzroku, zadajc 3,5 Obrae, blokuje pociski przeciwnikw oraz zadaje 2 Obrae kontaktowych na tick.",
  51: "+1 Obrae.\nZwiksza szans na Devil Room/Angel Room o 10%, lub 15% w przypadku 2 Pentagramw.",
  510: "Przy uyciu generuje przyjacielskiego bossa w formie Delirium na czas trwania obecnego pitra oraz zmienia wygld obecnego pokoju.\nBoss bdzie otrzymywa 1 punkt Obrae za normalne uderzenie oraz 5 punktw Obrae za wybuch, a nie bdzie otrzymywa obrae od kolcw, ognisk i czerwonych kup.\nZdrowie bossa bdzie powoli male, przy czym jego kolor bdzie coraz bardziej szary.\nUycie przedmiotu po raz drugi, podczas gdy poprzednio wygenerowany boss jeszcze yje, zabija poprzedniego bossa i generuje nowego.\nPrzedmiot ten ma wag obnion o 60%, co powoduje, e jest rzadziej spotykany ni inne przedmioty.",
  511: "Przyzywa muszk, ktra lata wok przeciwnikw, zadajc 2 punkty obrae na tick.",
  512: "Przy uyciu Isaac rzuca czarn dziur, ktra wsysa przeciwnikw i zadaje im 0,4 Obrae na tick (maksymalnie 6 DPS). Efek trwa okoo 6 sekund.\nDziura lekko wsysa rwnie Isaaca, znajdki i pociski przeciwnikw, oraz niszczy przeszkody.",
  513: "+1 Soul Heart.\n+0,1 Obrae.\nPrzeciwnicy mog losowo zosta oczarowani lub zastraszeni.\nOtrzymanie obrae moe spowodowa wygenerowanie Rainbow Poop.\nEfekty te s niezalene od Szczcia.",
  514: "Przeciwnicy, ich pociski, zy i znajdki zawieszaj si w miejscu w losowych momentach. Szansa na ten efekt jest zalena od Szczcia. Zawieszone pociski przeciwnikw po chwili znikn.\nMa 25% na podwojenie nagrody po przejciu pokoju. Nie moe podwoi skrzynek (oprcz tych uzyskanych z Options?), piguek ani kart.",
  515: "Przy uyciu generuje przedmiot z puli obecnego pokoju, z szans na wygenerowanie A Lump of Coal lub The Poop zamiast niego.\nSzansa na wygenerowanie przedmiotu zalena jest od Szczcia. Przy Szczciu 0 istnieje 83,3% szans na przedmiot z puli i po 8,3% szansy na A Lump of Coal/The Poop. Przy Szczciu 10 istnieje 90,9% szans na przedmiot z puli i po 4,5% szansy na A Lump of Coal/The Poop.",
  516: "Przy uycia przyzywa chowaca, ktry obraca si i strzela w 8 kierunkach.\nzy chowaca maj efekty ez Isaaca, a jego tempo obracania si i strzelania zaley od statystyki ez.",
  517: "+7 bomb przy podniesieniu.\nUsuwa opnienie stawiania bomb, co umoliwia szybsze ich stawianie.\nBomby nie odpychaj si nawzajem.",
  518: "Przyzywa chowaca z losowymi obraeniami, zami i efektami ez. Obraenia chowaca mog by w przedziale ok. 2-6, Opnienie ez w przedziale ok. 12-30, a moliwe efekty ez s takie same, jak efekty Fruit Cake.\nChowaniec jest przelosowany w nowego na pocztku kadego pitra.",
  519: "Przyzywa chowaca, ktry przeksztaca si w bia form losowego chowaca spord nastpujcych co 10 sekund: Acid Baby, BBF, Bob's Brain, Dry Baby, Headless Baby, Hushy, Incubus, Lil Brimstone, Lil Gurdy, Lil Haunt, Lil Monstro, Little Chubby, Little Gish, Robo-Baby 2.0, Rotten Baby, Seraphim, Super Meat Boy (4 stadium Cube of Meat), Big Chubby, Farting Baby, Fruity Plum, Isaac's Head, Lil Loki.",
  52: "zy Isaaca zastpione s przez bomby, ktre eksploduj ok. 1 sekundy po wystrzeleniu. Bomby zadaj 10x Obraenia Isaaca, jeli wyniesie to 60 Obrae lub mniej, powyej tego limitu 30 + 5x Obraenia Isaaca.\n2,5x Opnienia ez.\nZasig i Szybko Strzau wpywaj na szybko, z jak bomby s wystrzeliwane.\nBomby nie odbijaj si od siebie.",
  520: "Przedmiot aktywny Isaaca otrzymuje 1 adunek co 15 zabitych przeciwnikw.",
  521: "Przy uyciu w Sklepie, Devil Roomie lub Black Markecie za darmo wykupuje jeden artyku.\nPasywnie gwarantuje, e jeden losowy artyku w Sklepie bdzie przeceniony.\nWzicie przedmiotu z Devil Roomu za darmo nie wpywa na szans na Angel Room.\nUycie Couponu jako The Lost lub Tainted Lost nie spowoduje zniknicia pozostaych przedmiotw.",
  522: "Po uyciu, przez 3 sekundy, zatrzymuje wszystkie pociski przeciwnikw, ktre zbli si do Isaaca, oraz odpycha przeciwnikw. Po upywie tego czasu pociski s wystrzelone w kierunku przeciwnikw, zadajc im 3,5 Obrae.",
  523: "Jeli pudeko jest puste, uycie go podniesie maksymalnie 10 przedmiotw i znajdziek z pokoju (przedmioty i znajdki wybierane s losowo). Ponowne uycie pudeka umieci wszystkie przechowane w nim przedmioty i znajdki w obecnym pokoju.\nEfekt nie dziaa na niezakupionych przedmiotach w Sklepach i Devil Roomach.\nUycie przedmiotu w Challenge Roomach lub Boss Rushu zacznie wyzwanie.\nUycie przedmiotu w pokoju, w ktrym znajduje si kilka przedmiotw do wyboru, podniesie tylko 1 przedmiot, a reszta zniknie.\nPrzedmiot nie podniesie znikajcych monet/serc generowanych przez Tainted Keepera i Tainted Magdalene.",
  524: "Pomidzy zami Isaaca pojawiaj si elektryczne iskry, ktre zadaj 33% Obrae Isaaca.\nIskry mog pojawi si midzy zami Isaaca a zami jego chowacw.",
  525: "Przy otrzymaniu obrae, Isaac generuje orbitala, ktry blokuje pociski przeciwnikw i zadaje 7 Obrae kontaktowych na tick.\nIsaac moe mie maksymalnie 3 orbitale z Leprosy.\nOrbitale s zniszczone po zablokowaniu 3 pociskw i/lub zadaniu 30 tickw obrae.\nMa ma szans wypa z Fistuli.",
  526: "Przyzywa maego Jedca Apokalipsy, ktry zmienia si co 10 sekund. Dziaanie zaley od typu jedca:\n* Famine - lata po pokoju, zostawia czarny spowalniajcy luz, przyzywa te szaracze i zadaje 0,5 Obrae kontaktowych na tick\n* Pestilence - lata po pokoju, zostawia zielony luz, ktry zadaje 1 obrae/tick, przyzywa zielone szaracze i zadaje 0.5 Obrae kontaktowych/tick\n* War - ciga przeciwnikw, zadaje 1.5 Obrae kontaktowych/tick i przyzywa czerwone szaracze, ale wolniej ni inni jedcy\n* Death - ciga przeciwnikw, zadaje 1.5 obrae kontanktowych/tick i przyzywa czarne szaracze\n* Conquest - lata po pokoju, zadaje 0.5 Obrae kontaktowych/tick i przyzywa 2 biae szaracze na raz\nPo opisy szaraczy, patrz odpowiednie trinkety.",
  527: "Przy uyciu przyzywa duszka oraz niebieski celownik, ktrym Isaac moe sterowa. Po kilku sekundach, celownik zniknie, a duszek wykona akcj zalen od tego, co znajdowao si najbliej celownika:\n* przedmioty, znajdki, trinkety, skrzynki/Red Chesty/Spiked Chesty/Mega Chesty - przynosi je do Isaaca; dziaa rwnie w Sklepach, Devil Roomach i Black Marketach, gdzie ma 50% na przyniesienie przedmiotu/znajdki za darmo (ukradzione przedmioty/znajdki nie zostan zastpione przez nowe, nawet jeli Isaac posiada Restock); w pokojach z 2 przedmiotami do wyboru, jeli duszek podniesie jeden przedmiot, a w tym czasie Isaac podniesie drugi, duszek zniknie z przedmiotem\n* Golden Chesty/Eternal Chesty/Stone Chesty - otwiera je za darmo (otwarcie skrzynek w Challenge Roomach nie wywoa fali)\n* przyciski (wliczajc przycisk zatrzymujcy fale w Greed Modzie) - naciska przycisk\n* przeciwnicy - atakuje przeciwnika dopki on nie zginie lub nie bdzie mg by dalej atakowany (np. przez zmian formy); duszek zadaje 3 Obrae na tick (9 na sekund; BFFS! nie zwiksza tych obrae)\n* drzwi - otwiera drzwi za darmo; dziaa rwnie na drzwiach do Mega Szatana, drzwiach do przejcia do Mausoleum/Gehenny oraz wyjciu z Boss Rusha i Husha; nie dziaa na Strange Door i schowku w Home, ktry zawiera splamione postacie\n* ciany, gdzie znajduje si wejcie do Secret Roomu - wybucha, otwierajc wejcie\n* przeszkody moliwe do zniszczenia, Shopkeeperzy, maszyny, ebraczki - wybucha i je niszczy",
  528: "Orbital, ktry rozdziela kad z, jaka przez niego przejdzie, na 4 rnokolorowe zy.",
  529: "zy Isaaca s zastpione przez gaki oczne, ktre mog si od siebie odbija.\nOczy nie s ograniczone przez zasig, ale znikaj, gdy przestan si rusza lub trafi przeciwnika lub przeszkod. Oczy zaczn spada na ziemi po odbiciu si od siebie 8 razy.",
  53: "Znajdki (wyczajc sakiewki) s przycigane do Isaaca. Mog one przelatywa przez dziury i kamienie, ale nie Key Blocki.\nSerce i baterie s przycigane tylko jeli Isaac moe je podnie.\nSkrzynki, ktre nie wymagaj klucza ani bomby do otwarcia (wliczajc Spiked Chesty i Mimic Chesty), automatycznie si otworz, jeli Isaac zbliy si do nich na ok. 2 pytki. Otwarcie skrzynek w ten sposb nie aktywuje fali w Challenge Roomach.\nNiektrzy przeciwnicy, jak np. Horf, take s przycigani do Isaaca.",
  530: "Przy wejciu do pokoju, nad gow jednego z przeciwnikw pojawi si czaszka. Po zabiciu tego przeciwnika, czaszka przejdzie nad gow innego przeciwnika. Jeli wszyscy przeciwnicy zostan zabici w kolejnoci wyznaczonej przez czaszki, Isaac otrzyma jedn z nastpujcych nagrd: Soul Hearta, piguk, klucz, bomb, Nickel, +0,2 Szybkoci, +0,5 Zasigu, +0,5 ez, +0,2 Szybkoci Strzau, +1 Obrae lub +1 Szczcia.\nJeli Isaac zabije przeciwnika nieoznaczonego czaszk, czaszka zniknie i nie bdzie on mg otrzyma nagrody.\nJeli Isaac wyjdzie z pokoju przed przejciem go, ponownie wejcie zrestartuje czaszk i Isaac bdzie mg ponownie sprbowa wyzwania, nawet jeli wczeniej mu si nie udao.\nPokoje bez przeciwnikw, a jedynie z przyciskami, rwnie dadz Isaacowi nagrod.\nKolejno przeciwnikw do zabicia jest zawsze taka sama, zaczynajc od lewego grnego rogu pokoju. Jeli nowi przeciwnicy s wygenerowani w pokoju, zostaj dodani do koca kolejki.",
  531: "Minus do ez (Opnienie ez * 2 + 10).\n+1 Obrae.\n1,5x mnonik Obrae.\nOk. 1,71x wielkoci ez.\nIsaac bdzie strzela zami w uku. Po spadniciu na ziemi lub uderzeniu w przeszkod lub przeciwnika zy rozpadaj si na wiele maych ez we wszystkich kierunkach. Mae zy zadaj ok. 50-80% obrae gwnej zy.\nWiksza Szybko Strzaw obnia uk, a mniejsza go podwysza.",
  532: "zy Isaaca spowalniaj wraz z przebytym dystansem. Gdy si zatrzymaj lub trafi w przeszkod, rozpadaj si na 8 ez we wszystkich kierunkach, zadajc 1/2 Obrae rozbitej zy.\nIsaac moe strzela zami w inne zy, dodajc ich obraenia. Nakarmienie zy 5 innymi zami spowoduje, e wybuchnie ona natychmiast.\nWybuch zy moe nastpi z samego jej rozmiaru, zwikszone z obrae lub przedmiotw zwikszajcych rozmiar ez.",
  533: "zy Isaaca zostaj zastpione przez promienie wiata. Promienie zadaj 33% Obrae Isaaca, ale mog zada obraenia jednemu przeciwnikowi kilka razy.\nPromienie zmniejszaj rozmiar a do zniknicia.",
  534: "Pozwala Isaacowi na posiadanie 2 przedmiotw aktywnych.\nPrzedmioty na drugim miejscu bd adowa si tak samo, jak przedmioty na pierwszym miejscu.",
  535: "+1 Soul Heart\nLeczy 1 czerwone serce.\nDaje efekt Holy Mantle po wejciu do Boss Roomu. Efekt znika po wyjciu z niego.\nEfekt dodaje si z Holy Mantle i Holy Card, ale Holy Card zostanie zuyte jako pierwsze.\nW Greed Modzie, efekt aktywuje si tylko na pitrze Shop.\nEfekt nie aktywuje si na The Beast.",
  536: "Przy uyciu niszczy 1-2 chowace Isaaca, generujc przedmiot z puli Devil Roomu za kadego powiconego chowaca.\nDodatkowo niebieskie muchy, Niebieskie Pajki oraz szaracze zostan zmienione w 1 centa.\nJeli Isaac nie ma adnych chowacw ani pajkw/much/szaraczy, przedmiot nie zrobi nic i nie zostanie zuyty.\nJeli w puli Devil Roomu nie ma ju przedmiotw, wygenerowany zostanie Pentagram.\nW Greed Modzie wygenerowany zostanie przedmiot z puli normalnego Devil Roomu, nie z Devil Roomu Greed Mode'a.",
  537: "Generuje losow piguk przy podniesieniu.\nPrzyzywa chowaca, ktry, po chwili adowania, wypluwa rne typy luzu, zaczynajc od zielonego. Kolor chowaca i typ luzu zmieniaj si losowo, kiedy Isaac uyje piguki. Moliwe ypy luzu to:\n* zielony: zadaje 0,4 Obrae na tick (12 na sekund)\n* czarny: spowalnia przeciwnikw\n* czerwony: zadaje 1,2 Obrae na tick (36 na sekund); luz szybko znika, oprcz wikszej kauy na jego kocu\n* ty: ma ksztat stoka i zadaje tym wicej obrae, im dalej - 2 Obrae (ok. 6 na sekund) na tick w najbliszym punkcie, do 15,2 Obrae (ok. 47 na sekund) w najdalszym\n* biay - ma ksztat piercienia i spowalnia przeciwnikw\nMa ma szans wypa z Blastocysta.",
  538: "Generuje 3 losowe trinkety.\nPrzy otrzymaniu obrae Isaac ma 5% szans na poknicie trinketu (lub 2 trinketw), ktry trzyma, zyskujc jego efekt na stae.",
  539: "Przyzywa chowaca, ktry rozbija si i generuje przyjacielskiego przeciwnika, gdy Isaac otrzyma obraenia. Chowaniec regeneruje si po przejciu do nastpnego pokoju.\nTyp wygenerowanego przeciwnika zaley od tego, ile pokoi/fal w Greed Modzie zostao pokonanych bez otrzymania obrae:\n* 0-1: Black Fly, Attack Fly, Maggot, Spider, Dip, Embryo, Nerve Ending\n* 2-4: Moter, Pooter, Swarm Fly, Level 2 Fly, Full Fly, Horf, Sucker, Dart Fly, Big Spider, Guts, Hopper, Gusher, Round Worm, Heart, Gush\n* 5-7: Squirt, Ragling, Homunculus, Level 2 Spider, Small Baby Long Legs, Wall Creep, Nightcrawler, Ulcer, Ministro, Mulligan, Hive, Nest, Flaming Hopper, Gaper, Cyclopia, Skinny, Dople, Globin, Maw, Psychic Horf, Tumor, Fatty, Half Sack, Clotty, Charger, Brain, Conjoined Spitty, Buttlicker, Host, One Tooth, Baby, Dukie, Black Globin's Body, Black Globin's Head, Para-Bite\n* 8-10: Splasher, Boom Fly, Keeper, Fat Bat, Psy Tumor, Imp, Fred, Cod Worm, Lump, Swinger, Bony, Black Globin, Null, Begotten, Knight, Bone Knight, Blubber, Conjoined Fatty, Oob, Leech, MemBrain, Wizoob, Mr. Maw, Flesh Death's Head, Mushroom, Blind Creep, The Thing, Blister, Leaper, Hanger, Poison Mind, Fistuloid, Tar Boy\n* 11-13: Gurgle, Walking Gut, Black Bony, Black Maw, Camillo Jr., Eye, Roundy, Mom's Hand, Mega Clotty, Swarmer, Fat Sack, Mobile Host\n* wicej ni 13: Red Ghost, Rage Creep, Vis, Gurglings, Dinga, Portal",
  54: "Pokazuje wszystkie pokoje na mapie, oprcz Secret Roomu i Super Secret Roomu.\nNie pokazuje zawartoci jeszcze nieodwiedzonych pokoi.",
  540: "zy odbijaj si od ziemi, przy odbiciu zadajc 25% Obrae Isaaca pobliskim przeciwnikom.",
  541: "+1 Bone Heart.\nGeneruje 3 Czerwone Serca.\nDaje szans na wygenerowanie Polished Bone przy straceniu Bone Hearta.",
  542: "Przyzywa orbitala, ktry kry dookoa Isaaca i odbija pociski przeciwnikw. Odbite pociski zadaj przeciwnikom 5 Obrae, jeli zraniyby Isaaca za 1/2 serca, albo 10 Obrae, gdyby zraniy go za 1 serce.\nOrbital nie zadaje obrae kontaktowych, ale zabije przeciwnikw-muchy, podobnie jak Pretty Fly.",
  543: "Przyzywa chowaca, ktry generuje bia kup, kiedy Isaac otrzyma obraenia.\nBiaa kupa ma aur, ktra zwiksza Obraenia Isaaca o 25%, zwiksza zy, daje zom Isaaca efekt samonaprowadzajcy oraz daje mu szans na zablokowanie obrae.\nKupa jest odporna na ataki przeciwnikw, oprcz eksplozji i atakw deptajcych, i ma wytrzymao podobn do Zotej Kupy.",
  544: "Przyzywa ebro, ktrym mona sterowa klawiszami strzaw i ktre buja si tam i z powrotem w nadanym mu kierunku. ebro zadaje Obraenia Isaaca 6 razy na sekund oraz moe niszczy kupy i ogniska.",
  545: "Przy uyciu, generuje kocianego orbitala lub przyjacielskiego Bony'ego, Bone Fly, Black Bony'ego lub Revenanta za kadego zabitego przeciwnika w obecnym pokoju.\nKociane orbitale zadajc obraenia kontaktowe rwne Obraeniom Isaaca w momencie, gdy zosta wygenerowane, i odpychaj przeciwnikw.\nBony zadaj 8,3 Obrae kontaktowych i 5 Obrae rzucanymi kociami. Black Bony wybuchaj przy mierci, zadajc 40 Obrae.\nBook of the Dead najczciej generuje orbitale, rzadziej Bony i Black Bony, jeszcze rzadziej Bone Fly i Revenanty.\nBook of the Dead generuje orbitale/przeciwnikw z miejsc, w ktrych zginli przeciwnicy (wliczajc przyjacielskich przeciwnikw). Kade takie miejsce ma szans na wywoanie efektu. Uycie przedmiotu ponownie w tym samym pokoju przed wyjciem z niego moe wygenerowa orbitale/przeciwnikw z miejsc, z ktrych nie wygenerowa ich poprzednim razem. Przedmiot nie ma efektu, jeli Isaac wyszed z pokoju i wszed do niego ponownie.\nMa ma szans wypa z The Forsakena.",
  546: "Tworzy piercie wiata wok Isaaca, ktry unieruchamia przeciwnikw, ktrzy znajd si w jego obwodzie.\nZwykli przeciwnicy bd unieruchomieni tak dugo, jak znajduj si w obwodzie piercienia. Bossowie otrzymaj standardowy efekt unieruchomienia, ktry utrzymuje si nawet poza piercieniem, ale znika po pewnym czasie.",
  547: "+1 Bone Heart.\n+0,7 ez.\nWyrzuca Mysterious Paper, jeli zosta odblokowany.",
  548: "Przyzywa chowaca, ktry wystrzeliwuje przez pokj w kierunku, w ktrym Isaac strzela, i wraca do Isaaca, gdy napotka cian, zadaje 7 Obrae przeciwnikom i blokujc ich pociski.\nChowaniec ma efekt przeszywajcy, jeli zabija przeciwnikw, w przeciwnym wypadku odbija si od nich.\nChowaniec podnosi znajdki i przynosi je do Isaaca, podobnie jak The Boomerang, nawet przez przeszkody. Nie podnosi Troll Bomb.",
  549: "+6 pustych Bone Heartw.\nUsuwa wszystkie kontenery Czerwonych Serc i oprnia wszystkie Bone Hearty.\nGdy Isaac straci Bone Hearta (zdobytego z jakiegokolwiek rda; nie wliczajc wydania go na ukad z Diabem), wystrzeli 8 koci dookoa siebie. Koci zadaj 22 + 2x Obraenia Isaaca i rozpadaj si na mniejsze koci, ktre zadaj 1/2 tych obrae. Isaac otrzymuje rwnie +0,5 ez.\nW przypadku The Losta/Tainted Losta i Keepera/Tainted Keepera otrzymaj oni jednorazowe +0,5 ez. Keeper/Tainted Keeper otrzyma rwnie 24 Niebieskie Muszki.\nBonusy do ez utrzymuj si nawet w obszare Knife Piece 2 w Mines II/Ashpit II i wtedy, gdy Isaac pozbdzie si tego przedmiotu (np. na Tainted Isaacu). Uycie Genesis usunie bonusy.\nMa ma szans wypa z The Fraila.",
  55: "Daje Isaacowi szans na wystrzelenie zy z tyu gowy.\nSzansa jest zalena od Szczcia, wynosi 50% przy Szczciu 0 i wzrasta o 10% na kady bonus +1 do Szczcia (100% przy Szczciu 5).",
  550: "Gdy Isaac trzyma ten przedmiot, bdzie atakowany przez nogi Mamy (1-2 na raz) co kilka sekund w kadym pokoju oprcz walki z Mam. Ataki nie niszcz maszyn ani ebraczkw, ale otwieraj Stone Chesty, niszcz przeszkody i Shopkeeperw oraz otwieraj drzwi.\nPrzedmiot ten rwnie powoduje, e drzwi do Boss Rusha otworz si zawsze, niezalenie od czasu, wszystkie zapadnie prowadzcego do nastpnego pitra z Depths II/Necropolis II/Dank Depths II/Mausoleum II/Gehennie II bd zamknite a do przejcia Boss Rusha, po walce z Mam pojawi si tylko The Negative, a po walce z Mom's Heart/It Lives! pojawi si tylko przejcie do Sheolu.\nPrzy uyciu, zatrzymuje ataki Mamy na czas trwania obecnego pokoju, lub na obecn fal w Challenge Roomach i Boss Rushu.\nPo poczeniu z drug czci Broken Shovel tworzy Mom's Shovel.\nBroken Shovel mona zdoby poprzez pokonanie bossa na pierwszym pitrze w czasie mniejszym ni 1 minuta, po czym postawienie bomby w pokoju startowym. The Lamb musi by pokonany przynajmniej raz, eby mona byo uzyska Broken Shovel.",
  551: "Po poczeniu z pierwsz czci Broken Shovel tworzy Mom's Shovel.\nBroken Shovel mona zdoby poprzez przejcie Boss Rusha trzymajc pierwsz czc Broken Shovel.",
  552: "Uycie tego przedmiotu na kopcu ziemi w Grave Roomach w Dark Roomie odblokuje The Forgottena (tylko jeli The Negative zosta wczeniej odblokowany).\nOprcz tego, dziaa identycznie jak We Need to Go Deeper!.\nPrzedmiot mona zdoby poprzez zebranie 2 czci Broken Shovel.",
  553: "Daje 25% szansy na wystrzelenie zarodnika, ktry, oprcz zadawania obrae, przykleja si do przeciwnikw. Po 2,5 sekundy od przyklejenia, zarodnik wybucha, zadajc 5 Obrae i zatruwajc pobliskich przeciwnikw oraz wystrzeliwujc 4 nastpne, samonaprowadzajce zarodniki, ktre maj ten sam efekt.\nPrzedmioty, ktre zastpuj zy, np. Brimstone czy Technology 2, rwnie maj szans na przyklejenie zarodnikw do przeciwnikw mimo braku ez.",
  554: "Nakada efekt strachu na pobliskich przeciwnikw.",
  555: "+5 monet przy podniesieniu.\nPrzy uyciu zabiera Isaacowi 5 monet i daje mu +1,2 Obrae na czas trwania obecnego pokoju.\nEfekt dodaje si z samym sob.",
  556: "Uycie daje Isaacowi efekt Brimstone na czas trwania obecnego pokoju.\nJeli Isaac posiada ju Brimstone lub jeli Sulfur zostanie uyty 2 razy, daje Isaacowi +1,8 Obrae i szerszy Brimstone.\nDalsze uycia zwikszaj obraenia, ale nie szeroko Brimstone'a.",
  557: "Przy uyciu daje Isaacowi wrb, Soul Hearta (19% szansy), kart lub run (13,5% szansy) albo trinket (2,5% szansy).",
  558: "Kiedy Isaac strzela, ma szans (niezalen od Szczcia) na wystrzelenie 1-3 dodatkowy ez w losowych kierunkach.\nZwiksza aur wiata wok Isaac w ciemnoci, np. podczas Curse of Darkness.",
  559: "Przeciwnicy w pobliu Isaaca s raeni prdem. Kade poraenie zadaje 0,75x statystyki Obrae, a maksymalna szybko raenia to 6 razy na sekund.\nKade poraenie moe przenie si na dalszych przeciwnikw, do maksymalnie 4.\nJeli podoga jest mokra, jak np. w Downpourze lub Flooded Caves, zasig raenia jest wikszy.",
  56: "Przy uyciu tworzy ma kau tego luzu, ktry zadaje 8 Obrae na tick (24 na sekund) przeciwnikom, ktrzy w ni wejd.",
  560: "Przy otrzymaniu obrae Isaac wyrzuca z siebie 10 ez we wszystkich kierunkach i dostaje bonus do ez na czas trwania obecnego pokoju.\nzy zadaj 10 Obrae, nie synergizuj si z efektami ez Isaaca i zostawiaj mae kaue krwi.\nPierwsze otrzymane obraenia daj bonus +1,2 ez, kade nastpne +0,4 ez. Bonusy te mog przekroczy normalny limit ez.",
  561: "4x wspczynnik strzaw.\n 0,3x statystyki Obrae.\nzy dostaj losowe efekty trinketw-robakw, jak i niektrych przedmiotw (np. Rubber Cement, My Reflection, Tiny Planet).",
  562: "Powoduje, e statystyki Isaaca nie mog zosta obnione, tylko podwyszone.\nEfekt nie dotyczy zdrowia Isaaca.\nStatystyki nadal obliczane s normalnie, np. podniesienie Soy Milk nie spowoduje obnienia obrae, ale podwyszenie ich bdzie trudne, poniewa nadal bd one obliczane z mnonikiem z Soy Milk.",
  563: "+5 bomb.\nBomby Isaaca zyskuj efekt 0-2 losowych synergii bomb (wykluczajc Mr. Mega, Fast Bombs, Rocket in a Jar i Giga Bomby).",
  564: "+0,5 do statystyki ez.\n+0,2 do statystyki Szybkoci Strzaw.",
  565: "Przyzywa chowaca, ktry ciga przeciwnikw i zadaje im 2 punkty obrae na tick.\nPo zabiciu 15 przeciwnikw, Blood Puppy staje si wcieky. W tej formie zadaje 7,5 Obrae kontaktowych i szaruje na przeciwnikw w prostej linii od niego, wyrzuca 1/2 czerwonego serca po zabiciu 10 przeciwnikw oraz atakuje Isaaca, zadajc mu 1/2 serca obrae. Moe zosta atakowany przez Isaaca, po pokonaniu (ma 20 Zdrowia i 90 Pancerza) wraca do normalnego stanu.\nPo zabiciu kolejnych 25 przeciwnikw, Blood Puppy osiga kolejn form, w ktrej zadaje 15 Obrae kontaktowych na tick, niszczy przeszkody, w ktre zaszaruje, zadaje Isaacowi cae serce obrae, wyrzuca cae serce po zabiciu 10 przeciwnikw, i ma 50 Zdrowia i 90 Pancerza.",
  566: "Daje 1/2 Soul Hearta przy przejciu na nowe pitro.\nPodczas przechodzenia midzy pitrami, koszmar Isaac pokazuje przedmiot, jaki bdzie znajdowa si w Treasure Roomie, oraz bossa.\nPrzedmioty schowane przez Curse of the Blind lub te na alternatywnej ciece rwnie bd pokazane.\nNa pitrach XL, oba przedmioty i bossowie s pokazani.\nW Mausoleum/Gehennie, ktre prowadz do Ascentu, pokazanym bossem bdzie Larry Jr. W Home, sen zostanie pominity.\nW The Void, pokazani zostan wszyscy bossowie na pitrze, zaczynajc od Delirium.\nZamiast zgliczowanych przedmiotw wygenerowanych przez TMTRAINER, pokazane bdzie The Sad Onion.",
  567: "Przyzywa chowaca, ktre daje Isaacowi bonus +0,4 do ez na sekund po przejciu pokoju. Efekt dodaje si do 5 razy, maksymalnie dajc +2 ez na sekund (bonus moe przekroczy limit ez).\nBonus jest resetowany, gdy Isaac otrzyma obraenia. Nie tyczy si to obrae z przejcia do Curse Roomu, Sacrifice Roomu, piguki Bad Trip i przedmiotw zadajcych obraenia, takich jak IV Bag.\nW momencie podniesienia przedmiotu, Isaac od razu otrzymuje pierwszy bonus.",
  568: "Podwjne nacinicie klawiszu strzau stworzy tarcz, ktra bdzie si powoli porusza w wybranym kierunku.\nTarcza odpycha przeciwnikw. Jeli uderz oni w innego przeciwnika, cian lub przeszkod, otrzymuj i zadaj 10 Obrae.\nTarcza odpycha rwnie pociski przeciwnikw, wliczajc lasery Brimstone'a (wliczajc wielki laser The Beast) i promienie wiata. Odbity Brimstone zadaje 7 Obrae na tick (105 na sekund).\nTarcza utrzymuje si przez 1 sekund, a mona jej ponownie uy 3 sekundy od jej zniknicia.",
  569: "Chowaniec, ktry rani Isaaca na pocztku kadego pitra, jeli ma on wicej ni 1/2 czerwonego serca, zabierajc jak najwicej czerwonych serc bez zabijania go. Na reszt pitra, Isaac otrzymuje bonus do Obrae +(0,15 * x<sup>2</sup>) i bonus do Szybkoci +(0,1 * x), gdzie x to ilo straconych serc (maksymalnie +19,8 Obrae i +1,15 Szybkoci przy 12 czerwonych sercach).\nMimo e wizualnie Blood Oath atakuje raz, dla przedmiotw synergizujcymi si z przyjmowanymi obraeniami obraenia licz si jako osobne na kade 1/2 serca.\nAtak moe zosta zatrzymany przez nietykalno (np. z Book of Shadows), ale Isaac nie otrzymuje wtedy bonusw.\nDziaa rwnie na Keeperze i Tainted Keeperze, zostawiajc ich z 1 monet.\nBlooad Oath nie zada obrae Forgottenowi, jeli w momencie ataku postaci aktywn jest The Soul.",
  57: "Przyzywa muszk, ktra orbituje Isaaca i zadaje 5 Obrae kontaktowych na tick (75 na sekund).",
  570: "Kada za Isaaca ma losowy kolor i efekt spord poniszych:\n* brzowa - bez efektu\n* zielona - zatrucie\n* ta - strach\n* niebieska - zmniejszone obraenia (maksymalnie do 1/3)\n* jasnoniebieska - spowolnienie i zamroenie przy zabiciu\n* rowa - oczarowanie\n* czerwona - 2x obrae\n* ciemnoczerwona - zapalenie\n* fioletowa - samonaprowadzanie\n* ciemnopomaraczowa - naznaczenie (efekt Rotten Tomato)",
  571: "+2 Soul Hearty.\n+0,3 Szybkoci.\nIsaac staje si odporny na kolce i luz (ale nie na kolce Curse Roomu ani kamienie z kolcami).",
  572: "+1 Obrae.\n+7,5 Zasigu.\n-0,16 Szybkoci Strzau.\nzy Isaaca nie lec normalnie, ale s kontrolowane w locie przez klawisze strzau.\nMa ma szans wypa z The Adversary.",
  573: "+1 Czerwone Serce.\nLeczy wszystkie czerwone serca Isaaca.\n1,2x mnonik Obrae.\nIsaaca co jaki czas wystrzeli dodatkowe, spektralne zy, ktra bdzie go orbitowa dopki nie upadnie na ziemi, podobnie do efektu Tiny Planet.",
  574: "Daje Isaacowi aur, ktra zadaje obraenia przeciwnikom, ktrzy si w niej znajduj. Aura zadaje tym wicej obrae, im bliej przeciwnicy s do Isaaca, zaczynajc z 0,75 Obrae na brzegach do 4 Obrae przy samym Isaacu. Aura zadaje obraenia do 5 razy na sekund.",
  575: "Daje Isaacowi chowaca, ktry siedzi w jego gowie i wystrzeliwuje 4 spowalniajce zy, ktre zadaj 1,5 Obrae, co 1,5 sekundy.\nW momencie otrzymania obrae istnieje szansa (100% przy 4 Szczcia), e Isaac wygeneruje Niebieski Pajki i uwolni Intrudera, ktry zacznie skaka na przeciwnikw, zadajc im do 4,5 Obrae kontaktowych (zalenie jak szybko si porusza), i wystrzeliwuje 4 spowalniajce zy 2x szybciej ni wtedy, gdy jest w gowie Isaaca.\nPo wyjciu z pokoju chowaniec wraca do gowy Isaaca.",
  576: "Kamienie maj szans by zastpionymi kup.\nZamienia wszystkie Dipy w chowace Isaaca.\nNiszczenie kup generuje 1-4 przyjacielskie Dipy, ktre swobodnie podaj za Isaaciem, blokuj pociski przeciwnikw i zadaj obraenia kontaktowe. Obraenia kontaktowe s rwne Obraeniom Isaaca w momencie, w ktrym Dip by wygenerowany. Dipy mog zada 2 ticki obrae i/lub zablokowa 2 pociski przeciwnikw przed mierci.\nDodatkowe efekty Dipw zale od rodzaju kupy:\nCorn Poop, Dipy generowane przez Brownie i Dangle - Dipy generuj 1 niebiesk muszk przy mierci\n* Red Poop - Dipy zadaj 2x wicej Obrae i szaruj na przeciwnikw, ale gin po 15 sekundach\n* Golden Poop - maj szans na wywoanie efektu Midas' Touch i mog otrzyma/zada obraenia 5 razy\n* Rainbow Poop - Dipy zauraczaj przeciwnikw i maj szans na wyrzucenie 1/2 czerwonego serca przy mierci\n* Black Poop - Dipy zostawiaj za sob spowalniajcy luz\n* White Poop - wywouj efekt Hallowed Ground i mog otrzyma/zada obraenia 3 razy\n* Stone Poop - maj bonus +3,5 do Obrae i mog otrzyma/zada obraenia 6 razy\n* Stinky Poop - Dipy uwalniaj chmury trujcego gazu, ktre utrzymuj si 1,5 sekundy i zadaj 0,5x Obrae Isaaca na tick (ok. 1,2x Obrae Isaaca na sekund), a przy mierci generuj wiksz chmur, ktra utrzymuje si 8 sekund\n* Fire Poop - Dipy podpalaj przeciwnikw (podpalenie zadaje 3,5 Obrae do 6 razy) i zostawiaj za sob ogie po mierci (ktry zadaje 8 Obrae pocztkowo, 6 Obrae po otrzymaniu obrae raz, 4 Obrae po otrzymaniu obrae dwa i trzy razy)\nMa ma szans wypa z Brownie'ego.",
  577: "Przy uyciu przyzywa miecz, ktry bdzie wisia nad gow Isaaca. Dopki miecz wisi nad Isaaciem, dodatkowy przedmiot zostanie wygenerowany obok wszystkich piedestaw oraz przedmiotw wygenerowanych przez maszyny, ebraczkw i Mega Chest. Dodatkowy przedmiot bdzie z puli obecnego pokoju.\nJeli Isaac otrzyma obrae po tym jak przedmiot zostanie aktywowany, miecz moe na niego spa w kadej chwili, natychmiast go zabijajc. Miecz ma 1/10 000 szansy na spadnicie co kade 4 klatki.\nZadanie sobie samemu obrae (przez Blood Donation Machine, Devil Beggara, Curse Roomy, Sacrifice Roomy i aktywne przedmioty) nie liczy si dla miecza jako obraenia i nie aktywuje jego szansy na spadnicie.\nW pokojach, w ktre maj zestaw przedmiotw, z ktrych mona wybra jeden (np. w niektrych Treasure Roomach lub Boss Rushu), dodatkowy zestaw zostanie wygenerowany, z ktrego Isaac moe wybra rwnie tylko jeden.\nUycie kilku kopii przedmiotu nie wygeneruje dodatkowych przedmiotw.\nJedyny sposb na przetrwanie spadnicia miecza to posiadanie nietykalnoci w momencie, w ktrym spada. Po tym, jak miecz spadnie, przedmioty nie bd podwojone.\nStracenie ycia i odrodzenie si nie usuwa miecza.",
  578: "Przy uyciu generuje du kau tego luzu, podobn do tej z piguki Lemon Party. luz zadaje 8 Obrae na tick (24 na sekund) i utrzymuje si ok. 9 sekund.\nMa ma szans wypa z Peepa.",
  579: "zy Isaaca s zastpione przez miecz, ktrym moe atakowa na krtkim dystansie.\nAtak mieczem zadaje 3,5 + 3x Obraenia Isaaca i odrzuca przeciwnika i Isaaca, gdy trafia w przeciwnika. Miecz nie ma ograniczonego tempa atakw.\nAtak mieczem moe podnosi znajdki i otwiera skrzynki.\nJeli zdrowie Isaaca jest rwne lub wiksze od jego liczby kontenerw Czerwonych Serc, miecz wystrzeliwuje pocisk podczas ataku. Pocisk zadaje 2 + Obraenia Isaaca i ma cooldown rwny 2x wspczynnik ez Isaaca.\nPrzytrzymanie klawiszu strzau aduje atak obrotowy. Jest on gotowy, kiedy miecz miga. Atak ten zadaje 10 + 8x Obraenia Isaaca, odbija pociski przeciwnikw oraz wystrzeliwuje pocisk, ktry zadaje 4 + 4x Obraenia Isaaca. Czas adowania ataku jest rwny ok. 4x wspczynnika ez Isaaca.\nSpirit Sword ma najwikszy priorytet ze wszystkich modifykatorw ez w grze, wikszy ni Epic Fetus i Mom's Knife.",
  58: "Przy uyciu generuje tarcz, ktra zapobiega otrzymaniu obraen przez Isaaca przez 10 sekund.\nEfekt koczy si po wyjciu z pokoju.",
  580: "Pozwala Isaacowi na tworzenie dodatkowych, czerwonych pokoi, w miejscach, gdzie normalnie mgby by wygenerowany Secret Room. Moliwo stworzenia pokoju w danym miejscu jest sygnalizowana poprzez kontur czerwonych drzwi.\nCzerwone pokoje s pokojami 1x1 i maj 10% szansy na bycie pokojem specjalnym.\nW Home, Isaac moe stworzy pokj po lewej stronie, pod pokojem Mamy. Pokj ten zawiera splamione postacie, lub, jeli dana posta zostaa ju odblokowana, Inner Child (jeli przedmiot ten zosta odblokowany) albo Shopkeepera.\nJeli Isaac otworzy drzwi z innego czerwonego pokoju poza obrbem pitra (poza obszar 13x13), drzwi bd prowadzi do pokoju I AM ERROR (z ktrego nie bdzie wyjcia). W Corpse II, Chest, Dark Roomie, The Void, Mausoleum/Gehennie z Dad's Note oraz wyzwaniem Red Redemption nie da si utworzy tego pokoju, poza obrbem 13x13 nie bd pokazane adne kontury.\nCzerwone Sklepy mog oferowa przedmioty z puli Boss Roomu lub Treasure Roomu. Mog te by lepszymi sklepami Tainted Keepera.\nNie mona utworzy czerwonego pokoju obok Boss Roomu, oprcz w The Void, Lustrzanym Odbiciu, oraz ???, gdzie czerwony pokj moe zosta wygenerowany w miejscu, gdzie normalnie jest przejcie do The Void. Pokj ten moe zosta stworzony tylko z innego czerwonego pokoju, ale nie z pokoju Husha, z ktrego drzwi bd normalnie prowadziy do przejcia do the Void.\nRed Key pozwala na dotarcie do Ultra Secret Roomu. Ultra Secret Room nigdy nie bdzie odlegy od normalnej mapy o wicej ni 1 czerwony pokj. Kiedy Isaac wejdzie do czerwonego pokoju, ktry ssiaduje z Ultra Secret Roomem, przejcie do niego zostanie automatycznie otwarte.\nCzerwone Sacrifice Roomy maj osobny licznik uy ni Sacrifice Room w obrbie pitra.\nCzerwone pokoje nie mog zosta stworzone wok obszaru z Knife Piece 2 w Mines II/Ashpit II.\nCzerwone pokoje w Rozdziale 3 mog zawiera Marked Skull, te w Rozdziale 2,5 mog zawiera te przyciski (nacinicie ich nie ma jednak efektu), natomiast te w Chest/Dark Roomie mog by startowymi pokojami z tych piter (Czerwone Skrzynki w czerwonym pokoju w Dark Roomie nie bd zawiera ukadw z Diabem, ale bd normalnymi Czerwonymi Skrzynkami).",
  581: "Przyzywa muszk, ktra zadaje 1 Obrae kontaktowych na tick (15 na sekund) oraz unieszkodliwia pociski przeciwnikw, ktre lec w kierunku Isaaca, zamieniajc je w samonaprowadzajce zy zadajce 2 Obrae. Po zablokowaniu pewnej liczby pociskw muszka przez chwil blokuje pociski, ale ich nie ciga.\nZamienia niektrych przeciwnikw-muchy (Army Fly, Attack Fly, Fly, Ring Fly, Swarm Fly, Willo) w Niebieskie Muszki przy dotkniciu ich.",
  582: "Przy uyciu daje Isaacowi +0,75 ez (nawet poza limit) i -0,03 Szybkoci oraz znieksztaca obraz i muzyk. Efekt dodaje si.\nPrzy wyjciu z pokoju, bonus do ez otrzymany w tym pokoju jest mnoony przez 0,4, a minus do Szybkoci jest podwajany.\nPrzejcie pokoju bez uycia przedmiotu usuwa efekt jednego uycia, dajc +0,06 Szybkoci i -0,3 ez.\nPocztkowy czas adowania to 0,5 sekund, czas ten zwiksza si do 15 sekund po 9-tym uyciu. Po 10 uyciach przestaje adowa si automatycznie.",
  583: "+5 bomb.\nJeli podczas stawiania bomby Isaac bdzie strzela, zamiast zwykej bomby wybuchowa rakieta zostanie wystrzelona w kierunku strzelania. W przeciwnym wypadku, bomba zostanie postawiona normalnie.",
  584: "Przy uyciu generuje ognika, ktry orbituje Isaaca i strzela spektralnymi zami i ktry moe zosta zniszczony przez przeciwnikw i ich pociski. Zwyky ognik ma 2 Zdrowia oraz zadaje 1,6-2,4 Obrae i orbituje Isaaca w rodkowym piercieniu.\nWiele ognikw moe orbitowa Isaaca. Ogniki dziel si na 3 piercienie. Wewntrzy ma limit 6 ognikw, rodkowy - 8 ognikw, a zewntrzny - 12 ognikw. Przyzwanie ognika ponad limit zastpi najbardziej uszkodzonego ognika.\nPo podniesieniu innego przedmiotu aktywnego, poczy si on z Book of Virtues. Isaac bdzie mg go normalnie uywa, a Book of Virtues bdzie generowa ogniki ze specjalnymi efektami, w zalenoci od typu drugiego przedmiotu. Efekt ten dziaa nawet na aktywne przedmioty w miejscu na kart/piguk.\nPasywnie gwarantuje, e pierwszy Devil Room/Angel Room bdzie Angel Roomem oraz pozwala Angel Roomw pojawi si, nawet jeli Isaac wzi ju ukad z Diabem.\nBook of Virtues traktowany jest jako przedmiot pasywny przez gr. Zajmie on 1 miejsce na przedmiot Tainted Isaaca, NO! nie bdzie na niego oddziaywa, a Void zaabsorbuje go jako przedmiot pasywny. Jest to take jedyny pasywny przedmiot z puli Library, co oznacza, e nie moe zosta przelosowany np. przez D4, jeli zosta wzity z tej puli.\nPrzedmiot startowy Bethany, nawet jeli nie zosta jeszcze odblokowany.",
  585: "Alabaster Box zaczyna z 0 adunkami i adowany jest przez Soul i Black Hearty. Kada powka serca daje mu 1 adunek. Serca te niedodawane s do zdrowia Isaaca.\nPrzy uyciu przyzywa 3 Soul Hearty i 2 przedmioty z puli anielskich pokoi, lub, jeli poprzednio zosta wzity ukad z Diabem, 2 Soul Hearty i 1 przedmiot. Serca mog by rwnie Eternal Heartami.",
  586: "W pokoju startowym kadego pitra generowana jest drabina, ktra prowadzi do specjalnego Angel Roomu, ktry zawiera statu Anioa, Beggara, Konfesjona (jeli zosta odblokowany) oraz 3 przedmioty/znajdki na sprzeda. Moliwe przedmioty/znajdki:\n* przedmiot z puli Angel Roomw za 15 monet\n* Eternal Heart za 5 monet\n* Soul Heart za 5 monet\n* klucz za 5 monet\n* Holy Card za 5 monet\nDrabina znika po wyjciu z Angel Roomu lub z pokoju startowego.\nPrzedmioty/znajdki mog by przecenione. Steam Sale, Credit Card i Coupon dziaaj jak na przedmioty w sklepie, natomiast Restock oraz Store Credit nie dziaaj.",
  588: "Pokazuje pooenie Boss Roomu na mapie (jednego losowego, jeli na pitrze jest wicej ni 1 Boss Room).\nPo zabiciu pokazanego bossa, aktywuje efekt karty The Sun, daje Isaacowi +3 Obrae i +1 Szczcia na czas trwania pitra, aduje przedmiot aktywny i usuwa wszystkie kltwy.\nBonusy zostan zaaplikowane na kadym pitrze w Ascencie, ale karta The Sun nie zostanie aktywowana. W Home, efekty aktywuj si po przespaniu si w ku mamy lub pokonaniu pierwszej fazy Dogmy.\nEfekty Sol utrzymuj si z poprzedniego pitra do pitra ???.\nPrzy pokonaniu wielosegmentowego bossa (np. Chuba, Carrion Queen, itp.), efekt aktywuje si raz na segment, dajc bonus +9 Obrae i +3 Szczcia, ktry jednak zmniejszy si do normalnego bonusu po wyjciu z pokoju.\nEfekt nie aktywuje si po pokonaniu bossa w Lustrzanym Odbiciu.",
  589: "Dodaje dodatkowy Secret Room oraz Super Secret Room do kadego nastpnego pitra.\nPokazuje na mapie losowy (Super) Secret Room przy podniesieniu i na kadym nastpnym pitrze.\nKady jeszcze nieodwiedzony (Super) Secret Room bdzie zawiera promie wiata, wejcie w ktry da Isaacowi bonus do ez (+1 w pierwszy promie, +0,5 w kady nastpny; bonus moe przekroczy normalny limit ez) oraz 1/2 Soul Hearta. Isaac otrzymuje rwnie lekk niebiesk aur, ktra moe pomc przy Curse of Darkness lub ciemnych pokojach w Mines/Ashpit.\nW Greed Modzie nie zostanie dodany dodatkowy Secret Room, ale istniejce Secret Roomy bd zawieray promie wiata.\nBonus do ez utrzymuje si nawet w obszare Knife Piece 2 w Mines II/Ashpit II.",
  590: "+0,4 Szybkoci.\nWikszo drzwi pozostaje otwartych nawet w pokojach z przeciwnikami.\nEfekt nie dziaa na drzwi do pokojw specjalnych (wliczajc pokoje z mini-bossami), nawet jeli zostay wczeniej otwarte, na drzwiach do Boss Rusha oraz na drzwiach do czerwonych pokoi, jeli zostay one stworzone w obecnym pokoju z przeciwnikami.",
  591: "+1 Czerwone Serce.\nLeczy dodatkowe 1 czerwone serce.\nZauracza przeciwnikw blisko Isaaca. Efekt zanika, gdy si oni oddalaj.",
  592: "+1 Obrae.\nzy Isaaca zamieniaj si w kamienie, ktre zadaj losowo od 0,5x do 2x Obrae Isaaca.\nKamienie maj szans na zniszczenie przeszkd, otwarcie przej do Secret Roomw lub drzwi zamknitych podczas przechodzenia pokoju.\nKamienie mog by rwnie uyte do zaatania dziur w ziemi.",
  593: "Podwjne nacinicie na klawisz kierunku spowoduje, e Isaac zaszaruje w tym kierunku.\nPodczas szary Isaac jest nietykalny, nie liczc Blood Donation Machine, Devil Beggara i przedmiotw aktywnych, ktre zadaj mu obraenia.\nSzara zadaje 8 + 4x Obraenia Isaaca.\nSzara jest w stanie zniszczy Shopkeeperw, a po uderzeniu w przeszkody lub przeciwnikw generuje krg ognia, ktry podpala przeciwnikw, i wywouje efekt odrzutu.\nPo szary, Isaac bdzie nietykalny jeszcze przez 1 sekund.\nNastpna szara moe zosta wykonana po ok. 3 sekundach. Zostanie to zasygnalizowanie przez to, e Isaac zamiga, a z jego nozdrzy uniesie si para.",
  594: "+2 kontenery Czerwonych Serc.\n-0,3 Szybkoci.\nStanie w miejscu powoduje, e Isaac dostanie bonus do Szybkoci +0,25 na sekund, maksymalnie +0,5.\nPo poruszeniu si, Isaac bdzie zostawia za sob chmury gazu, zadajce 0,5x swoje Obraenia na tick (1,5x na sekund) i utrzymujce si ok. 2,5 sekund oraz zatruwajce przeciwnikw. Poruszanie si zabiera Isaacowi bonus do szybkoci; gdy si on skoczy, Isaac skoczy wytwarza chmury gazu.\nIsaac otrzymuje odporno na trujce chmury.",
  595: "Przy wejciu do pokoju, Isaac otrzymuje do 7 ez, ktre posiadaj jego efekty ez i bd go orbitowa. Po 13 sekundach zy spadaj na ziemi. zy zadaj 5 + 1,5x Obraenia Isaaca.\nPokj nie musi by nieodwiedzony, eby wygenerowa zy.\nPociski przeciwnikw maj szans na bycie zapanymi przez orbit, stajc si zami Isaaca. zy te utrzymuj si na czas trwania obecnego pokoju.",
  596: "zy Isaaca zamieniaj si w sople lodu, ktre zamraaj przeciwnikw przy ich zabijaniu (bossy i mini-bossy nie mog zosta zamroeni).\nZamroeni przeciwnicy mog by popchnici. Bd si oni wtedy lizga po pokoju, zostawiajc luz (ktry zadaje 2 Obrae przez 11 tickw w czasie 1 sekundy), zanim nie uderz ciany, przeszkody lub innego przeciwnika. Uderzenie w przeciwnika zadaje mu 35 Obrae.\nZamroeni przeciwnicy mog take zosta rozbici przez 10 pociskw lub wybuch.\nPrzy rozbiciu, zamroeni przeciwnicy roztrzaskiwuj si i wystrzeliwuj 10 sopli, ktre zadaj obraenia rwne 7 + x, gdzie x = numer obecnego pitra. Sople mog spowolni przeciwnikw na 4 sekundy lub zamrozi, jeli ich zabij.\nZabicie przeciwnika przez zamroenie zapobiega wszelkim efektom towarzyszcym ich mierci, np. rozdwojenie si, wystrzelenie pociskw.\nZamroeni przeciwnicy s uwaani przez gr za zabitych i nie musz zosta roztrzaskani, aby przej pokj. Znikaj po wyjciu z pokoju.",
  597: "Kiedy Isaac nie strzela, zacznie adowa zy. Wcinicie klawiszu strzau spowoduje, e wystrzeli zgromadzone zy w krtkim czasie.\nCzas adowania i liczba zgromadzonych ez na adunek zaley od statystyki ez Isaaca.",
  598: "+0,7 ez.\nZmniejsza rozmiar Isaaca, pozwalajc mu na przecinicie si midzy obiektami, a pociski przeciwnikw mog nad nim przelecie.",
  599: "Generuje dodatkowy Curse Room na kadym pitrze oraz powoduje, e Curse Roomy daj lepsze nagrody.\nCurse Roomy zawsze bd zawieray 1 centa.\nNie ma efektu na Greed Modzie.",
  6: "+1,5 ez.\n-1,5 Zasigu i -20% mnonik Zasigu.\n+0,76 Wysokoci ez.\nzy s wystrzeliwane z dolnej czci ciaa Isaaca zamiast z gowy.",
  60: "Pozwala Isaacowi na pokonywanie przepaci o szerokoci 1 pytki, umieszczajc na nich drabin.\nPrzeciwnicy nie mog chodzi po drabinie.",
  600: "Ok. -28% Opnienia ez wystrzeliwanych z lewego oka Isaaca.",
  601: "+0,7 do statystyki ez.\n+1 Eternal Heart.\nPowoduje, e anielskie pokoje mog si pojawi mimo wczeniejszego wzicia ukadu z Diabem.",
  602: "W kadym sklepie zostanie wygenerowana zapadnia do specjalnego sklepu, ktry sprzedaje 2-5 z nastpujcych artykuw:\n* trinket za 15 monet\n* przedmiot z puli Secret Roomu, Sklepu, Devil Roomu, Angel Roomu lub Boss Roomu; przedmioty z Secret Roomu kosztuj do 99 monet, z Devil/Angel Roomu do 50 monet, z pozostaych pul 15-30 monet\n* karta/runa/Soul Stone za 6-8 monet\n* Black Heart/Bone Heart za 8 monet\n* Eternal Heart za 15 monet\n* Rotten Heart za 5 monet\nZapadnia jest generowana nawet jeli Sklep zosta zastpiony przez Greeda lub Super Greeda, ale otworzy si dopiero po pokonaniu mini-bossa.\nJeli na pitrze zostanie wygenerowana wicej ni 1 zapadnia (np. w czerwonych Sklepach), bdzie ona prowadzi do tego samego sekretnego sklepu. Tyczy si to rwnie sklepu w Lustrzanym Odbiciu.\nJeli Member Card zostanie usunita z ekwipunku Isaaca (np. grajc jako Tainted Isaac), ju wygenerowana zapadnia pozostanie otwarta.",
  603: "Przy podniesieniu w peni aduje aktywny przedmioti wyrzuca 2-4 baterie.",
  604: "Przy uyciu podnosi przeszkod znajdujc si przed Isaaciem. Moe by ona rzucona w przeciwnikw, zadajc 40 Obrae i niszczc si przy kontakcie z nimi, wyrzucajc swoj zawarto.\nPrzeszkody mog zosta przenoszone midzy pokojami.\nUycie przedmiotu na Hocie zdejmie z niego skorup i zamieni go w Red Hosta. Skorupa moe zosta rzucona.\nIsaac moe rwnie rzuca postawionymi bombami lub Troll Bombami, przyjacielskimi Dipami wygenerowanymi przez Dirty Mind oraz Cube Baby.\nKamienie z kolcami nie mog zosta podniesione, nawet jeli ich kolce si schoway.\nPonowne uycie przedmiotu zrzuci przeszkod w miejscu, w ktrym Isaac stoi, bez zuycia adunku. Kiedy przeszkoda spada, moe zosta ponownie podniesiona.",
  605: "Przyzywa na czas obecnego pokoju chowaca identycznego do tego z przedmiotu The Peeper.\nChowaniec blokuje pociski przeciwnikw, zadaje 12 Obrae kontaktowych na tick oraz zostawia za sob czerwony luz, ktry zadaje 2 obraenia kontaktowe na tick.\nzy wystrzeliwane z prawego oka Isaaca zadaj 34% wicej obrae.",
  606: "Daje 5% szansy na wystrzelenie zy, ktra utworzy szczelin w miejscu, w ktrym spadnie. Szczelina rani pobliskich przeciwnikw oraz przyciga przeciwnikw, znajdki i pociski przez 2 sekundy.\nSzczelina zadaje 0,5x Obraenia Isaaca 2 razy na tick (3x Obraenia Isaaca na sekund), a pociski przeciwnikw wcignite do szczeliny zadaj obraenia przeciwnikom.\nWielko szczelin zaley od obrae Isaaca.",
  607: "Przyzywa chowaca, ktry, w pokoju z przeciwnikami, co chwil wyrzuca z siebie we wszystkich kierunkach grup ez. Kada za zadaje 3,5 Obrae lub 5,3 Obrae.",
  608: "Przyzywa chowaca, ktry co 2/3 sekundy strzela soplami lodu, ktre maj szans na unieruchomienie przeciwnikw, w ktrych trafi, i zamroenie przeciwnikw, ktrych zabij.\nSople zadaj 3,5 Obrae.\nZamroeni przeciwnicy bd blokowa pociski przeciwnikw i lizga si po dotkniciu przez Isaaca. Jeli trafi oni w przeszkod lub Isaac strzeli w nich odpowiedni liczb razy, eksploduj oni w piercie 10 lodowych ez, ktre mog spowolni lub zamrozi innych przeciwnikw.",
  609: "Przy uyciu zmienia kady przedmiot w pokoju na przedmiot z puli obecnego pokoju, ale kady przedmiot ma 30% szansy na zniknicie.\nPrzedmiot startowy The Losta, nawet jeli nie zosta jeszcze odblokowany.\nMa szans wypa z walki z Ultra Pride'em, jeli Florian (duszek) zostanie zabity jako drugi.\nStartowy przedmiot The Losta.",
  610: "Przyzywa chowaca, ktry w momencie otrzymania przez Isaaca obrae lduje na pobliskim przeciwniku, zadajc 45 obrae oraz wytwarzajc skaln fal, ktra zadaje 10 obrae. Przez reszt pokoju zachowuje si jak Dead Bird, zadajc przeciwnikom 3 Obrae kontaktowych na tick.\nMa ma szans wypa z The Cage'a.",
  611: "Przy uyciu Isaac wydaje z siebie krzyk, zadajc obraenia i odpychajc pobliskich przeciwnikw.\nPrzedmiot moe zosta uyty, kiedy nie jest w peni naadowany, ale obraenia i zasig krzyku zale od liczby adunkw, jakie ma przy uyciu. Obraenia to 20 + 28 * x, gdzie x = liczba zuytych adunkw.\nKrzyk zadaje dodatkowe obraenia przeciwnikom znajdujcym si blisko Isaaca - 2,5 + 3,5 * x, gdzie x = liczba zuytych adunkw. Liczba tickw tych obrae to maksymalnie 1/2 liczby zuytych adunkw, zaokrglajc do gry.\nKrzyk moe te zniszczy kupy i ogniska oraz otworzy drzwi, ktre normalnie mona otworzy bomb, wliczajc przejcia do Secret Roomw.\nPrzedmiot zyskuje 1 adunek, kiedy Isaac otrzymuje obraenia.",
  612: "Przyzywa chowaca, ktry poda blisko za Isaaciem. Jeli chowaniec otrzyma obraenia, umiera i odradza si na kolejnym pitrze.\nJeli chowaniec przeyje do pocztku nastpnego pitra, generuje on 3 Soul Hearty, 2 Eternal Hearty lub przedmiot z puli Treasure Roomu lub Angel Roomu.\nMa ma szans wypa z Blighted Ovum.",
  614: "+1 Czerwone Serce.\nLeczy 5 czerwonych serc przy podniesieniu.\nBomby postawione przez Isaaca zostawiaj czerwony luz, ktry utrzymuje si przez ok. 15 sekund i zadaje 2 Obrae na tick.\nJeli Isaac nie ma bomb, pozwala na postawienie bomby kosztem 1/2 serca. Czerwone serca maj priorytet przez innymi sercami. Stracenie zdrowia tym sposobem nie zmniejsza szansy na Devil/Angel Room.",
  615: "Przyzywa chowaca, ktry poda za Isaaciem. W momencie, gdy Isaac otrzymaby obraenia, chowaniec puszcza bka, ktry odpycha pobliskich przeciwnikw i strzay. Odepchani przeciwnicy, ktrzy uderz w przeszkod lub cian, otrzymuj obraenia.\nPo ochronieniu Isaaca, Lil Dumpy staje si nieaktywny i zasypia na kilka sekund.\nEfekt moe take zosta wywoany, gdy pocisk przeciwnika zbliy si na pewn odlego do Lil Dumpy'ego.",
  616: "Daje Isaacowi szans na wystrzelenie ogni obok normalnych ez.\nSzansa obliczana jest wg wzoru 1/(12 - Szczcie), maksymalnie 50% przy Szczciu 10.\nWielko ogni zaley od obrae Isaaca.\nOgnie blokuj do 4 strzaw przeciwnikw i zadaj do 4 tickw obrae przed zganiciem, i trwaj do 10 sekund.\nOgnie zadaj 4x Obraenia Isaaca w penej formie, 3x po otrzymaniu obrae raz i 2x w innym wypadku.\nOgnie s w stanie zniszczy Shopkeeperw.\nMa ma szans wypa z Singe'a.",
  617: "Daje Isaacowi szans na wystrzelenie magnetyzujcej zy. Namagnetyzowani przeciwnicy przycigaj znajdki, przeciwnikw i pociski, rwnie innych przeciwnikw (ktre zadaj im obraenia).\nSzansa jest zalena od Szczcia i wynosi 100% przy Szczciu 10.",
  618: "Daje szans na wystrzelenie zy, ktra naznaczy przeciwnika. Naznaczeni przeciwnicy maj zmniejszon szybko poruszania i bd atakowani przez innych przeciwnikw. Przeciwnicy ignoruj wtedy Isaaca. Efekt po chwili wygasa.\nSzansa na wystrzelenie naznaczajcej zy wynosi 1/(6 - Szczcie).",
  619: 'Efekt i opis zaleny od postaci.\n* Isaac: "More options" - wszystkie nowe piedestay przeczaj si midzy 2 opcjami przedmiotw\n* Magdalene: "Limit breaker + HP up" - +1 Czerwone Serce, zwiksza limit kontenerw serc do 18\n* Cain: "Better arcades + luck up" - +1 Szczcia; wszystkie pitra po Rozdziale 2 (oprcz ???, Chest, Dark Roomu, The Void i Home) bd zawieray ulepszony Arcade\'y, z wiksz liczb maszyn do gier\n* Judas: "Belial incarnate" - The Book of Belial staje si przedmiotem pasywnym, podobnie do Book of Virtues, dajc Judasowi bonus do Obrae zaleny od iloci adunkw przedmiotu aktywnego, ktry posiada; jeli Judas obecnie nie posiada The Book of Belial, otrzymuje go\n* ???: "Stronger spirit" - Soul Hearty otrzymane z health-upw s podwojone; piguka Health Down zabiera 2 Soul Hearty\n* Eve: "Forever cursed" - efekt Whore of Babylon i Dead Bird s aktywne bez wzgldu na zdrowie czy otrzymane obraenia\n* Samson: "Rage up" - Bloody Lust moe da 4 dodatkowe bonusy do Obrae w odstpach +0,2 (maksymalnie +14)\n* Azazel: "Wide breath" - Brimstone Azazela staje si szeroki jak Mega Blast (obraenia si nie zmieniaj)\n* Lazarus: "Come back stronger" - daje efekt Birthright po odrodzeniu jako Lazarus Risen\n* Lazarus Risen: "Temporary DMG up" - bonus +21,6 do Obrae, ktry zanika w tempie 0,36 na sekund; zabicie przeciwnika dodaje 0,07 Obrae do bonusu, jeli jest on jeszcze aktywny\n* Eden: "???" - generuje 3 przedmioty z 3 losowych pul; tylko 1 moe zosta wzity\n* The Lost: "Better destiny" - usuwa z pul przedmioty dajce zdrowie, latanie, zy spektralne lub aktywujce si przy otrzymaniu obrae\n* Lilith: "Offensive formation" - wikszo chowacw, wliczajc Incubusa Lilith, bd ustawia si przed Lilith i zwraca si w kierunku, w ktrym strzela\n* Keeper: "Coin up" - zwiksza limit monet-serc Keepera o 1\n* Apollyon: "Regurgitate" - uycie Void ma szans na wyrzucenie przedmiotu, ktry poprzednio zosta nim wessany (kady tylko raz, chyba e zostanie ponownie wessany); szansa jest tym wiksza, im wicej przedmiotw zostao wessanych\n* The Forgotten: "Unchained" - The Soul porusza si niezalenie od Forgottena\n* Bethany: "Conserve your faith" - aktywowanie przedmiotu przy uyciu Soul Heartw czasami jest darmowe\n* Jacob & Esau: "What\'s yours is mine" - posta, ktra podniesie Birthright, otrzyma kopie 3 pasywnych przedmiotw drugiej postaci\n* Tainted Isaac: "Inventory up" - dodaje 4 miejsca na pasywne przedmioty; samo Birthright nie zuywa miejsca\n* Tainted Magdalene: "HP up!" - dodaje 1 stae czerwone serce\n* Tainted Cain: "Salvage" - liczba znajdziek generowanych przez podniesienie przedmiotw jest podwojona\n* Tainted Judas: "Extended darkness" - zwiksza zasig uderzania przeciwnikw i ez przez Dark Arts\n* Tainted ???: "Poop up" - maksymalna liczba kup jest zwikszona do 29\n* Tainted Eve: "Coagulate" - krwiaczki z Czerwonych Serc generuj tymczasowe 1/2 Czerwonych Serc przy mierci; serca te znikaj po 2 sekundach\n* Tainted Samson: "Unstoppable force" - zabijanie przeciwnikw w stanie berserkera przedua go o 3 sekundy, a nie 1\n* Tainted Azazel: "Stronger sneeze" - podwaja rozmiar kichnicia z Hemoptysis\n* Tainted Lazarus: "Superposition" - nieaktywna forma Tainted Lazarusa pojawi si jako duch obok aktywnej formy, podobnie do Jacoba i Esau; duch jest odporny na wszystkie obraenia i zadaje obraenia zmniejszone do 20%, podczas gdy aktywna forma zadaje obraenia zmniejszone do 80%\n* Tainted Eden: "Eternal" - przedmioty wzite przed wziciem Birthright nie mog zosta przelosowane (przez otrzymanie obrae ani przez D4 czy Missing No.), a bazowe statystyki zostaj zablokowane do takich, jakie byy w momencie podniesienia Birthright.\n* Tainted Lost: "Extra life" - dodatkowe ycie; przy mierci Tainted Lost zostanie odrodzony w tym samym pokoju, zadajc przeciwnikom 200 obrae\n* Tainted Lilith: "Conjoined" - chowace Lilith zaczn zachowywa si jak jej Gello, chowajc si w niej, gdy nie atakuje; atak Gello dostaje bonus +3 do Obrae za kadego chowaca; chowace automatyczne strzelaj do przeciwnikw\n* Tainted Keeper: "Money money money" - przyciga monety wyrzucone z zabitych przeciwnikw\n* Tainted Apollyon: "Torment" - przy przytrzymaniu klawiszu strzau, szaracze Tainted Apollyona bd cay czas atakowa przeciwnikw, zamiast do niego wraca\n* Tainted Forgotten: "Recall" - Tainted Soul otrzymuje aktywny przedmiot Recall, ktry umoliwia przycignicie Forgottena z daleka\n* Tainted Bethany: "Artifact" - generuje 2 ogniki o jakoci 3 i 2 ogniki o jakoci 4, majce znacznie wicej HP ni inne ogniki\n* Tainted Jacob: "It\'s not yours" - Dark Esau dzieli si na p; oboje bd atakowa w tym samym czasie, a jeden nie zaatakuje, jeli drugi nie jest w odpowiedniej pozycji; uycie Anima Sola uwizi obu Esau w jednym miejscu, a jej kolejne uycie bd ich uwalnia po kolei',
  62: "+0,3 Obrae.\nLeczy 1/2 serca Isaaca za kadych 13 zabitych przeciwnikw.\nEfekt dziaa na Keeperze i Tainted Keeperze.",
  621: "Przy podniesieniu leczy wszystkie czerwone serca Isaaca oraz daje mu bonus +21,6 Obrae, ktry powoli spada do 0 w przecigu 3 minut.\nGdy bonus jest aktywny, Isaac otrzymuje may bonus do Obrae za kadego zabitego przeciwnika.",
  622: "Przy uyciu posta zostaje zresetowana, wliczajc przedmioty i statystyki, i zostaje przeteleportowana do specjalnego pokoju, ktry zawiera ko, dwie losowe skrzynki, dwie sakiewki i losowy trinket. Za kady przedmiot usunity po uyciu Genesis, Isaac bdzie mia do wyboru 1 z 3 przedmiotw z puli tego przedmiotu. Jedynym wyjciem z pokoju jest promie wiata, ktry zabierze Isaaca na nastpne pitro.\nResetowanie postaci obejmuje powrt do oryginalnej, jeli zostaa ona zmieniona, np. przez odrodzenie si po zebraniu Ankh lub Lazarus' Rags.\nPo uyciu na jednym z ostatnim piter (Chest, Dark Room, The Void), pokj, zamiast promienia wiata, bdzie zawiera Crawl Space, ktry zabierze Isaaca do pokoju startowego obecnego pitra. W Chest i Dark Room skrzynki bd zawieray przedmioty.\nUycie po pokonaniu Mom's Heart w Mausoleum II/Gehennie II teleportuje Isaaca do pustego pokoju zawierajcego przejcie do Corpse i wybr 3 przedmiotw.\nUycie podczas walki z The Beast teleportuje Isaaca na rodek pokoju i nie bdzie miao innych efektw.\nJacob & Esau nie mog podnie 2 przedmiotw na raz.\nTainted Bethany straci swoje ogniki, ale nie adunki z czerwonych serc. Bethany nie straci adunkw z Soul Heartw.\nPo uyciu jako Eden, przelosuje to jego startowe statystyki, przedmioty oraz wygld.\nUycie Pandora's Box w pokoju Genesis wygeneruje The Bible.\nUycie Genesis nie usuwa przedmiotw fabularnych. Nie usuwa rwnie efektu zastpowania innych przedmiotw przez Magic Skin.\nUycie Genesis po zebraniu Eden's Blessing zuyje go i da Isaacowi przedmiot.",
  623: "+5 kluczy przy pierwszym podniesieniu.\nPrzy uyciu, Isaac bdzie trzyma nad gow klucz, ktry rzuci w danym kierunku po naciniciu klawiszu strzau. Klucz jest przeszywajcym pociskiem, ktry zadaje 30 + 5x Obraenia Isaaca i dostaje efekty ez Isaaca. Rzucenie kluczem kosztuje 1 klucz (nawet jeli Isaac ma Golden Key).\nRzucony klucz otwiera drzwi (wliczajc drzwi do Mom's Heart w Mausoleum/Gehennie, drzwi do Mega Szatana, drzwi do przejcia do Mines/Ashpit, drzwi do przejcia do Mausoleum/Gehenny, oraz Strange Door) i skrzynki, w ktre wleci, oraz niszczy kamienie. Jeden klucz moe otworzy drzwi, ktre wymagaj 2 kluczy, oraz kilka skrzynek na raz. Otwarcie skrzynek w Challenge Roomach nie wywoa fali przeciwnikw.",
  624: "Generuje 5 losowych kart.",
  625: "Przy uyciu zmienia Isaaca w giganta na 30 sekund, co daje mu nastpujce efekty:\n* 4x Obraenia\n* ok. +7 Zasigu\n* 2,5x Opnienia ez\n* nietykalno\n* niszczenie przeszkd (wliczajc metalowe bloki) oraz otwieranie drzwi przez wchodzenie w nie\n* 80 Obrae kontaktowych na tick (1600 na sekund)\n* zwiksza hitbox Isaaca ok. 2 razy\nUycie Mega Mush ponownie, gdy efekt jest aktywny, dodaje 30 sekund do trwajcego efektu.",
  626: "W poczeniu z Knife Piece 2 tworzy n, ktry wystrzeliwuje w kierunku, w ktrym Isaac strzela, zadajc 25 Obrae.\nN moe by uyty na czerwonych drzwiach w Boss Roomie w Mausoleum II/Gehennie II, ktre umoliwiaj przejcie do Corpse. Otwarcie tych drzwi niszczy n.\nPrzedmiot mona zdoby w Lustrzanym Odbiciu w Downpour II/Dross II.\nPodniesienie Knife Piece 1 umoliwia pniejsze zdobycie Knife Piece 2.",
  627: "W poczeniu z Knife Piece 1 tworzy n, ktry wystrzeliwuje w kierunku, w ktrym Isaac strzela, zadajc 25 Obrae.\nN moe by uyty na czerwonych drzwiach w Boss Roomie w Mausoleum II/Gehennie II, ktre umoliwiaj przejcie do Corpse. Otwarcie tych drzwi niszczy n.\nPrzedmiot mona zdoby w Mines II/Ashpit II, jeli podniesiony zosta Knife Piece 1, w pokoju z wagonikiem.",
  628: "Przy uyciu teleportuje Isaaca do specjalnego obszaru, ktry zawiera kady odblokowany przedmiot w grze (oprcz Dad's Note, Recall, Hold i samego Death Certificate).\nPodniesienie jakiegokolwiek przedmiotu zniszczy wszystkie inne przedmioty w pokoju i wyteleportuje Isaaca z powrotem do pokoju, w ktrym uy Death Certificate.\nSpecjalny obszar zawiera rwnie znajdki i skrzynki. Jeli skrzynka bdzie zawieraa przedmiot, podniesienie go nie spowoduje wyteleportowanie Isaaca.\nPrzedmioty w obszarze Death Certificate s ustawione w kolejnoci ich ID.\nJeli Isaac uyje Death Certificate ponownie podczas tej samej rozgrywki, zostanie przeniesiony do tego samego obszaru.\nJeli Isaac zostanie przeniesiony do Devil Roomu lub Angel Roomu, po wyjciu z niego powrci do obszaru Death Certificate.",
  629: "Przyzywa chowaca, ktry orbituje Isaaca, zadajc obraenia kontaktowe i strzelajc zami z tarcz (podobnie do efektu Lost Contact) w nadchodzce pociski przeciwnikw.\nIsaac moe posiada maksymalnie 5 kopii Bot Fly.",
  63: "Aktywne przedmioty mog zosta naadowane 2 razy. Dodatkowe adunki s pokazane na to.",
  631: "Przy uyciu dzieli wszystkich przeciwnikw w pokoju na dwch mniejszych, kady z ok. 40% ich oryginalnego zdrowia.\nJeli przeciwnik dzieli si normalnie jako cz jego mechaniki dziaania (np. Teratoma lub The Fallen), Meat Clever zadaje mu tyle obrae, ile wymaganych jest do rozdzielenia go.\nJeli przeciwnik ma segmenty (np. Larry Jr.), Meat Clever zmniejszy liczb segmentw do poowy, zaokrglajc w d.\nJeli przeciwnik skada si z kilku czci poczonych jakim cznikiem (np. Mr. Maw, Gemini), cznik zostanie przecity.\nUycie na Lokim zmieni go w Lokii.\nUycie grajc jako Tainted Jacob podzieli Dark Esau na p. Gdy podzielenie Dark Esau zgin, Jacob nie zmieni si w form ducha.\nNie ma efektu na nietykalnych przeciwnikw, np. zamknitych Hostw.\nNie ma efektu na kocowych bossw oprcz Delirium, zamiast tego zadajc im troch obrae.",
  632: "+2 Szczcia.\nIsaac jest odporny na podpalenie, zdezorientowanie, strach, trucizn, toksyczne chmury, spowolnienie przez pajczyny, ogniska, ogniste ataki oraz aur z pociskw Dogmy.\nIsaac moe sta w luzie przeciwnikw przez 1 sekund, zanim otrzyma obraenia.",
  633: "+2 Obrae.\n+0,1 Szybkoci.\nJeli Isaac ma mniej ni 6 serc, zostanie uleczony do 6 serc poprzez odnowienie Czerwone Serca i/lub dodanie Soul Heartw.\nDaje Isaacowi latanie.\nDaje Isaacowi jednorazowy efekt Holy Mantle, ktry znika po wyjciu z pokoju.\nW trybie normalnym, daje Isaacowi efekt The Wafer, redukujc obraenia do 1/2 serca.\nIsaac automatycznie otrzymuje ten przedmiot po pokonaniu Dogmy.",
  634: "W kadym pokoju z przeciwnikami wytworzy si pknicie w ziemi. Przejcie nad nim przyzwie duszka, ktry po 1 sekundzie poleci w kierunku najbliszego przeciwnika i na nim wybuchnie, zadajc 2x Obraenia Isaaca na tick, do 4 razy, oraz niszczc pociski przeciwnikw. Wybuch nie rani Isaaca.\nPo eksplozji na ziemi pojawia si nastpnie pknicie, i tak dalej, a pokj zostanie pokonany.",
  635: "Przyzywa chowaca, ktry porusza si w t stron, ktr Isaac strzela. Chowaniec zadaje 1,5 Obrae kontaktowych na tick (3 Obrae na sekund).\nUycie przedmiotu powoduje, e Isaac i chowaniec zamieniaj si miejscami.\nPrzeteleportowanie si na przeciwnika zada mu 40 + 3x Obraenia Isaaca. Przeteleportowanie si na Shopkeepera zniszczy go, a na ognisko zgasi je.\nIsaac otrzymuje krtk chwil nietykalnoci po teleporcie, co moe zosta uyte do wejcia do Curse Roomu lub otworzenia Spiked Chest/Mimic Chest.",
  636: "Przenosi Isaaca na pocztek nowej rozgrywki w Rozdziale 1, ze wszystkimi statystykami, przedmiotami, znajdkami i trinketami.\nSzansa na Angel Room, szansa na Planetarium oraz Donatiom Machine s zresetowane. Key Piece 1/2 oraz Knife Piece 1/2 mog zosta ponownie zebrane.\nCzas i wynik punktowy rozgrywki, pule przedmiotw oraz pula piguek **nie** s resetowane.\nR Key zawsze kosztuje 99 monet (oprcz specjalnego sklepu wygenerowanego przez Member Card).",
  637: "Daje Isaacowi szans na wystrzelenie zy-pici, ktra mocno odrzuca przeciwnikw i dezorientuje ich na 2 sekundy.\nOdrzuceni przeciwnicy po wleceniu w cian, przeszkod lub innego przeciwnika, otrzymuj i zadaj 10 + 2 * (x - 1) Obrae, gdzie x = numer pitra.\nSzansa zalena jest od Szczcia i wynosi 100% przy 9 Szczcia.",
  638: "Przy uyciu Isaac moe rzuci gumk. Pierwszy przeciwnik, ktrego gumka trafi, zostanie usunity z gry na czas trwania caej rozgrywki.\nW przypadku bossw, gumka zadaje im 15 Obrae. Jeli obraenia te s miertelne, boss jest rwnie wymazany z gry.\nWymazanie zapobiega wszelkim efektom mierci. Grzechy oraz Anioki nadal wyrzuc znajdki lub przedmioty.\nUycie na pierwszej fazie bossw, ktre maj kilka faz, np. Hush czy Ultra Greedier, spowoduje pomincie nastpnych faz.\nNie ma efektu na nietykalnych przeciwnikach, np. Stone Grimace'ach, nieodsonitych Hostach czy Eternal Fly'ach.\nPrzedmiot moe zosta naadowany tylko przez przejcie na nowe pitro.",
  639: "Daje Isaacowi 1 Rotten Heart.\nMa ma szans wypa z Rotguta.",
  64: "Zmniejsza ceny artykuw na sprzeda w sklepie. Cena zmniejszona jest do (x / (n + 1)), zaokrglone do gry, gdzie x = oryginalna cena, n = ilo posiadanych Steam Sale'w.\nSklepy nie bd miay dodatkowych zniek.\nMa szans wypa z Greeda lub z Shopkeeperw.",
  640: "Zabijanie przeciwnikw daje przedmiotowi adunki dusz, maksymalnie 20. Bossy daj wiksz liczb adunkw. Kady adunek jest warty 15 pomieniom w czasie 1/2 sekundy.\nPrzy uyciu wystrzeliwuje strumie pomieni w kierunku, w ktrym zwrcony jest Isaac. Kady pomie zadaje 1,5-2,1 Obrae na tick, zalenie od dystansu, jaki pokona.\nIsaac jest lekko odpychany do tyu przy uyciu.\nPomienie niszcz pociski przeciwnikw, kupy i inne pomienie, ale odbijaj si od kamieni i nie niszcz shopkeeperw.",
  641: "W pokoju z przeciwnikami generuje acuch ez, ktre cign si za Isaaciem i zadaj 3,5 Obrae.\nIm krtszy acuch, tym szybciej ronie. Maksymalna dugo acucha to 20 ez.\nMa ma szans wypa z The Shella.",
  642: "Przy uyciu generuje przedmiot z puli obecnego pokoju, zabierajc Isaacowi 1 Czerwone Serca, 1 Bone Heart lub 2 Soul Hearty (w tej kolejnoci) i zastpujc je Zamanym Sercem, ktre nigdy nie moe zosta napenione i w rezultacie obnia maksymalne zdrowie Isaaca.\nPo uyciu przynajmniej raz, jeli Isaac obecnie nie trzyma Magic Skin, przedmiot ten ma szans zastpi kady inny przedmiot odtd wygenerowany. Im wicej razy Magic Skin zostao uyte, tym wiksza szansa na to.\nThe Lost i Tainted Lost mog uy tego przedmiotu raz, po czym znika on.\nTainted Jacob w formie ducha moe uy tego przedmiotu 12 razy, po czym znika on. Nie zabiera to mu serc.\nUycie przedmiotu w formie ducha po dotknicu Biaego Ogniska w Downpour II/Dross II zabiera serca i zamienia je w Zamane Serca.\nMa ma szans wypa z The Scourge'a.",
  643: "+2 Soul Hearty.\nDaje Isaacowi latanie.\nStrzelanie przez 2,35 sekundy aduje promie wiata, ktry zostanie wystrzelony przy puszczeniu klawiszw strzau. Promie zadaje Obraenia Isaac na tick (maksymalnie 15 razy) i utrzmyuje si ok. 1,28 sekundy. Promie moe by wystrzelony po przektnej.",
  644: "Daje Isaacowi jeden z nastpujcych bonusw, zalenie od tego, ktra statystyka jest najmniejsza:\n* +1 Obrae\n* -0,5 Opnienia ez\n* +0,2 Szybkoci\n* +1,5 Zasigu\nWyrzuca 3 monety, 1 bomb lub 1 klucz, w zalenoci od tego, ktrej znajdki Isaac ma najmniej. Bomba i klucz s warte 3 monety. W przypadku Bethany, moe rwnie wyrzuci Soul Hearta, jeli ma tej znajdki najmniej; Soul Heart jest warty 2 monety.\nPozbycie si tego przedmiotu, np. przez Tainted Isaaca, nie odbiera bonusu do statystyk.",
  645: "Orbital podony do Teratomy, ktry odpycha przeciwnikw i zadaje im 3,5 Obrae kontaktowych raz na sekund.\nPo zablokowaniu 3 strzaw lub zadania 3 tickw obrae, orbital rozdziela si na 2 mniejsze, ktre orbituj Isaaca naprzeciwko siebie. Po otrzymaniu 2 obrae, mniejsze orbitale rozbijaj si na 3 Niebieskie Pajki.\nPo 5 sekundach od rozbicia mniejszych orbitali odradza si oryginalny orbital.\nOrbital moe zosta rozbity na kamieniu z kolcami.\nMa ma szans wypa z Teratomy.",
  646: "+5 bomb.\nPrzy wybuchu, bomby Isaaca wystrzeliwuj laser Brimstone'a prostopadle w 4 kierunkach. Lasery zadaj 8 Obrae na tick, do 19 razy. Lasery te mog popycha bomby.\nMa ma szans wypa z The Fallen.",
  647: "Aktywny przedmiot Isaaca aduje si, kiedy zadaje on obraenia, a nie przy przejciu pokoju.\nObraenia potrzebne do otrzymania 1 adunku s obliczane wg wzoru (40 + 20 x numer pitra), np. dla Basement I nr pitra = 1, dla Depths II = 6.\nPitra w Ascencie liczone s jak ich normalne odpowiedniki.",
  649: "Przyzywa chowaca, ktry atakuje podobnie jak Baby Plum, plujc zami i odpychajc si po przektnej. Chowaniec zadaje 2 Obrae kontaktowych na tick (6 na sekund) i 3 Obraenia zami.",
  65: "Przyzywa 6 Troll Bomb, zazwyczaj blisko rodka pokoju.\nMoe wypa z Pride'a.",
  650: "Przyzywa Baby Plum jako chowaca na ok. 10 sekund. Baby Flum wykonuje atak z obrotem, potem atak ze skokiem, a na kocu plucie pociskami i odbijanie si po pokoju, po czym odlatuje.\nBaby Plum zadaje 12 Obrae kontaktowych na tick (24 na sekund), 3,5 Obrae na pocisk oraz 2 Obrae na tick (20 na sekund) luzem.\nPrzedmiot mona otrzyma z walki z Baby Plumem, jeli Isaac nie bdzie zadawa mu adnych obrae (wliczajc obraenia chowacw) przez ok. 1/2 minuty.",
  651: "Przyzywa chowaca, ktry wolno poda w stron Boss Roomu oraz emituje aur, ktra daje Isaacowi +1 Obrae, mnonik obrae 1,2x, mnonik opnienia ez 0,4x, samonaprowadzajce zy, oraz szans na zablokowanie obrae.\nChowaniec moe przechodzi przez Secret Roomy, ujawniajc ich lokacj.\nW The Void, chowaniec bdzie poda w stron Boss Roomu z Delirium.\nNa pitrach Chest lub Dark Room, jeli drzwi do Mega Szatana zostay otwarte, Star of Bethlehem wleci tam i bdzie obecna podczas walki z Mega Szatanem.",
  652: "Przyzywa chowaca, ktry sam si nie porusza, ale lizga si po pokoju, gdy Isaac w niego wejdzie lub przeciwnicy w niego strzel.\nChowaniec blokuje pociski przeciwnikw, zadaje obraenia kontaktowe (do 11 Obrae, zalenie od tego, jak szybko si porusza) i spowalnia przeciwnikw, ktrych dotknie.\nPrzeciwnicy zabici przez Cube Baby zostaj zamroeni.",
  653: "Zabici przeciwnicy zostawiaj za sob czerwone duszki. Przy uyciu przedmiotu wszystkie duszki wybuchaj, zadajc przeciwnikom obraenia rwne 3 + 0,04 * x, gdzie x to zdrowie przeciwnika, z ktrego wygenerowa si duszek.\nZasig wybuchu jest tym wikszy, im jest on silniejszy. Przeciwnicy z wiksz iloci zdrowia, jak mini-bossy i championy, zostawiaj za sob wiksze duszki, ktre wytwarzaj 3 wybuchy zamiast jednego.\nWybuchy nie rani Isaaca ani nie uszkadzaj przeszkd, ale mog zniszczy pociski przeciwnikw.\nDuszki mog by przesuwane przez strzelanie w nie.\nDuszki, ktre w danym momencie nie s widoczne na ekranie (np. w duych pokojach), nie wybuchn.\nUycie przedmiotu natychmiastowo zabija kadego przeciwnika-ducha, ktry ma 50% zdrowia lub mniej. Do przeciwnikw-duchw zaliczaj si: Candler, Dust, Kineti, Lil' Haunt, Polty, Red Ghost, Wizoob, the Forsaken, the Haunt, the Heretic, the Rainmaker, oraz Delirium w formie ktregokolwiek z wymienionych bossw.",
  654: "+1 Black Heart.\nWszystkie piguki s zidentyfikowane.\nZamienia dobre piguki w ze:\n* piguka zwikszajca statystyki  piguka zmniejszajca statystyki\n* Bad Gas, Friends Till the End!  Health Down\n* I can see forever!, Lemon Party  Amnesia\n* Explosive Diarrhea, One makes you larger  Range Down\n* Bombs are Key, Infested!  Tears Down\n* 48 Hour Energy, One makes you small  Speed down\n* Pretty Fly, Infested?  Luck Down\n* Balls of Steel, Full Health, Hematemesis  Bad Trip\n* Pheromones  Paralysis\n* Telepills  ???\n* I'm Drowsy... -> I'm Excited!!!\n* Percs!  Addicted!\n* Feels Like I'm Walking on Sunshine!  Retro Vision\n* Power Pill!  R U A Wizard?\n* Something's Wrong...  X-Lax\n* Gulp!, Vurp!  Horf!\n* Experimental Pill - moe tylko zmniejszy statystyki\n* Gold Pill - ma efekty tylko negatywnych piguek\nPiguki obniajce statystyki daj Isaacowi +0,6 Obrae, lub, jeli byy to koskie piguki, +1,2 Obrae. Odnosi si to te do piguek wzitych przed podniesieniem przedmiotu.\nInne negatywne piguki wyrzuc Black Hearta po wziciu ich.\nPiguki Bad Trip i Health Down zamieni si w I Found Pills, jeli Isaac ma 1 Czerwone Serce lub mniej.",
  655: "Przyzywa orbitala, ktry blokuje pociski przeciwnikw i zadaje 3,5 Obrae na tick (10,5 Obrae na sekund).\nPrzedmiot moe by uyty poprzez przytrzymywanie klawiszu uycia przedmiotu. Podczas tego, Isaac otrzymuje +0,5 Szybkoci (bonus ten moe przekroczy normalny limit 2 Szybkoci), a jego orbitale krc si szybciej, dostaj bonus do obrae 6 + 1,5 x Obraenia orbitala, oraz odrzucaj przeciwnikw i Isaaca, kiedy zadaj obraenia.",
  657: "Przeciwnicy przy mierci wyrzucaj z siebie zy w losowych kierunkach.\nLiczba ez jest rwna liczbie ez, jaka uderzya przeciwnika, minimalnie 2 a maksymalnie 16.\nzy otrzymuj efekty ez Isaaca.",
  658: "Przy otrzymaniu obrae, przyzwany jest chowaniec, ktry ciga pobliskich przeciwnikw i ich atakuje.\nChowace zadaj Obraenia rwne 1,35 + 0,15 * (x - 1), gdzie x = numer pitra, i strzelaj 3 zami na sekund.\nChowace utrzymuj si midzy pokojami i pitrami.\nKady chowaniec jest przyzwany z 15 punktami zdrowia, ktre jest zmniejszane w tempie 0,4 na sekund, dopki nie osignie ono 0,8 punktw zdrowia. Przeciwnicy mog zadawa im obraenia kontaktowe lub przez pociski, ale s one odporne na kolce i lasery.\nChowace s leczone do penego zdrowia, gdzie Isaac otrzyma obraenia.\nMaksymalna ilo chowacw z Giant Cell to 16.\nJeli Isaac posiada lot, chowace te bd latay.\nChowace w przypadku The Forgottena przyjmuj form postaci (The Forgottena lub The Soul), ktra otrzymaa obraenia. W formie The Forgottena bd one miay ko, ktr bd atakoway 1,5 raza na sekund, zadajc obraenia rwne 8,1 + 0,9 * (x - 1), gdzie x = numer pitra.\nMa ma szans wypa z Polycephalusa.",
  659: "+1,5 Zasigu.\nZwiksza wielko ez.",
  66: "Przy uyciu wszyscy przeciwnicy w pokoju zostaj spowolnienie na ok. 8 sekund.",
  660: "Na pocztku kadego pitra zostan wygenerowane 2 portale, ktre, zalenie od koloru, prowadz do:\n* czerwony - Boss Roomu (losowego w The Void)\n* ty - Treasure Roomu\n* niebieski - Secret Roomu\n* zielony - Shopu\nOd Rozdziau 4, tylko 1 portal zostanie wygenerowany.\nPortale znikaj po wyjciu z pokoju startowego pitra.",
  661: "Przy zabiciu przeciwnikw, generuje w miejscu ich mierci nieruchomego chowaca, strzelajcego w kierunku, w ktrym strzela Isaac.\nMaksymalnie moe zosta wygenerowanych 5 chowacw.\nMoliwe chowace to Abel, Boiled Baby, Brother Bobby, Demon Baby, Freezer Baby, Ghost Baby, Guardian Angel, Harlequin Baby, Lil Brimstone, Little Steven, Multidimensional Baby, Robo-Baby, Rotten Baby, Seraphim, Sister Maggy.",
  663: "Co 6 sekund, Isaac otrzymuje na 1 sekund nietykalno i zadaje 40 Obrae kontaktowych.\nIsaac miga na biao 3 razy przed otrzymaniem nietykalnoci.",
  664: "Daje Isaacowi 1 serce i cakowicie go leczy.\nWszystkie piedestay czasowo zmieniaj si midzy jedzeniem a przedmiotem, ktre normalnie by zawieray.\nZebranie jedzenia leczy 3 serca zamiast 1 oraz daje tymczasowy bonus +3,6 do Obrae, stay bonus do dwch statystyk oraz -0,03 Szybkoci.\nTymczasowy bonus trwa 30 sekund i nie dodaje si ze sob. Zabijanie przeciwnikw go przedua.\nJedzenie oraz statystyki, jakie dodaje:\n* Breakfast: +0,2 Szybko Strzaw, +1,5 Zasig\n* Dessert: +1 Obraenia, +0,2 Szybko Strzaw\n* Dinner: +0,5 ez, +0,2 Szybko Strzaw\n* Lunch: +0,5 zy, +1,5 Zasig\n* Midnight Snack: +1 Obraenia, +1 Szczcie\n* Rotten Meat: +1 Obraenia, +1,5 Zasig\n* A Snack: +0,2 Szybko Strzaw, +1 Szczcie\n* Supper: +0,5 zy, +1 Szczcie",
  665: "Isaac jest w stanie zobaczy zawarto skrzynek, sakiewek, ognisk i Shopkeeperw przed otwarciem ich.\nMaksymalnie 4 znajdki mog zosta pokazane. Jeli jest ich wicej, znajdki o wikszej wartoci (np. Soul Heart) maj priorytet.\nJeli Red Chest wyglda, jakby nie zawieraa nic, teleportuje ona Isaaca do Devil Roomu/Angel Roomu.\nMimic Chesty zmieniaj swoj zawarto, kiedy ich kolce si wysuwaj. Jedyny sposb na otrzymanie oryginalnej zawartoci to otwarcie skrzynki przy pomocy Magneto.",
  667: "+1 bomba.\nPrzyzywa Keepera jako dodatkow posta, ktre jest kontrolowana tak samo jak Esau. Keeper bdzie mia swoje podstawowe statystyki, ale nie otrzymuje startowych przedmiotw (takich jak Wooden Nickel czy Store Key).\nKeeper moe podnosi pasywne przedmioty niezalenie od gwnej postaci. Nie moe podnosi przedmiotw aktywnych, trinketw ani znajdziek.\nSzybko Keepera jest niezalena od gwnej postaci.\nUkady z Diabem bd kosztoway pienidze.\nJeli gwna posta postawi bomb, Keeper postawi drug za darmo.\nKeeper nie moe przechodzi przez drzwi ani zapadnie, ale moe wchodzi w promienie wiata oraz skrzynie w Cathedral lub Sheolu.\nJeli Keeper zginie, wygeneruje 5 niebieskich pajkw, a Strawman zostanie usunity z ekwipunku Isaaca.",
  668: "Przy podniesieniu rozpoczyna Ascent.\nDad's Note nie moe zosta przelosowany ani zaabsorbowany wikszoci sposobw, np. przez The D6, Void ani Abyss. Nie moe rwnie zosta podwojony ani zniszczony przez Crooked Penny.\nPrzedmiot dostpny tylko w specjalnej wersji Mausoleum II/Gehenny II, do ktrej mona dosta si przez Strange Door.",
  669: "+1 Czerwone Serce.\n+0,2 Szybkoci.\n+0,5 Obrae.\n+0,5 ez.\n+1,5 Zasigu.\n+0,2 Szybkoci Strzaw.\n+1 Szczcia.",
  67: "Przyzywa chowaca, ktry co 2/3 sekundy strzela zami zadajcymi 6 Obrae.",
  670: "Przy przejciu pokoju, jeli normalnie wygenerowana zostaaby nagroda, druga nagroda zostaje wygenerowana obok niej. Gdy Isaac podniesie jedn z nich, druga znika.\nJeli jedn z nagrd jest Troll Bomba, podniesienie drugiej nagrody nie spowoduje jej zniknicia.\nOtwarcie Stone Chest i podniesienie jej zawartoci, podniesienie zawartoci Haunted Chest po jej otwarciu przez Polty'ego oraz zamienienie serca w muchy przez Apple of Sodom lub Keepera/Tainted Keepera nie spowoduje zniknicia drugiej nagrody, a podniesienie drugiej nagrody nie spowoduje zniknicia zawartoci otwartych skrzynek, tylko samej otwartej skrzynki.",
  671: "Generuje czerwone serce.\nDaje Isaacowi jeden z poniszych bonusw do statystyk za kade 1/2 serca uleczonego podniesionymi czerwonymi sercami (wliczajc Rotten Hearty):\n* +0,02 Szybkoci\n* +0,05 ez\n* +0,1 Obrae\n* +0,15 Zasigu\n* +0,02 Szybkoci Strzau\n* +0,1 Szczcia\nEfekt dziaa tylko przy podnoszeniu serc, a nie przy leczeniu si przy pomocy przedmiotw lub innych efektw.\nEfekt nie dziaa na Tainted Bethany.\nPozbycie si tego przedmiotu, np. na Tainted Isaacu, spowoduje utrat bonusu przy nastpnej zmianie danej statystyki.",
  672: "Przedmioty z ukadw z Diabem i Czarnego Rynku kosztuj pienidze.\nPrzedmioty w sklepie kosztuj serca. Ich zakup nie anuluje szansy na Angel Room.\nZnajdki w sklepie otoczone s przez kolce, na ktre nie dziaa odporno na kolce i z ktrych nie da si ich wypchn bomb.",
  673: "Przedmiot do zdobycia tylko z Konfesjonau lub Sacrifice Roomu, gdy normalnie wyrzuciyby one przedmiot z puli Angel Roomu, a Isaac wzi wczeniej ukad z Diabem.\nPrzy wejciu do Devil Roomu lub czerwonego Treasure Roomu wygenerowanego przez Devil's Crown nad gow Isaaca pojawi si krzy. Wzicie jakiekolwiek przedmiotu lub znajdki (wliczajc otwarcie Czerwonych Skrzynek) w tych pokojach spowoduje, e krzy zniknie.\nPrzejcie z krzyem na nastpne pitro da Isaacowi Soul Hearta oraz +1 Obrae.\nWzicie znajdziek/przedmiotw wygenerowanych przez ogniska, Devil Beggara lub Krampusa nie usun krzya.",
  674: "Przy mierci Isaac zamienia si w ducha, ktry jest poczony acuchem z jego ciaem. Moe kontynuowa walk z 1/2 serca i wrci do ycia po 10 sekundach.\nW formie ducha Isaac posiada lot, ale nie zy spektralne.\nPo wykorzystaniu efektu, nie moe on zadziaa ponownie, dopki Spirit Shackles nie zostan naadowane poprzez podniesienie Soul Hearta lub Black Hearta (powki lub caego). Kiedy s naadowane, kajdany na rkach Isaaca s niebieskie, nienaadowane s natomiast szare.\nPrzedmioty, ktre dodaj Soul Hearty lub Black Hearty bezporednio do zdrowia Isaaca, naaduj Spirit Shackles i dodadz 1 serce mniej.\nIsaac jest nietykalny przez chwil po powrceniu do ycia.\nJeli Isaacowi uda si wyjc z pokoju w formie ducha, nie bdzie mia acuchw i powrci do ycia w jego obecnej lokacji.\nSpirit Shackles nie mog zosta aktywowane w Lustrzanym Odbiciu.",
  675: "Przy otrzymaniu obrae przez Isaaca, otwiera wszystkie drzwi w pokoju, ujawnia losowy pokj na mapie (wliczajc Ultra Secret Room) oraz niszczy wszystkie Tinted Rocki i kamienie, pod ktrymi znajduje si Crawl Space.\nEfekt otwarcia drzwi jest w stanie otworzy drzwi do Mega Szatana, drzwi do przej do alternatywnych piter, drzwi do Mom's Heart w Mausoleum II/Gehennie II oraz Strange Door.",
  676: "Jeli Isaac ma 1 wypenione czerwone serce lub mniej (wliczajc brak kontenerw Czerwonych Serc) na pocztku pitra, otrzymuje on puste Czerwone Serce.\nEfekt nie dziaa na postaci, ktre nie mog mie Czerwonych Serc.\nMa ma szans wypa z The Visage.",
  677: "Przy pierwszym otrzymaniu obrae w pokoju, czas zatrzymuje si na 2 sekundy, a Isaac zamienia si w ducha, ktry lata i posiada zy spektralne.\nDuch wraca do ciaa Isaaca po otrzymaniu pierwszego uderzenia (Isaac nie traci zdrowia) lub po przejciu pokoju.\nMa ma szans wypa z Clutcha.",
  678: "Isaac zamiast ez bdzie wystrzeliwa samonaprowadzajce, przeszywajce i spektralne pody, ktre zadaj 75% jego Obrae 5 razy na sekund.",
  679: "Przyzywa chowaca, ktry, po naadowaniu przez 1 sekund, wytwarza czarny piercie podobny do tego z Maw of the Void, ktry zadaje 3,5 Obrae na tick, do 15 razy.",
  68: "zy Isaaca s zastpione przez przebijajcy laser o nieskoczonym zasigu.",
  680: "Strzelanie bez przerwy przez 2,35 sekundy aduje atak, ktry moe zosta aktywowany przez puszczenie klawiszw strzau. Atak skada si z lasera, ktry zadaje Obraenia Isaaca na tick (15x Obraenia Isaaca na sekund), uderza 19 razy i utrzymuje si ok. 1,28 sekundy, oraz z losowej liczby kukurydzianych ez, ktre zadaj 6 Obrae. Atak wystrzeliwany jest do tyu od Isaaca.\nMa ma szans wypa z Dangle'a.",
  681: "Przyzywa chowaca, ktry po podwjnym naciniciu klawiszu strzau szaruje w kierunku, w ktrym Isaac strzela, zadajc obraenia kontaktowych oraz absorbujc znajdki i trinkety, zamieniaj je w Niebieskie Muszki.\nObraenia kontaktowe zadawane przez chowaca s rwne (2 + 2 * x), gdzie x = liczba zaabsorbowanych znajdziek/trinketw.\nGdy chowaniec zaabsorbuje liczb znajdziek/trinketw rwn to numer pitra + 1, generuje portal oraz znika na czas trwania obecnego pitra. Portal prowadzi do specjalnego pokoju, zawierajcego zazwyczaj jedn skrzynk i jedn znajdk. Moe rwnie zawiera Butter! lub Moving Box.\nPortal wygenerowany na kadym pitrze prowadzi do tego samego pokoju. Wszystko, co zostao zostawione w tym pokoju, zostanie zachowane midzy pitrami.\nLil Portal nie wygeneruje portalu w anielskim sklepie generowanym przez The Stairway, sekretnym sklepie generowanym przez Member Card, Crawlspace'ach ani Black Marketach. Jeli zaabsorbuje on znajdki/trinkety wystarczajce do wygenerowania portalu w tych pokojach, wygeneruje go on dopiero po wyjciu do innego pokoju.\nW przypadku posiadania wicej ni jednego Lil Portala, tylko jeden z nich bdzie generowa portale, a reszta pozostanie ofensywnymi chowacami.",
  682: "Przyzywa chowaca, ktry wychodzi spod ziemi i chwyta przeciwnikw. Pochwyceni przeciwnicy s na 4 sekundy unieruchomieni, spowolnieni i otrzymuj 4 punkty Obrae na tick (32 Obrae na chwyt).\nPo wypuszczeniu/zabiciu przeciwnika, Worm Friend musi odczeka 5 sekund przez pochwyceniem nastpnego.\nEfekt dziaa rwnie na wszystkie bossy.\nMa ma szans wypa z The Staina.",
  683: "Przy mierci przeciwnicy generuj 1-2 koci, ktre blokuj pociski przeciwnikw, zadaj obraenia kontaktowe rwne Obraeniom Isaaca w momencie, w ktrym zostay wygenerowane, oraz odpychaj przeciwnikw.\nKoci mog zablokowa 2 pociski i/lub zada 2 ticki obrae, po czym znikaj.\nMa ma szans wypa z Carrion Queen.",
  684: "W momencie mierci przeciwnicy maj szans na wygenerowanie duszka, ktry bdzie goni innych przeciwnikw i zadawa im 0,5x Obraenia Isaaca na tick (2x Obraenia Isaaca na sekund). Po 5 sekundach, duszek wybucha, zadajc 7 Obrae pobliskim przeciwnikom, do 3 razy.\nPrzeciwnicy zabici przez obraenia kontaktowe duszkw (ale nie ich eksplozj) nie generuj nastpnych duszkw.",
  685: "Przy uycia generuje ognika, ktry orbituje Isaaca, strzelajc zami, blokujc pociski przeciwnikw i zadajc obraenia kontaktowe, podobnie to tych stworzonych przez Book of Virtues.\nPrzy pierwszym uyciu generuje 2 ogniki, przy drugim - 3 ogniki, itd., do maksymalnie 12.\nIsaac moe maksymalnie mie 26 ognikw (w piercieniach po kolei po 6, 8, 12).\nOgniki mog by kadym wariantem, jakie moe wygenerowa Book of Virtues.",
  686: "Generuje Soul Heart.\nGdy Isaac podnosi Soul Heart lub Black Heart, otrzymuje jeden z nastpujcych bonusw:\n* +0,04 Szybkoci\n* +0,1 ez\n* +0,2 Obrae\n* +0,3 Zasigu\n* +0,04 Szybkoci Strzaw\n* +0,2 Szczcia\nEfekt aktywuje si tylko przy podnoszenie serc, a gdy serca s bezporednio dodawane do zdrowia Isaaca (np. przez piguk Balls of Steel albo Book of Revelations).",
  687: "Przy uyciu przyzywa losowego przyjacielskiego przeciwnika, ktry naladuje ruchy i ataki Isaaca.\nNiektrzy przeciwnicy bd mieli celownik, ktrym Isaac moe sterowa, decydujc, gdzie bd oni strzela.\nMoliwi do przyzwania przeciwnicy to: Clotty, Clot, I.Blob, Grilled Clotty, Vis, Double Vis, Chubber, Danny, Coal Boy, Gurgle, Hive, Nest, Holy Mulligan, Drowned Hive, Bony, Black Bony, Holy Bony, Blaster, Dople, Evil Twin, Maw.",
  688: "Po mierci odradza Isaaca w tym samym miejscu, w ktrym umar, z 1/2 serca (jeli Isaac mia Czerwone Serca, bdzie to 1/2 Czerwonego Serca; w przeciwnym wypadku 1/2 Soul Hearta), +0,2 Szybkoci oraz zmniejszonym rozmiarem.\nIsaac odradza si z eksplozj, ktra zadaje 35 Obrae przeciwnikom w jej zasigu.\nInner Child jest pitym przedmiotem w kolejnoci aktywacji przedmiotw odradzajcych, po Soul of Lazarus, 1up!, drugim yciem Lazarusa i Dead Cat.\nJeli Inner Child zosta odblokowany, a splamiona wersja obecnej postaci zostaa ju odblokowana lub obecna posta jest splamiona, schowek w Home bdzie zawiera Inner Child, nawet jeli zosta ju podniesiony wczeniej w rozgrywce lub gra odbywa si Tainted Lostem (ktry nie moe normalnie spotka tego przedmiotu).",
  689: "Przedmioty na piedestaach zmieniaj si pomidzy 5 przedmiotami z puli obecnego pokoju co 0,2 sekundy, podobnie do umiejtnoci Tainted Isaaca.\nNie wpywa na przedmioty fabularne.",
  69: "2,5x Opnienie ez.\nDaje Isaacowi adowane zy, ktrych obraenia zale od tego, jak dugo je aduje - ok. 10% Obrae Isaaca przy najmniejszym naadowaniu do 400% przy najwikszym.",
  690: "Przeciwnicy, ktrzy dotkn Isaaca, zostan odrzuceni w przeciwnym kierunku. Jeli wlec w cian, przeszkod lub innego przeciwnika, otrzymaj obraenia rwne 8 + 2 * pitro.\nKiedy przeciwnik odbije si od Isaaca, istnieje 50% szansy, e Isaac nie otrzyma obrae kontaktowych.\nRwnie daje 50% szansy, e pociski przeciwnikw odbij si od Isaaca bez zadawania obrae.",
  691: "Zapobiega generowaniu si przedmiotw o sabej jakoci.\nPrzedmioty o jakoci 0-1 s automatyczne zmieniane.\nPrzedmioty o jakoci 2 maj 33% szansy na zostanie zmienionymi.",
  692: "Devil Roomy odwiedzone po wziciu Sanguine Bond bd zawieray kolce, wejcie na ktre ma szans na danie Isaacowi nagrody:\n* 33% szansy: +0,5 Obrae\n* 15% szansy: 6 centw\n* 10% szansy: 2 Black Hearty\n* 5% szansy: losowy przedmiot z puli Devil Roomu\n* 2% szansy: transformacja w Leviathana",
  693: "Daje Isaacowi 9 orbitujcych wok niego muszek.\nGdy muszka zablokuje obraenia, staje si niebiesk muszk.\nPrzejcie pokoju dodaje 1 orbitujc muszk, maksymalnie 9.",
  694: "+3 Zamane Serca.\nIsaac otrzyma +0,25 Obrae za kade posiadane przez niego Zamane Serce.\nKade obraenia, ktre normalnie zabiyby Isaaca, nie zabiera mu serc, ale dodaje 2 Zamane Serce i zadaje 40 Obrae wszystkim przeciwnikom w pokoju.\nKeeper oraz Tainted Keeper otrzymuj 1 Zamane Serce.\nEfekt dziaa na The Losta, Tainted Losta i Tainted Jacoba w formie ducha. Mog oni otrzyma 4 dodatkowe obraenia, gin za pitym. Jeli Tainted Jacob podniesie Heartbreak w normalnej formie, bdzie mg otrzyma 5 obrae, zginie za szstym.",
  695: "Przy otrzymaniu obrae Isaac dostaje bonus do Szybkoci i ez, trwajcy przez cae pitro.\nBonus do Szybkoci to (0,04 * x) + 0,07, a do ez (0,1 * x) + 0,25, gdzie x to liczba otrzymanych na pitrze uderze.\nEfekty dodaj si do 6 razy, maksymalnie dajc +1,02 Szybkoci i +3 ez.",
  696: "Isaac otrzymuje piercie dookoa siebie. Jeli przeciwnik wejdzie w obrb piercienia i utrzyma si w nim ok. 1 sekundy, promie wiata wystrzeli z nieba, zadajc 3,5 Obrae na tick (do 15 razy) oraz tworzc 4 promienie prostopade do niego, ktre zadaj 1,75 Obrae na tick (do 15 razy).\nGdy Isaac otrzyma obraenia, piercie zwiksza swj rozmiar, do 10 razy. Rozmiar resetuje si do oryginalnego po przejciu na nowe pitro.",
  697: "Przyzywa chowaca, ktry poda za Isaaciem. Po wejciu do walki z bossem, Vanishing Twin staje si kopi tego bossa. Zdrowie obu bossw bdzie zmniejszone do 75% oryginalnego oraz bd oni lekko spowolnieni.\nPo pokonaniu kopia bossa wyrzuca przedmiot z puli Boss Roomu (nawet jeli by to boss z ustalonym przedmiotem, jak np. Headless Horseman), nawet jeli walka z oryginalnym bossem nie zostaa jeszcze zakoczona.\nVanishing Twin wraca do Isaaca na nastpnym pitrze.\nEfekt nie dziaa na ustalonych bossach (Mamie, Sercu Matki, itp.).",
  698: "Przyzywa dwa chowace, ktre strzelaj zami, ktre posiadaj statystyki ez, Zasigu, Szybkoci Strzaw oraz efekty ez Isaaca, i zadaj 0,375x Obrae Isaaca.",
  699: "Po przejciu 4 pokoi lub fal Greed Mode'a, (Boss) Challenge Roomu lub Boss Rusha, przy wejciu do nastpnego pokoju z przeciwnikami lub nastpnej fali Isaac wystrzeli wielki Brimstone na ok. 3,33 s. Brimstone zadaje obraenia Isaaca na tick.",
  7: "+1 Obrae.\nDodatkowo daje 1,5x mnonik Obrae, jeli Isaac posiada Book of Belial (mnonik nie dodaje si z mnonikami z Magic Mushroom i Cricket's Head).",
  70: "+1 Obrae.\n+0,2 Szybkoci.",
  700: "Przy uyciu piguki, karty lub runy zostanie raz aktywowany efekt 3 ostatnich piguek/kart/run, ktrych Isaac uy, odkd podnis Echo Chamber.\nPrzedmioty, ktre modyfikuj efekty kart, piguek lub run, maj wpyw na aktywowany efekt, np. PHD/False PHD zmieni piguki.\nZote Piguki i Blank Rune'y bd miay losowy efekt za kadym razem, gdy Echo Chamber je aktywuje.",
  701: "Generuje Old Chest na pocztku kadego nastpnego pitra.",
  702: "Przy otrzymaniu obrae, Isaac generuje czerwonego ognika, ktry strzela zami spektralnymi i zadaje obraenia kontaktowe. Ogniki utrzymuj si na cae pitro.\nOgniki strzelaj co 1 sekund i zadaj 3 + 0.1 * (x - 1) Obrae, gdzie x = numer obecnego pitra.\nOgniki zadaj 0,5x Obraenia Isaaca na tick obrae kontaktowych.\nMaksymalna liczba ognikw to 6. Ogniki te dziel przestrze z pierwsz warstw ognikw z Book of Virtues, razem z nimi moe by ich maksymalnie 8.",
  703: "Przy uyciu zamienia oryginaln posta z Esau Jr. lub na odwrt.\nEsau Jr. bdzie mia takie same bazowe statystyki i zdolnoci charakterystyczne dla postaci (np. opaska na oczy i Incubus Lilith) jak oryginalna posta. Jedynym wyjtkiem jest Eden i Tainted Eden, gdzie bdzie mia inne statystyki i pasywne przedmioty startowe.\nEsau Jr. zaczyna z 3 Black Heartami, +2 Obraeniami i lotem, oraz otrzymuje losowe pasywne przedmioty liczb rwne pasywnym przedmiotom oryginalnej postaci (oprcz przedmiotw fabularnych i przedmiotw o ustalonych pulach, jak np. The Small Rock). Przedmiote s brane z tych samych pul, z ktrych wzite byy przedmioty oryginalnej postaci. Wszystko, co zostaoby wygenerowane przez te przedmioty przy ich podniesieniu, jest generowane, gdy Esau Jr. jest pierwszy raz uyty.\nEsau Jr. dostanie pasywne przedmioty startowe oryginalnej postaci, ale wlicz si one rwnie do losowych przedmiotw, ktre dostani, np. jeli Eve uyje Esau Jr., dostanie on Dead Bird i Whore of Babylon + 2 dodatkowe przedmioty z puli Treasure Roomu. Tyczy si to rwnie aktywnych przedmiotw w miejscu na piguk/kart, jak np. Dark Arts Tainted Judasa.\nMonety, bomby i klucze s dzielone midzy postaciami, natomiast wszystko inne jest osobne.\nJeli oryginalna posta moga uywa tylko pewnych rodzajw serc jako zdrowia (np. ???, The Forgotten czy Keeper), Esau Jr. te bdzie uywa tych serc.\nJeli ktrakolwiek z postaci umrze, rozgrywka si zakoczy.",
  704: "Przy uyciu daje Isaacowi +0,4 Szybkoci, +3 Obrae oraz 2x Opnienie ez + 2 zy na sekund na 5 sekund, podczas ktrych moe atakowa tylko szczk zamiast zami.\nSzczka zadaje 3x Obraenia Isaaca przy machaniu ni i 1,5x Obraenia Isaaca przy rzucaniu.\nZabicie przeciwnika, gdy przedmiot jest aktywowany, wydua jego czas o 1 sekund.\nPrzedmiot jest adowany przez obraenia zadawane przeciwnikom. Peny adunek to 120 Obrae.\nTainted Samson ma wbudowan mechanik podobn do efektu tego przedmiotu.",
  705: "Przy uyciu Isaac otrzymuje bonus +1 Szybkoci, staje si nietykalny i moe przechodzi przez przeciwnikw i ich pociski przez 1 sekund lub dopki nie strzeli. Przeciwnicy i pociski, przez ktrych przeszed Isaac, zatrzymuj si w miejscu, dopki efekt si nie zakoczy, po czym otrzymuj 3,5 + 2x Obraenia Isaaca. Jeli Isaac przeszed przez kilku przeciwnikw/pociskw, kady nastpny atak zadaje dodatkowo +3,5 Obrae.\nPo zakoczeniu efektu w miejscu, w ktrym stoi Isaac, wywoany jest efekt wybuchu, ktry zadaje obraenia rwne sumie wszystkich obrae zadanych podczas ataku, a w przypadku uycia przez Dark Judasa lub Tainted Judasa otrzymuj oni tymczasowy bonus +1 Obrae za kadego przeciwnika/pocisk, przez ktry przeszed. Bonus ten zmniejsza si w tym wikszym tempie, im wikszy jest.\nCzas trwania efektu zwiksza si z kadym uyciem w tym samym pokoju.\nAtak zada obraenia nawet przeciwnikom, ktrzy s tymczasowo nietykalni, np. nieodsonitym Hostom czy Scolexowi. Isaac moe rwnie zaatakowa cakowicie nietykalnych przeciwnikw, np. Stoney czy Stone Grimace'y, aby zwikszy bonus do obrae.\nIsaac nie moe wchodzi w interakcj np. z Blood Donation Machine czy Devil Beggarami podczas trwania efektu, ale moe przechodzi przez kolce, otwiera Spiked Chest/Mimic Chest lub przej do Curse Roomu bez otrzymania obrae.\nWybuchowe pociski wybuchn po zniszczeniu przez Dark Arts.\nAtak wywoa niektre efekty ez, np. podpalenie z Fire Mind.\nOrbitale Isaaca nadal zadaj obraenia podczas ataku.\nStartowy przedmiot Tainted Judasa (w miejscu na piguk/kart), nawet jeli nie zosta jeszcze odblokowany.",
  706: "Przy uyciu niszczy wszystkie przedmioty w pokoju i przyzywa 1 czerwon muszk za kady przedmiot (w przypadku niektrych przedmiotw, np. The Inner Eye lub Twisted Pair, 2 lub 3).\nW przypadku niektrych przedmiotw muszki maj dodatkowe waciwoci.\nMuszki szaruj w kierunku, w ktrym Isaac strzela, zadajc obraenia kontaktowe zalene od jakoci przedmiotu:\n* 50% Obrae Isaaca dla przedmiotu o jakoci 0\n* 75% Obrae Isaaca dla przedmiotu o jakoci 1\n* 100% Obrae Isaaca dla przedmiotu o jakoci 2\n* 150% Obrae Isaaca dla przedmiotu o jakoci 3\n* 200% Obrae Isaaca dla przedmiotu o jakoci 4\nMaksimum posiadanych muszek to 64. Limit jest wspdzielony z Niebieskimi Muszkami.\nPrzedmiot startowy Tainted Apollyona w miejscu na kart/piguk.",
  707: "+1 Czerwone Serce.\nDodatkowo leczy 1 czerwone serce.",
  708: "+1 Obrae.\nIsaac nie moe strzela z lewego oka.",
  709: "Przy uyciu Isaac szaruje przez krtk chwil. Jeli podczas szary wbiegnie w przeciwnika, podniesie go, a na ziemi pojawi si celownik, ktrym mona sterowa klawiszami strzau. Po 1 sekundzie, Isaac skacze i wrzuca przeciwnika w ziemi, zadajc mu i wszystkim innym przeciwnikom, na ktrych wylduje, 50 Obrae  10 Obrae za kade powikszenie/pomniejszenie, ktre posiada, oraz wysyajc kamienne fale dookoa, ktre zadajc obraenia i niszcz przeszkody.\nIsaac jest nietykalny podczas trwania efektu przedmiotu.\nIsaac moe supleksowa zwykych przeciwnikw, bossw, Shopkeeperw, Poky i podobnych przeciwnikw, Stone Grimace'y i podobnych przeciwnikw, ogniska, bomby, oraz normalnie nietykalnych przeciwnikw (np. duszek Blighted Ovum albo The Haunt przed zmaterializowaniem si).\nNieruchomi przeciwnicy (np. Mega Maw albo Stone Grimace) wrc na swoj pozycj po supleksie.",
  71: "+0,3 Szybkoci.\n+1,5 Zasigu.\nZmniejsza rozmiar Isaaca.\nMa szans wypa z grzybw.",
  710: "Przy uyciu wykonuje machnici torb, ktre zadaje 3 punkty obrae i silny odrzut.\nMachnicie moe zosta uyte do otwarcia skrzynek z kolcami i mimikw, oraz do blokowania strzaw przeciwnikw. \nUycie machnicia na znajdce umieszcza j w torbie. Torba mieci maksymalnie 8 znajdziek, w przypadku Tainted Caina po przekroczeniu limitu znajdka w lewym grnym rogu zostanie zastpiona now.\nW przypadku Tainted Caina kolejno znajdziek mona zmieni klawiszem zmiany piguki/karty (domylnie Ctrl).\nPodwjne znajdki dodaj dwa egzemplarze znajdki do torby.\nGdy w torbie znajduje si 8 znajdziek, uycie przedmiotu generuje piedesta z okrelonym przedmiotem. W przypadku Tainted Caina pokazany jest przedmiot, ktry zostanie stworzony (chyba e aktywna jest Kltwa lepoty), a stworzenie przedmiotu daje mu Cainowi bezporednio, bez generowania piedestau. \nKolejno znajdziek nie ma znaczenia. Jako przedmiotu zaley od jakoci znajdziek.\nW przypadku Tainted Caina stworzenie przedmiotu aktywnego, podczas gdy posiada on ju jeden, zamieni obecnie posiadany przedmiot w znajdki.\nJeli przedmiot, ktry normalnie odpowiada danemu zestawowi znajdziek, nie jest jeszcze odblokowany, zamiast niego zostanie stworzony inny losowy przedmiot.\nPrzedmiot startowy Tainted Caina w miejscu na kart/piguk, nawet jeli nie zosta jeszcze odblokowany.",
  711: "Gdy Isaac wchodzi do pokoju z przedmiotem, piedesta bdzie zawiera drugi przedmiot-duch. Uycie Flipu zamienia rzeczywisty przedmiot z przedmiotem-duchem.\nWzicie rzeczywistego przedmiotu i wyjcie z pokoju spowoduje zniknicie przedmiotu-ducha.\nW przypadku Tainted Lazarusa rwnie przecza posta midzy Tainted Lazarusem i Dead Tainted Lazarusem.\nPrzedmiot startowy Tainted Lazarusa w miejscu na kart/piguk, nawet jeli nie zosta jeszcze odblokowany.\nDla Tainted Lazarusa czas adowania wynosi 4 adunki.",
  712: "Przy uyciu generuje losowy przedmiot pasywny jako orbitujcego Isaaca ognika. Przedmiot ma 25% szansy na bycie z puli obecnego pokoju, w przeciwnym wypadku jest z losowej puli spord Treasure Roomu, Boss Roomu lub Sklepu. Efekt przedmiotu dziaa na Isaaca.\nOgniki zadaj obraenia kontaktowe rwne 2x Obraeniom Isaaca na tick (4x Obraenia Isaaca na sekund) oraz blokuj pociski przeciwnikw. Po zablokowaniu 4 pociskw i/lub 2 uderzeniach od eksplozji, ognik znika i usuwa efekt swojego przedmiotu.\nIsaac moe mie maksymalnie 24 ogniki (w piercieniach po 6, 8 i 10). Po uyciu Lemegetonu ponownie przy 24 ognikach, najstarszy z nich zostanie zastpiony nowym.\nIsaac moe normalnie znale przedmioty wygenerowane przez Lemegeton, ogniki nie licz si do transformacji oraz do kolekcji przedmiotw.\nIsaac moe mie kilka ognikw z tym samym przedmiotem.\nPrzedmioty z ognikw nie mog zosta przelosowane.\nLemegeton moe wygenerowa ognika z Magic Skin, jeli przedmiot ten zosta uyty w obecnej rozgrywce.\nPrzedmioty, ktre normalnie dodaj lub usuwaj serca lub znajdki, nie zrobi tego jako ogniki.\nStartowy przedmiot Tainted Bethany (w miejscu na piguk/kart), nawet jeli nie zosta jeszcze odblokowany.",
  713: "Uycie spowoduje przyzwanie chowaca-krwiaczka i zabiera 0,5 serca. W przypadku Tainted Eve, strzelanie powoduje przyzwanie chowaca co 2 sekundy.\nZabieranie serc nie liczy si jako otrzymanie obrae. Serca zabierane s w kolejnoci: Golden Hearty, Eternal Hearty, Rotten Hearty, Czerwone Serca, a nastpnie od prawej strony Soul, Black, lub Bone Hearty.\nChowace podaj za Isaaciem i maj jego efekty ez. S odporne na kolce i ogniska, ale otrzymuj obraenia. Zaczynaj z 15 HP, a ich zdrowie powoli spada do 1 HP (lub 5 HP w przypadku chowacw z innych serc ni czerwone).\nUycie klawiszu zamiany (domylnie Ctrl) pozwala Isaacowi na poruszanie si niezalenie od chowacw.\nKady typ serca generuje innego chowaca:\n* Czerwone Serca - czerwony chowaniec, ktry zadaje 33% Obrae Isaaca\n* Soul Hearty - niebieski chowaniec, ktry zadaje 33% Obrae Isaaca\n* Eternal Hearty - biay chowaniec, ktry zadaje 50% Obrae Isaaca i ma efekt samonaprowadzajcy Sacred Hearta\n* Black Hearty - czarny chowaniec, ktry zadaje 40% Obrae Isaaca i ma efekt Dark Matter\n* Golden Hearts - zoty chowaniec, ktry zadaje 33% Obrae Isaaca i ma efekt Midas' Touch\n* Bone Hearty - chowaniec-szkielet, ktry zadaje 33% Obrae Isaaca i ma efekt Compound Fracture\n* Rotten Hearty - zielony chowaniec, ktry zadaje 33% Obrae Isaaca i ma efekt The Common Cold\nUycie Sumptorium jako Tainted Eve, e chowace powrc do niej i uleczy j na tak ilo serc, jaka bya uyta do ich wygenerowania. Chowace zadaj obraenia przeciwnikom, przez ktrych przelatuj, gdy wracaj do Isaaca.\nJeli Tainted Eve nie ma miejsca na tyle serc, ile ma chowacw, po uyciu Sumptorium zostan one obok niej.\nJeli Tainted Eve nie ma adnego chowaca i ma tylko 1/2 serca, otrzymuje efekt podobny do Whore of Babylon i bdzie uywa Sumptorium jako bro (podobnie do Mom's Knife).\nEfekt jest wywoany take, gdy Tainted Eve wygeneruje chowaca nie majc adnych innych i zostanie jej 1/2 czerwonego serca. Efekt wtedy znika po wyjciu z pokoju.\nPodczas trwania efektu, wszystkie chowace bd atakoway maym Mom's Knife.\nPrzedmiot startowy Tainted Eve w miejscu na kart/piguk, nawet jeli nie zosta jeszcze odblokowany.",
  714: "Przedmiot dostpny tylko przez zdobycie Birthright przez Tainted Forgottena. Zostanie on umieszczony w miejscu na kart.\nPozwala na odzyskanie ciaa Tainted Forgottena przez The Soul z dystansu. I ciao, i The Soul s nietykalni podczas, gdy ciao wraca do The Soul. Ciao zadaje obraenia kontaktowe ok. 4-6 Obrae Tainted Forgottena, podczas gdy wraca do The Soul.\nUycie przedmiotu kilka razy na raz (np. dziki podniesieniu baterii) spowoduje, e ciao bdzie wolno lecie w kierunku The Soul. Podczas tego i ciao, i The Soul s nietykalni, ale ciao nie zadaje obrae kontaktowych. Moe za to wykonywa atak koci. Ciao bdzie leciao przez maksymalnie 5 sekund na adunek.\nPrzedmiot nie moe zosta zmieniony przez D4, D100, itp.",
  715: "Przedmiot dostpny tylko jako przedmiot startowy Tainted ???.\nPrzy uyciu, przechowuje pierwsz w kolejce kup Tainted ???. Po ponownym uyciu, uywa tej kupy.",
  716: "Generuje 3 monety i 1 klucz przy podniesieniu.\nIsaac otrzyma bonusy do statystyk (+0,5 Obrae, +0,03 Szybkoci lub +0,25 Zasigu) przy zakupie przedmiotw/znajdziek - jeden bonus za sam zakup + jeden bonus za kade wydane 5 centw.\nEfekt dziaa wszdzie, gdzie Isaac moe wyda pienidze (Devil Roomy/Black Markety jako Keeper/Tainted Keeper lub z Keeper's Bargain albo A Pound of Flesh, przedmioty/znajdki z Keeper's Box, anielski sklep z The Stairway, sklep z Member Card).\nWszystkie bonusy do statystyk s powizane z przedmiotem i zostan usunite, jeli Isaac straci przedmiot (np. jako Tainted Isaac).",
  717: "W pokoju z przeciwnikami kamienie i inne przeszkody bd co jaki czas generowa Niebieskie Pajki.\nWszystkie kamienie i inne przeszkody przy zniszczeniu wygeneruj 0-2 Niebieskie Pajki.",
  719: "Generuje losowy przedmiot z puli Sklepu lub znajdk, ktre mog zosta zakupione za ich normaln cen w sklepie.\nJeli przedmiot zostanie uyty w Devil Roomie lub Black Markecie, wygenerowany przedmiot/znajdka bd otoczone przez kolce.",
  72: "+0,5 ez.\n+3 Soul Hearty.\nDodaje The Bible do wszystkim pul przedmiotw (jeli nie zostaa wczeniej podniesiona).\nSzanse na The Bible w poszczeglnych pulach to:\n* Treasure Room - 2,26%\n* Shop - 12,82%\n* Boss - 11,36%\n* Devil Room - 12,82%\n* Angel Room - 20,83%\n* Secret Room - 20,83%\n* Library - 45,45%\n* Golden Chest - 55,56%\n* Red Chest - 71,43%\n* Beggar - 27,78%\n* Devil Beggar - 45,45%\n* Curse Room - 38,46%\n* Key Master - 71,43%",
  720: "Przedmiot moe zosta uyty, gdy nie jest w peni naadowany. Generuje wtedy, po kolei, zaczynajc od 1 adunku, nastpujce znajdki: kup, centa, bomb, klucz, pene czerwone serce, piguk, kart, Soul Heart, Gold Heart, Gold Key, Gold Bomb.\nJeli przedmiot jest w peni naadowany, uycie go moe mie jeden z nastpujcych efektw:\n* nic\n* generuje losow liczb rnych znajdziek, takich jak monety, serca, bomby, klucze, karty, piguki i trinkety\n* generuje zwyk much\n* generuje losow liczb przyjacielskich Dipw, niebieskich pajkw i niebieskich muszek\n* generuje losowych przeciwnikw przed drzwiami pokoju i zamyka te drzwi\n* generuje piedesta, ktry przecza si midzy 9 rnymi przedmiotami z puli obecnego pokoju 30 razy na sekund\n* generuje wiele kolorowych promieni wiata, ktre zadaj Obraenia Isaaca przeciwnikom w pokoju\n* wypenia pokj kupami\n* generuje 6 Troll Bomb\n* obraca wszystkie grafiki, daje Isaacowi losowe efekty ez na czas trwania obecnego pokoju i na stae zwiksza lub zmniejsza jedn ze statystyka Isaaca\n* generuje wiele Shopkeeperw\n* generuje Giga Bomb",
  721: 'Wszystkie przysze przedmioty staj si "zgliczowane", dostajc kombinacj 2-3 efektw innych przedmiotw. Efekty te przypisane s do losowych wydarze (np. przy zabiciu przeciwnika, przy otrzymaniu obrae, itp.).\nZgliczowane przedmioty w sklepach kosztuj losow liczb monet.',
  722: "Wizi najbliszego przeciwnika w acucach na 5 sekund, uniemoliwiajc mu ruch i ataki.\nW przypadku Tainted Jacoba wizi Dark Esau. Uycie przedmiotu ponownie, gdy Dark Esau jest uwiziony, spowoduje, e zostanie on uwolniony i bdzie szarowa w stron Tainted Jacoba.\nPrzedmiot startowy Tainted Jacoba (w miejscu na kart/piguk), nawet jeli nie zosta jeszcze odblokowany.",
  723: "Zmienia wszystkie przedmioty w pokoju, obniajc ich ID o 1.\nPomija ukryte przedmioty: Bag of Crafting, Flip, Sumptorium, Recall, Hold, oraz pasywny przedmiot generowany przez uycie Damoclesa.\nThe Sad Onion (ID 1) znika po uyciu Spindown Dice.",
  724: "Gdy Isaac otrzyma obraenia, wyrzuca czerwone serce, ktre znika po 1,5 sekundy.\nTyp serca zaley od otrzymanych obrae - jeli byo to 1/2 serca obrae, wyrzuci 1/2 serca; jeli byo to 1 serce obrae, wyrzuci 1 serce. Nie dotyczy to rde obrae, ktre normalnie zadaj 1/2 serca, ale zwikszaj si do 1 serca od Rozdziau 4.\nSerca nie s generowana, gdy Isaac nie ma adnych kontenerw czerwonych serc.\nNastpujce rda obrae nie aktywuj Hypercoagulation:\n* drzwi do Curse Roomu\n* kolce w Sacrifice Roomie\n* Spiked Chests i Mimic Chests\n* piguki Bad Trip\n* Zote Baterie\n* Kamikaze!\n* Breath of Life\n* kolce w sklepie z A Pound of Flesh\n* IV Bag\n* Sharp Plug\n* Shard of Glass\n* Blood Bombs\n* Blood Oath",
  725: "Kiedy Isaac zada wystarczajco obrae przeciwnikom, zacznie wieci si na czerwono. Przy puszczeniu klawiszu strzau, wywoany zostanie efekt jednej z kup Tainted ???.\nMoliwe efekty dziel si na moliwe do rzucenia i inne. Moliwe do rzucenia kupy zadaj 15 Obrae:\n* Poop - normalna kupa\n* Corn Poop - generuje niebiesk muszk co 2 sekundy, maksymalnie 3 muszki; kiedy Isaac wyjdzie z pokoju z niezniszczon Corn Poop, zostanie wygenerowanych do 3 niebieskich muszek\n* Flaming Poop - zostawia za sob ognisko, ktre utrzymuje si przez 5 sekund i moe zablokowa 5 pociskw przeciwnikw i/lub zada 3 ticki obrae kontaktowych (pierwszy raz 16,5 Obrae, drugi i trzeci 11 Obrae)\n* Stinky Poop - generuje toksyczn chmur, ktra zadaje 3,5 Obrae na tick (10,5 na sekund); jeli eksplozja lub ogie dotknie chmury, wybucha ona, zadajc 15 Obrae; kiedy kupa zostanie zniszczona, zostawia za sob wiksz toksyczn chmur, ktra nie eksploduje i utrzymuje si przez 5 sekund\n* Black Poop - generuje spowalniajcy czarny luz, a kiedy zostanie zniszczona, zadaje 10 Obrae i dezorientuje wszystkich przeciwnikw w pokoju\n* White Poop - dziaa jak kupa z Hallowed Ground\n* Stone Poop - zadaje obraenia do 3 razy i jest bardziej odporna na obraenia przeciwnikw\n* Bomb - wybucha po 1,5 sekundy, synergizuje si z efektami bomb\nInne efekty to:\n* Fart - zadaje 5 Obrae i zatruwa pobliskich przeciwnikw, odpychajc przeciwnikw i ich pociski; zostawia za sob trujc chmur, tak sam jak Stinky Poop\n* Explosive Diarrhea - wyrzuca 5 bomb, podobnie do piguki Explosive Diarrhea\n* Brown Creep - tworzy strumie brzowego luzu, ktry zadaje 1,5x Obrae Isaaca na tick (15x na sekund); stanie w obrbie luzu daje Isaacowi +1 Obrae i +1,5 ez oraz inne efekty, zalene od tego, czy luz ma kontakt z innymi kupami\nEfekty Brown Creepu to:\n* Black Poop - luz i zy Isaaca spowalniaj przeciwnikw\n* trujca chmura z Stinky Poop - luz i zy Isaaca zatruwaj przeciwnikw\n* Flaming Poop - luz podpala przeciwnikw, a Isaac otrzymuje efekt Fire Mind\n* White Poop i jej aura - Isaac otrzymuje samonaprowadzajce zy oraz 3x Obraenia i 3x zy\n* Stone Poop - luz zachowa efekty wszystkich innych kup, nawet jeli zostan one znisczone",
  726: "Po podwjnym naciniciu klawisza strzau Isaac kichnie krwi, odpychajc przeciwnikw i zadajc im 1,5x swoich Obrae. Jeli przeciwnik uderzy w cian, przeszkod lub innego przeciwnika, otrzymuje on i zadaje 10 Obrae.\nKichnicia mona uy ponownie po 1 sekundzie.\nZasig kichnicia zaleny jest od statystyki Szybkoci Strzaw.\nKichnicie nadaje przeciwnikom na 5 sekund kltw, ktra powoduje, e otrzymuj oni wicej obrae od Brimstone'a i podobnych atakw (np. Trisagionu i Shoop da Whoop), a przy mierci wybuchaj i przekazuj kltw pobliskim przeciwnikom.\nTainted Azazel ma wbudowan mechanik podobn do Hempotysis, ale technicznie nie jest to jego przedmiot startowy (nie moe zosta przelosowany i znika przy zmianie postaci np. przy uyciu Clickera lub odrodzeniu jako inna posta).",
  727: "+5 bomb.\nBomby Isaaca s spektralne, a przy wybuchu generuj duszka, ktry ciga przeciwnikw i zadaje im obraenia kontaktowe rwne 0,5x Obrae Isaaca na tick (2x Obraenia Isaaca na sekund).\nPo 10 sekundach, duszek wybucha, zadajc 7 Obrae pobliskim przeciwnikom, do 3 razy. Wybuch ten nie rani Isaaca.",
  728: "Przyzywa chowaca, ktry jest przymocowany do Isaaca i porusza si i strzela w kierunku, w ktrym Isaac strzela, uywajc ez, Zasigu, Szybkoci Strzaw i synergii ez Isaaca.\nChowaniec zadaje 0,75x Obrae Isaaca, lub 1x Obrae grajc jako Lilith.\nWbudowana mechanika Tainted Lilith dziaa dokadnie jak Gello.",
  729: "Przy uyciu oddziela gow Isaaca od jego ciaa. Gowa moe zosta rzucona przy uyciu klawiszw strzau.\nGowa zadaje 24 Obrae przy rzuceniu oraz 4 Obrae kontaktowych na tick (8 na sekund), gdy ley na ziemi. Gowa moe normalnie strzela zami, natomiast ciao traci moliwo strzelania.\nGowa moe zosta odzyskana przez wejcie w ni lub ponowne uycie przedmiotu (nawet jeli nie jest naadowany). W drugim przypadku gowa zada obraenia kontaktowe przeciwnikom podczas powrotu do ciaa.",
  73: "Przyzywa orbitala, ktry zadaje 7 Obrae kontaktowych na tick (56 na sekund) i blokuje pociski przeciwnikw.\nZebranie kolejnego Cube of Meat zmienia go w orbital, ktry co 0,66 sekundy strzela zami zadajcymi 3,5 Obrae.\nZebranie kolejnego Cube of Meat zmienia go w Meat Boya, ktry ciga pobliskich przeciwnikw i zadaje im 5 Obrae kontaktowych na tick (10 na sekund).\nZebranie kolejnego Cube of Meat zmienia go w wikszego Meat Boya, ktry zadaje 10 Obrae kontaktowych na tick (20 na sekund).\nZebranie kolejnych Cube of Meat powtarza cykl.\nPrzedmiot wypada z walki z Jedcami Apokalipsy.",
  74: "+25 monet.\nMa szans wypa z Super Greeda lub z dzbanw.",
  75: "Leczy 2 czerwone serca.\nWyrzuca 1 piguk.\nDodaje 0-2 monety (1 w Hard Modzie) do wypaty przy uyciu Blood Donation Machine lub IV Bag.\nWszystkie piguki s zidentyfikowane.\nZamienia ze piguki w ich dobre odpowiedniki:\n* piguka zmniejszajca statystyki  piguka zwikszajca statystyki\n* Amnesia  I can see forever!\n* ???  Telepills\n* Addicted  Percs!\n* I'm Excited!!!  I'm Drowsy...\n* Paralysis  Pheromones\n* Retro Vision  I can see forever!\n* R U A Wizard?  Power Pill!\n* X-Lax  Something's wrong...\n* Bad Trip  Balls of Steel\n* Horf!  Gulp!\n* Experimental Pill - moe tylko zwikszy statystyki\n* Gold Pill - ma efekty tylko pozytywnych piguek\nReszta piguek pozostaje niezmieniona.",
  76: "Automatycznie otwiera Secret Roomy i Super Secret Roomy.",
  77: "Przy uyciu daje nietykalnoci i +0,28 Szybkoci na 6 sekund. Podczas trwania efektu Isaac nie moe strzela zami, ale zadaje 20 Obrae kontaktowych 2 razy na sekund.",
  78: "Przy uyciu dodaje Soul Hearta do zdrowia Isaaca oraz gwarantuje, e bossem danego pitra bdzie Jedziec Apokalipsy (Famine w Rozdziale 1, Pestilence w Rozdziale 2, War w Rozdziale 3, Death lub Conquest w Rozdziale 4) lub The Headless Horseman (na wszystkich moliwych pitrach).\nUycie zastpi bossa tylko, gdy jest to moliwe. Efekt nie nastpi np. na ustalonych bossach, jeli Boss Room jest podwjny lub jeli Isaac ju spotka danego jedca jako bossa. Na pitrach XL tylko pierwszy boss zostanie zastpiony.\nPasywnie zwiksza szans na Devil Room/Angel Room o 17,5%, jeli Isaac posiada przedmiot zabijajc bossa.",
  79: "+1 Black Heart.\n+1 Obrae.\n+0,2 Szybkoci.",
  8: "Przyzywa chowaca, ktry co 1/2 sekundy strzela zami zadajcymi 3,5 Obrae.",
  80: "+0,5 Obrae.\n+0,7 ez.\n+2 Black Hearty.",
  81: "Przy podniesieniu liczba Czerwonych Serc Isaaca zostanie ustawiona na 1. Nie usuwa innych typw serc, oprcz postaci, ktre nie posiadaj Czerwonych Serc.\nDaje Isaacowi 9 dodatkowych y. Jeli Isaac zginie, zostanie odrodzony w poprzednim pokoju z 1 Czerwonym Sercem, a jego przedmioty aktywne zostan rozadowane.\nDead Cat jest czwartym przedmiotem w kolejnoci aktywacji przedmiotw odradzajcych, po Soul of Lazarus, 1up! i drugim yciem Lazarusa.\nMoe wypa z Super Pride'a.",
  82: "+0,3 Szybkoci.\nDaje Isaacowi latanie.",
  83: "Przy uyciu daje +1 Black Heart oraz +2 Obrae, -0,18 Szybkoci, 40 Obrae kontaktowych (zadawanych maksymalnie 1 na sekund) i moliwo niszczenia przeszkd na czas trwania obecnego pokoju.\nBonus do obrae z kilkukrotnego uycia The Nail dodaje si (ale obnienie szybkoci nie).",
  84: "Przy uyciu generuje klap do nastpnego pitra.\nUycie na pododze ze specjaln grafik (np. z traw w Basemencie) wygeneruje przejcie do Crawl Space.\nPrzy uyciu w Womb II/Utero II/Scarred Womb II, klapa bdzie prowadzia do Sheolu.\nKlapa nie moe zosta wygenerowana przed wejciem do Secret Roomu/Super Secret Roomu.\nPrzedmiot moe zosta uyty do zniszczenia kamieni, gdy Isaac nad nimi lata.\nPrzedmiot moe zosta take uyty do odkopania skrzynki z Dirt Patchu w Dark Roomie.",
  85: "Przy uyciu generuje losow kart, wliczajc Dice Shard i Cracked Key.",
  86: "Przy uyciu wzywa Monstra, ktry skacze na losowego przeciwnika, zadajc 120 Obrae i niszczc pobliskie przeszkody.\nUycie przedmiotu w pokoju bez przeciwnikw spowoduje, e Monstro zaatakuje Isaaca.\nMonstro moe zaatakowa i zniszczy Shopkeeperw.\nMa ma szans wypa z Monstra.",
  87: "Daje Isaacowi szans prostopadle w 4 kierunkach. Szansa wynosi 25% przy Szczciu 0 i wzrasta o 5% za kade +1 Szczcia (100% przy Szczciu 15).\nMa ma szans wypa z Lokiego.",
  88: "Przyzywa chowaca, ktry szaruje przez pokj, gdy Isaac strzela, zadajc 3,5 Obrae kontaktowych na tick (ok. 52,5 na sekund), dopki nie napotka ciany lub przeszkody, po czym wraca do Isaaca.\nMa ma szans wypa z Chuba.",
  89: "Daje szans na wystrzelenie spowalniajcej zy, ktra spowalnia przeciwnikw na 2,5 sekundy.\nSzansa zaley od statystyki Szczcia i wynosi 100% przy Szczciu 15.\nMa ma szans wypa z Triachnida.",
  9: "Zmienia zachowanie muszych przeciwnikw:\n* Attack Fly, Dart Fly, Eternal Fly oraz Ring Fly zamieni si w zwyk neutraln muszk Fly\n* Moter, Level 2 Fly, Full Fly, Pooter oraz Super Pooter poruszaj si wolniej i nie zadaj obrae kontaktowych\n* Pootery nie strzelaj do Isaaca\n* Motery nie dziel si na 2 Attack Fly\n* Boom Fly, Red Boom Fly oraz Drowned Boom Fly poruszaj si wolniej (ale zadaj obraenia kontaktowe)\nJedna z nagrd z Shell Game.",
  90: "+1 Obrae.\n+0,2 ez.\n-0,2 Szybkoci.\nMa szans wypa z Tinted Rocka.",
  91: "Pokazuje na mapie rodzaj ssiadujcych pokoi (wliczajc Secret Room, Super Secret Room i walk z mini-bossem), do 2 pokoi od obecnego pokoju.\nIsaac jest odporny na pociski spadajce na niego z gry.\nIsaac bardziej wieci w ciemnoci.",
  92: "+1 Czerwone Serce.\n+2 Soul Hearty.\nDodatkowo leczy 1 czerwone serce.",
  93: "Na 6 sekund daje Isaacowi nastpujce efekty:\n* daje mu nietykalno\n* usuwa moliwo strzelania zami\n* wywouje efekt strachu u wszystkich przeciwnikw w pokoju\n* Isaac zadaje 40 Obrae kontaktowych, maksymalnie 1 uderzenie na sekund na przeciwnika\n* leczy 1/2 czerwonego serca za kadych 2 zabitych przeciwnikw",
  94: "Przyzywa chowaca, ktry wyrzuca monet co 2 pokoje. Pierwsze wyrzucenie nastpi po 1 pokoju.",
  95: "Przyzywa chowaca, ktry co 2/3 sekundy strzela spektralnym laserem, ktry zadaje 3,5 Obrae.",
  96: "Przyzywa chowaca, ktry wyrzuca 1/2 Czerwonego Serca co 3 pokoje.\nZa pierwszym razem po podniesieniu wyrzuca 1/2 Czerwonego Serca po 2 pokojach.\nMa szans wypa z C.H.A.D.a.",
  97: "Przy uyciu generuje serce, monet, bomb, klucz, bateri, piguk, kart lub run.\nSerca, ktre przedmiot generuje, to pene czerwone serce, Soul Heart lub Black Heart.\nBomby i klucze zawsze bd pojedyncze (przedmiot nie generuje 1+1 bomb ani kluczy).",
  98: "Chowaniec, ktry wyrzuca Soul Hearty co na zmian 7 lub 8 pokoi.",
  99: "Przyzywa chowaca, ktry poda za Isaaciem i co 2/3 sekundy strzela spowalniajcymi zami, ktre zadaj 3,5 Obrae.\nMa szans wypa z Gisha.",
  730: "+0,75 Obrae.\n+1 Szczcia.",
  731: "+28% Obrae na prawym oku Isaaca.\n+7 Zasigu na prawym oku Isaaca.\n-0,3 Szybkoci Strzaw na prawym oku Isaaca.",
  732: "+1 Obrae.\nWyrzuca losow run lub Soul Stone'a przy podniesieniu (jeli jakiekolwiek zostay odblokowane).",
};

