  104: "Gdy Isaac otrzyma obraenia, istnieje 5% szansy na zniszczenie trinketu i wygenerowanie losowego przedmiotu z puli obecnego pokoju.",
  121: "Daje efekt Holy Mantle na pocztku kadego pitra.\nEfekt dodaje si z efektem Holy Mantle i Blanketu (ale nie Holy Card) i ma z nich najwyszy priorytet.",
  173: "Trinket moe zosta uyty do zapaty za ukad z Diabem/Black Market bez zuycia serc. Po uyciu trinket znika.\nNie moe zosta uyty do zakupu ukadu z Diabem za pienidze, np. jako Keeper lub z A Pound of Flesh.\nUycie trinketu liczy si jako wzicie ukadu z Diabem do szansy na Angel Room.",
  102: "Powoduje, e na nowych pitrach zostan wygenerowane 2 Secret Roomy.\nNie ma efektu w Greed Modzie.",
  111: "Powoduje, e na pitrach Womb/Utero/Scarred Womb/Corpse zostan wygenerowane Treasure Roomy.",
  82: "Dodaje 15% szansy na to, e na kolejnych pitrach wygenerowane zostan Treasure Roomy z 2 przedmiotami. Mona wzi tylko jeden z tych przedmiotw, drugi zniknie.",
