  122: "Gdy Isaac uyje przedmiotu aktywnego, zostanie on wyrzucony na piedesta na ziemi.\nGdy Isaac otrzymuje obraenia, istnieje 2% szansy na to, e jeden z jego przedmiotw pasywnych zostanie wyrzucony na piedesta na ziemi.\nW przypadku przedmiotw aktywnych w miejscu na piguk, s one traktowane jak przedmioty pasywne. Po wyrzuceniu na piedesta i ponownym podniesieniu zajm one normalne miejsce przedmiotu aktywnego, a miejsce w miejscu na piguk zostanie stracone.",
